<template>
  <div id='bd-app-admin'>
    <!-- TODO:å¯¹æŽ¥æ·˜å®ã€äº¬ä¸œç­‰ç”µå•†å¹³å° -->
    <el-tabs v-model="activeName" @tab-click="handleClick" style="height: 100%;" @edit="handleTabsEdit">
      <el-tab-pane label="ç½‘ç«™" name="web" style="height: 100%;">
        <bd-web></bd-web>
      </el-tab-pane>
      <el-tab-pane label="App" name="app" style="height: 100%;">
        <bd-app></bd-app>
      </el-tab-pane>
      <el-tab-pane label="å…¬ä¼—å·" name="mp" style="height: 100%;">
        <bd-mp></bd-mp>
      </el-tab-pane>
      <el-tab-pane label="å°ç¨‹åº" name="mini" style="height: 100%;">
        <bd-mini></bd-mini>
      </el-tab-pane>
      <!-- <el-tab-pane label="ä¼ä¸šå¾®ä¿¡" name="cp" style="height: 100%;" v-if="showAuthority.debug">
        <bd-cp></bd-cp>
      </el-tab-pane> -->
      <el-tab-pane label="æ·˜å®" name="taobao" style="height: 100%;" v-if="showAuthority.debug">
        <bd-taobao></bd-taobao>
      </el-tab-pane>
      <el-tab-pane label="äº¬ä¸œ" name="jd" style="height: 100%;" v-if="showAuthority.debug">
        <bd-jd></bd-jd>
      </el-tab-pane>
      <el-tab-pane label="è‹å®" name="suning" style="height: 100%;" v-if="showAuthority.debug">
        <bd-suning></bd-suning>
      </el-tab-pane>
      <!-- <el-tab-pane label="é’‰é’‰" name="ding" style="height: 100%;" v-if="showAuthority.debug">
        <bd-ding></bd-ding>
      </el-tab-pane> -->
      <el-tab-pane label="å¾®åš" name="weibo" style="height: 100%;" v-if="showAuthority.debug">
        <bd-weibo></bd-weibo>
      </el-tab-pane>
      <el-tab-pane closable style="height: 100%;"
        :key="item.name"
        v-for="item in editableTabs"
        :label="item.title"
        :name="item.name">
        <bd-admin :type="item.type" :object="item.object"></bd-admin>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdAdmin from '@/components/dashboard/main/setting/children/app/BdAdmin'
import BdWeb from '@/components/dashboard/main/setting/children/app/BdWeb'
import BdApp from '@/components/dashboard/main/setting/children/app/BdApp'
import BdMp from '@/components/dashboard/main/setting/children/app/BdMp'
import BdMini from '@/components/dashboard/main/setting/children/app/BdMini'
import BdWeibo from '@/components/dashboard/main/setting/children/app/BdWeibo'
import BdTaobao from '@/components/dashboard/main/setting/children/app/BdTaobao'
import BdJd from '@/components/dashboard/main/setting/children/app/BdJd'
import BdSuning from '@/components/dashboard/main/setting/children/app/BdSuning'
import BdCp from '@/components/dashboard/main/setting/children/app/BdCp'
import BdDing from '@/components/dashboard/main/setting/children/app/BdDing'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-app-admin',
  components: { BdWeb, BdApp, BdMp, BdAdmin, BdMini, BdWeibo, BdTaobao, BdJd, BdSuning, BdCp, BdDing },
  data () {
    return {
      activeName: 'web',
      tabIndex: 1,
      editableTabs: [],
      daxuezhang: constants.IS_DAXUEZHANG
    }
  },
  computed: {
    ...mapGetters([ 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getWebs', 'getApps', 'getMps', 'getMinis' ]),
    handleClick (tab, event) {
      let payload = { page: 0, size: 20 }
      if (tab.name === 'web') {
        this.getWebs(payload)
      } else if (tab.name === 'app') {
        this.getApps(payload)
      } else if (tab.name === 'mp') {
        this.getMps(payload)
      } else if (tab.name === 'mini') {
        this.getMinis(payload)
      }
    },
    handleTabsEdit (targetName, action) {
      // console.log(targetName, action)
      if (action === 'remove') {
        let tabs = this.editableTabs
        let activeName = 'mp'
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1]
              if (nextTab) {
                activeName = nextTab.name
              }
            }
          })
        }
        this.activeName = 'mp'
        this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      }
    }
  },
  mounted () {
    // ç¼–è¾‘å®¢æœä»£ç 
    bus.$on(constants.EVENT_BUS_MP_ADMIN, mp => {
      // console.log('agent:', agent)
      this.editableTabs.push({
        title: 'å…¬ä¼—å·' + mp.nickname + 'ç®¡ç†',
        name: '' + this.tabIndex,
        type: 'mp',
        object: mp
      })
      this.activeName = '' + this.tabIndex
      this.tabIndex++
    })
  }
}
</script>

<style scoped>
#bd-app-admin {
  height: 100%;
  padding-left: 5px;
  text-align: left;
}
</style>



