import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-mp-admin',
  data () {
    return {
      dialogVisible: false,
      isEdit: false,
      form: {
        wid: '',
        nickname: '',
        number: '',
        description: '',
        company: '',
        original_id: '',
        app_id: '',
        app_secret: '',
        aes_key: 'OadnyZnUEPiscOA4DsjCSCW4TY4uJQ3zj3aSB2XKDVg',
        encode_type: '3'
      },
      rules: {
        nickname: [
          { required: true, message: 'è¯·è¾“å…¥åç§°', trigger: 'blur' }
        ],
        number: [
          { required: true, message: 'è¯·è¾“å…¥å¾®ä¿¡å·', trigger: 'blur' }
        ],
        description: [
          { required: true, message: 'è¯·è¾“å…¥ç®€ä»‹', trigger: 'blur' }
        ],
        company: [
          { required: true, message: 'è¯·è¾“å…¥å…¬å¸åç§°', trigger: 'blur' }
        ],
        original_id: [
          { required: true, message: 'è¯·è¾“å…¥åŽŸå§‹ID', trigger: 'blur' }
        ],
        app_id: [
          { required: true, message: 'è¯·è¾“å…¥AppId', trigger: 'blur' }
        ],
        app_secret: [
          { required: true, message: 'è¯·è¾“å…¥AppSecret', trigger: 'blur' }
        ],
        aes_key: [
          { required: true, message: 'è¯·è¾“å…¥AesKey', trigger: 'blur' }
        ]
      },
      encodeTypes: [
        { id: 1, value: '1', name: 'æ˜Žæ–‡æ¨¡å¼' },
        { id: 2, value: '2', name: 'å…¼å®¹æ¨¡å¼' },
        { id: 3, value: '3', name: 'å®‰å…¨æ¨¡å¼' }
      ],
      // ä¸Šä¼ å›¾ç‰‡ç›¸å…³å‚æ•°
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL
    }
  },
  computed: {
    ...mapGetters([ 'mps', 'userInfo', 'accessToken' ]),
    mainHost () {
      return document.domain.split('.').slice(-2).join('.') + (location.port === '8081' ? ':8000' : '')
    }
  },
  methods: {
    ...mapActions([ 'getMps', 'createMp', 'updateMp', 'deleteMp' ]),
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201808252119251')
    },
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('ä¸Šä¼ å›¾ç‰‡é”™è¯¯ï¼Œè¯·ç¨åŽé‡è¯•...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // éªŒè¯å›¾ç‰‡æ ¼å¼
      if (!isJPGorPNG) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡åªèƒ½æ˜¯ jpg æˆ– png æ ¼å¼!')
      }
      // éªŒè¯å›¾ç‰‡å¤§å°
      if (!isLt2M) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡ 2MB!')
      }
      // è®¾ç½®æ–‡ä»¶å
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // è¿”å›ž
      return isJPGorPNG && isLt2M
    },
    gotoOAuth () {
      let url = constants.HTTP_PREFIX + 'wechat.' + this.mainHost + '/redirect/' + this.userInfo.uid
      window.location.href = url
      // window.open(url)
    },
    showAddDialog () {
      this.dialogVisible = true
      this.isEdit = false
      //
      this.form.wid = ''
      this.form.nickname = ''
      this.form.number = ''
      this.form.description = ''
      this.form.company = ''
      this.form.original_id = ''
      this.form.app_id = ''
      this.form.app_secret = ''
    },
    addMp () {
      this.dialogVisible = false
      if (this.isEdit) {
        this.updateMp(this.form)
      } else {
        this.createMp(this.form)
      }
    },
    handleAdmin (index, row) {
      bus.$emit(constants.EVENT_BUS_MP_ADMIN, row)
    },
    handleEdit (index, row) {
      // console.log(index, row)
      this.form.wid = row.wid
      this.form.nickname = row.nickname
      this.form.number = row.wxNumber
      this.form.description = row.description
      this.form.company = row.company
      this.form.original_id = row.userName
      this.form.app_id = row.appId
      this.form.app_secret = row.appSecret
      this.form.aes_key = row.aesKey
      this.form.encode_type = '3'
      //
      this.isEdit = true
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteMp(row.wid)
      }).catch(() => {
        //
      })
    },
    getApi () {}
  }

}
</script>

<style scoped>
