        <el-radio-group v-model="form.style">
          <el-radio :label="1">ç‹¬ç«‹çª—å£</el-radio>
          <el-radio :label="2">åµŒå…¥å¼</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- TODO: å¯å®žæ—¶é¢„è§ˆå¤§å° -->
