          <el-radio :label="3">å°</el-radio>
          <el-radio :label="4">æœ€å°</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- <el-form-item label="é¢œè‰²">
        <el-color-picker v-model="form.color"></el-color-picker>
      </el-form-item> -->
      <!-- <el-form-item label="ä½ç½®">
        <el-radio-group v-model="form.position">
          <el-radio :label="1">å³ä¸‹è§’</el-radio>
          <el-radio :label="2">å³ä¾§</el-radio>
        </el-radio-group>
      </el-form-item> -->
      <el-form-item label="ä»£ç ">
        <el-input type="textarea" :autosize="{ minRows: 12, maxRows: 20}" :value="styledCode"></el-input>
