</template>

<script>
import { mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import Clipboard from 'clipboard'

export default {
  name: 'bd-code-admin',
  props: [ 'type', 'object' ],
  data () {
    return {
      form: {
