        style: 1,
        size: 1,
        position: 1,
        code: ''
      },
      htmlTip: 'å¤åˆ¶ä»£ç ï¼Œå¹¶æ”¾åœ¨ç½‘é¡µ</body>ä¹‹å‰',
      isIp: constants.IS_IP,
      isDxz: constants.IS_DAXUEZHANG,
      homeUrl: constants.HOME_URL
    }
  },
  computed: {
    ...mapGetters([ 'userInfo' ]),
    mainHost () {
      return document.domain.split('.').slice(-2).join('.') + (location.port === '8081' ? ':8000' : '')
    },
    styledCode () {
      if (this.form.style === 1) {
        if (this.type === 'workGroup') {
          // å·¥ä½œç»„ç‹¬ç«‹çª—å£
          if (this.isIp) {
            return '<a href="' + constants.CHAT_BASE_URL + '?uid=' + this.userInfo.uid + '&wid=' + this.object.wid + '&type=workGroup&aid=&ph=ph" target="_blank">åœ¨çº¿å®¢æœ</a>'
          } else {
            return '<a href="' + constants.HTTP_PREFIX + this.userInfo.subDomain + '.' + this.mainHost + '/chat?uid=' + this.userInfo.uid + '&wid=' + this.object.wid + '&type=workGroup&aid=&ph=ph" target="_blank">åœ¨çº¿å®¢æœ</a>'
          }
        } else {
          // å®¢æœä¸€å¯¹ä¸€å¯¹è¯: ' + this.object.workGroups[0].wid + '
          if (this.isIp) {
            return '<a href="' + constants.CHAT_BASE_URL + '?uid=' + this.userInfo.uid + '&wid=&type=appointed&aid=' + this.object.uid + '&ph=ph" target="_blank">åœ¨çº¿å®¢æœ</a>'
          } else {
            return '<a href="' + constants.HTTP_PREFIX + this.userInfo.subDomain + '.' + this.mainHost + '/chat?uid=' + this.userInfo.uid + '&wid=&type=appointed&aid=' + this.object.uid + '&ph=ph" target="_blank">åœ¨çº¿å®¢æœ</a>'
          }
        }
      } else {
        if (this.type === 'workGroup') {
          // å·¥ä½œç»„åµŒå…¥å¼ä»£ç 
          return '<!--è™¾å©†Â·æ™ºèƒ½å®¢æœä»£ç  å¼€å§‹ -->\n' +
            '<div id="byteDesk"></div>\n' +
            '<script type="text/javascript">\n' +
            'window.adminUid = "' + this.userInfo.uid + '";\n' +
            'window.workGroupWid = "' + this.object.wid + '";\n' +
            'window.subDomain = "' + this.userInfo.subDomain + '";\n' +
            'window.type = "workGroup";\n' +
            'window.agentUid = "";\n' +
            '(function () {\n' +
            '  d = document;\n' +
            '  s = d.createElement("script");\n' +
            '  s.src = "' + this.homeUrl + this.jsDirName + this.floatJs + '";\n' +
            '  s.async = 1;\n' +
            '  d.getElementsByTagName("head")[0].appendChild(s);\n' +
            '})();\n' +
            '<' +
            '/script>\n' +
            '<!--./è™¾å©†Â·æ™ºèƒ½å®¢æœä»£ç  ç»“æŸ -->'
        } else {
          // å®¢æœä¸€å¯¹ä¸€å¯¹è¯
          return '<!--è™¾å©†Â·æ™ºèƒ½å®¢æœä»£ç  å¼€å§‹ -->\n' +
            '<div id="byteDesk"></div>\n' +
            '<script type="text/javascript">\n' +
            'window.adminUid = "' + this.userInfo.uid + '";\n' +
            'window.workGroupWid = "";\n' +
            'window.subDomain = "' + this.userInfo.subDomain + '";\n' +
            'window.type = "appointed";\n' +
            'window.agentUid = "' + this.object.uid + '";\n' +
            '(function () {\n' +
            '  d = document;\n' +
            '  s = d.createElement("script");\n' +
            '  s.src = "' + this.homeUrl + this.jsDirName + this.floatJs + '";\n' +
            '  s.async = 1;\n' +
            '  d.getElementsByTagName("head")[0].appendChild(s);\n' +
            '})();\n' +
            '<' +
            '/script>\n' +
            '<!--./è™¾å©†Â·æ™ºèƒ½å®¢æœä»£ç  ç»“æŸ -->'
        }
      }
    },
    iconSizeVisible () {
      return this.form.style === 2
    },
    jsDirName () {
      if (this.isDxz) {
        return '/js/deploy/dxz/'
      } else {
        return '/js/'
      }
    },
    floatJs () {
      if (this.form.size === 1) {
        return 'n.js'
      } else if (this.form.size === 2) {
        return 'me.js'
      } else if (this.form.size === 3) {
        return 'sm.js'
      } else if (this.form.size === 4) {
        return 'mi.js'
      } else {
        return 'n.js'
      }
    }
  },
  methods: {
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201809182325351')
    },
    onCopy () {
      console.log('submit!')
      var clipboard = new Clipboard('.copyBtn')
      clipboard.on('success', e => {
        this.$message({
          message: 'å¤åˆ¶æˆåŠŸ',
          type: 'success'
        })
        // é‡Šæ”¾å†…å­˜
        clipboard.destroy()
      })
      clipboard.on('error', e => {
        // ä¸æ”¯æŒå¤åˆ¶
        this.$message.error('è¯¥æµè§ˆå™¨ä¸æ”¯æŒè‡ªåŠ¨å¤åˆ¶')
        // é‡Šæ”¾å†…å­˜
        clipboard.destroy()
      })
    }
  }

}
</script>

<style scoped>
#bd-code-admin {
  height: 100%;
  overflow: auto;
  text-align: left;
}
</style>



