<template>
  <div id='bd-visitor'>
    <el-tabs v-model="activeName" @tab-click="handleClick" type="card" style="height: 100%;">
      <el-tab-pane label="æŽ’é˜Ÿè®¿å®¢" name="queue" style="height: 100%;">
        <bd-queue></bd-queue>
      </el-tab-pane>
      <el-tab-pane label="ç½‘ç«™åœ¨çº¿" name="browse" style="height: 100%;">
        <bd-browse></bd-browse>
      </el-tab-pane>
      <!-- TODO: æœªæƒ³æ¸…æ¥šï¼Œå¦‚ä½•ä½¿ç”¨ -->
      <el-tab-pane v-if="showAuthority.debug" label="APPåœ¨çº¿" name="app" style="height: 100%;">
        <bd-app></bd-app>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdQueue from '@/components/dashboard/main/chat/children/visitor/BdQueue'
import BdBrowse from '@/components/dashboard/main/chat/children/visitor/BdBrowse'
import BdApp from '@/components/dashboard/main/chat/children/visitor/BdApp'

export default {
  name: 'bd-visitor',
  data () {
    return {
      activeName: 'browse'
    }
  },
  components: { BdQueue, BdBrowse, BdApp },
  computed: {
    ...mapGetters([ 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getQueue', 'getBrowse' ]),
    handleClick (tab, event) {
      if (tab.name === 'queue') {
        let payload = { page: 0, size: 20 }
        this.getQueue(payload)
      } else if (tab.name === 'browse') {
        let payload = { page: 0, size: 20 }
        this.getBrowse(payload)
      } else if (tab.name === 'app') {
        // let payload = { page: 0, size: 20 }
      }
    }
  }
}
</script>

<style scoped>
#bd-visitor {
  height: 100%;
}
</style>



