<template>
  <div id='bd-filter-admin'>
    <div style="padding-top: 5px; height: calc(100% - 10px)">
      <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" plain>å¸®åŠ©æ–‡æ¡£</el-button>
      <el-button size="mini" type="success" style="float: left;" @click="addTaboo">æ·»åŠ æ•æ„Ÿè¯</el-button>
      <el-pagination style="float: right;"
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        :total="taboos.totalElements">
      </el-pagination>
      <el-table stripe size="mini" height="calc(100% - 80px)" :data="taboos.content" style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="standard" label="è¯è¯­"></el-table-column>
        <el-table-column prop="taboos" label="ç›¸ä¼¼è¯">
          <template slot-scope="scope">
            <span v-for="(taboo, index) in scope.row.taboos" :key="taboo.id">
              {{ taboo.standard }} <span v-if="index < scope.row.taboos.length-1">,</span>
            </span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="æ“ä½œ" width="200">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination style="float: right; margin-top: 10px;"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[20, 100, 200, 300, 400]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="taboos.totalElements">
      </el-pagination>
      <el-dialog
        title="æ·»åŠ æ•æ„Ÿè¯"
        :visible.sync="dialogVisible"
        width="30%"
        center>
        <el-form ref="form" :model="form" :rules="rules" label-width="80px">
          <el-form-item label="æ ‡å‡†è¯" prop="standard">
            <el-input v-model="form.standard" style="width: 250px;"></el-input>
          </el-form-item>
          <el-form-item
            v-for="(synonym, index) in myTaboos"
            label="ç›¸ä¼¼è¯"
            :key="synonym.id">
            <el-input v-model="synonym.synonym" style="width: 250px;"></el-input>
            <el-button v-if="index > 0" @click.prevent="removeSynonym(synonym.id)">åˆ é™¤</el-button>
            <el-button v-if="index === 0" @click.prevent="addTaboos()">æ·»åŠ </el-button>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">å– æ¶ˆ</el-button>
          <el-button type="primary" @click="doAddTaboo()">ç¡® å®š</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'bd-filter-admin',
  data () {
    return {
      //
      pageSize: 20,
      currentPage: 1,
      //
      isEdit: false,
      dialogVisible: false,
      synonymIndex: 0,
      tableData: [],
      form: {
        tid: '',
        standard: '',
        synonyms: [{
          id: 0,
          synonym: ''
        }]
      },
      myTaboos: [{
        id: 0,
        synonym: ''
      }],
      rules: {
        standard: [
          { required: true, message: 'è¯·è¾“å…¥æ ‡å‡†è¯', trigger: 'blur' }
        ],
        synonym: [
          { required: true, message: 'è¯·è¾“å…¥ç›¸ä¼¼è¯', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([ 'taboos' ])
  },
  methods: {
    ...mapActions([ 'getTaboos', 'createTaboo', 'updateTaboo', 'deleteTaboo' ]),
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
    },
    addTaboos () {
      this.synonymIndex++
      this.myTaboos.push({
        id: this.synonymIndex,
        synonym: ''
      })
    },
    removeSynonym (id) {
      for (let i = 0; i < this.myTaboos.length; i++) {
        const synonym = this.myTaboos[i]
        if (synonym.id === id) {
          this.myTaboos.splice(i, 1)
        }
      }
    },
    transformSynonyms () {
      let localSynonyms = []
      for (let i = 0; i < this.myTaboos.length; i++) {
        const synonym = this.myTaboos[i]
        localSynonyms.push(synonym.synonym)
      }
      this.form.synonyms = localSynonyms
    },
    addTaboo () {
      this.isEdit = false
      //
      this.form.tid = ''
      this.form.standard = ''
      this.myTaboos = [{ id: 0, synonym: '' }]
      //
      this.dialogVisible = true
    },
    doAddTaboo () {
      this.transformSynonyms()
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          if (this.isEdit) {
            this.updateTaboo(this.form)
          } else {
            this.createTaboo(this.form)
          }
        } else {
          return false
        }
      })
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.isEdit = true
      //
      this.myTaboos = []
      this.form.tid = row.tid
      this.form.standard = row.standard
      for (let i = 0; i < row.synonyms.length; i++) {
        const syn = row.synonyms[i]
        this.synonymIndex = i
        this.myTaboos.push({ id: i, synonym: syn.standard })
      }
      //
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        //
        this.deleteTaboo(row.tid)
      }).catch(() => {
        //
      })
    }
  },
  created () {
    let payload = { page: 0, size: 20 }
    this.getTaboos(payload)
  }
}
</script>

<style scoped>
#bd-filter-admin {
  height: 100%;
  /* padding-top: 5px; */
}
</style>



