// http response æ‹¦æˆªå™¨
axios.interceptors.response.use(
  response => {
    return response
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 400:
          // TODO: ä¿®æ”¹ç™»å½•é”™è¯¯æç¤ºä¸ºï¼šå¯†ç é”™è¯¯
          console.log('axios interception error 400')
          bus.$emit(constants.EVENT_BUS_LOGIN_ERROR_400, '400')
          break
        case 401:
          // FIXME: 401æŠ¥é”™è‡ªåŠ¨æ¸…ç†æœ¬åœ°å­˜å‚¨access_token, ç„¶åŽé‡æ–°èŽ·å–access_token
          console.log('axios interception error 401')
          bus.$emit(constants.EVENT_BUS_LOGOUT, '401')
          break
        case 403:
          // TODO: é€šè¿‡refresh_tokenèŽ·å–æœ€æ–°access_token?
          // 403 æ— æƒé™ï¼Œè·³è½¬åˆ°é¦–é¡µ
          console.log('axios interception error 403')
          bus.$emit(constants.EVENT_BUS_LOGOUT, '403')
          break
        case 500:
          // TODO: æœåŠ¡å™¨é”™è¯¯
          console.log('axios interception error 500')
          bus.$emit(constants.EVENT_BUS_SERVER_ERROR_500, '500')
          break
      }
    }
    return 'return axios interception error'
  })

