/**
 * ç™»å½•OAuth2
 * @param {*} username ç”¨æˆ·å
 * @param {*} password å¯†ç 
 */
export function login (username, password, successcb, failedcb) {
  axios({
    method: 'post',
    url: '/oauth/token',
    params: {
      'username': username,
      'password': password,
      'grant_type': 'password',
      'scope': 'all'
    },
    auth: {
      username: 'client',
      password: 'secret'
    }
  }).then(function (response) {
    successcb(response.data)
  }).catch(function (error) {
    failedcb(error)
  })
}

