    // è”ç³»äººæ¶ˆæ¯
    bus.$on(constants.EVENT_BUS_NOTIFICATION_CONTACT_MESSAGE, message => {
      // ä¸å«è‡ªå·±å‘é€çš„æ¶ˆæ¯
      if (this.userInfo.uid !== message.user.uid) {
        let content = ''
        if (message.type === constants.MESSAGE_TYPE_TEXT) {
          content = message.content
        } else if (message.type === constants.MESSAGE_TYPE_IMAGE) {
          content = 'img[' + message.imageUrl + ']'
          // content = '<img src="' + message.imageUrl + '"/>'
          // content = message.imageUrl
        }
        this.layim.getMessage({
          id: message.user.uid, // æŽ¥æ”¶è€…ï¼šæ¶ˆæ¯çš„æ¥æºIDï¼ˆå¦‚æžœæ˜¯ç§èŠï¼Œåˆ™æ˜¯ç”¨æˆ·idï¼Œå¦‚æžœæ˜¯ç¾¤èŠï¼Œåˆ™æ˜¯ç¾¤ç»„idï¼‰
          username: message.user.realName, // æ¶ˆæ¯æ¥æºç”¨æˆ·å
          avatar: message.user.avatar, // æ¶ˆæ¯æ¥æºç”¨æˆ·å¤´åƒ
          type: 'friend', // èŠå¤©çª—å£æ¥æºç±»åž‹ï¼Œä»Žå‘é€æ¶ˆæ¯ä¼ é€’çš„toé‡Œé¢èŽ·å–
          content: content, // æ¶ˆæ¯å†…å®¹
          cid: 0, // æ¶ˆæ¯idï¼Œå¯ä¸ä¼ ã€‚é™¤éžä½ è¦å¯¹æ¶ˆæ¯è¿›è¡Œä¸€äº›æ“ä½œï¼ˆå¦‚æ’¤å›žï¼‰
          mine: false, // æ˜¯å¦æˆ‘å‘é€çš„æ¶ˆæ¯ï¼Œå¦‚æžœä¸ºtrueï¼Œåˆ™ä¼šæ˜¾ç¤ºåœ¨å³æ–¹
          fromid: message.user.uid, // æ¶ˆæ¯çš„å‘é€è€…idï¼ˆæ¯”å¦‚ç¾¤ç»„ä¸­çš„æŸä¸ªæ¶ˆæ¯å‘é€è€…ï¼‰ï¼Œå¯ç”¨äºŽè‡ªåŠ¨è§£å†³æµè§ˆå™¨å¤šçª—å£æ—¶çš„ä¸€äº›é—®é¢˜
          timestamp: moment(message.createdAt).valueOf() // æœåŠ¡ç«¯æ—¶é—´æˆ³æ¯«ç§’æ•°ã€‚æ³¨æ„ï¼šå¦‚æžœä½ è¿”å›žçš„æ˜¯æ ‡å‡†çš„ unix æ—¶é—´æˆ³ï¼Œè®°å¾—è¦ *1000
        })
      }
    })
    // ç¾¤ç»„æ¶ˆæ¯
    bus.$on(constants.EVENT_BUS_NOTIFICATION_GROUP_MESSAGE, message => {
      // console.log('ç¾¤ç»„æ¶ˆæ¯:', this.userInfo.uid, message.user.uid)
      if (this.userInfo.uid !== message.user.uid) {
        let content = ''
        if (message.type === constants.MESSAGE_TYPE_TEXT) {
          content = message.content
        } else if (message.type === constants.MESSAGE_TYPE_IMAGE) {
          content = 'img[' + message.imageUrl + ']'
        }
        this.layim.getMessage({
          id: message.gid, // æŽ¥æ”¶è€…ï¼šæ¶ˆæ¯çš„æ¥æºIDï¼ˆå¦‚æžœæ˜¯ç§èŠï¼Œåˆ™æ˜¯ç”¨æˆ·idï¼Œå¦‚æžœæ˜¯ç¾¤èŠï¼Œåˆ™æ˜¯ç¾¤ç»„idï¼‰
          username: message.user.realName, // æ¶ˆæ¯æ¥æºç”¨æˆ·å
          avatar: message.user.avatar, // æ¶ˆæ¯æ¥æºç”¨æˆ·å¤´åƒ
          type: 'group', // èŠå¤©çª—å£æ¥æºç±»åž‹ï¼Œä»Žå‘é€æ¶ˆæ¯ä¼ é€’çš„toé‡Œé¢èŽ·å–
          content: content, // æ¶ˆæ¯å†…å®¹
          cid: 0, // æ¶ˆæ¯idï¼Œå¯ä¸ä¼ ã€‚é™¤éžä½ è¦å¯¹æ¶ˆæ¯è¿›è¡Œä¸€äº›æ“ä½œï¼ˆå¦‚æ’¤å›žï¼‰
          mine: false, // æ˜¯å¦æˆ‘å‘é€çš„æ¶ˆæ¯ï¼Œå¦‚æžœä¸ºtrueï¼Œåˆ™ä¼šæ˜¾ç¤ºåœ¨å³æ–¹
          fromid: message.user.uid, // æ¶ˆæ¯çš„å‘é€è€…idï¼ˆæ¯”å¦‚ç¾¤ç»„ä¸­çš„æŸä¸ªæ¶ˆæ¯å‘é€è€…ï¼‰ï¼Œå¯ç”¨äºŽè‡ªåŠ¨è§£å†³æµè§ˆå™¨å¤šçª—å£æ—¶çš„ä¸€äº›é—®é¢˜
          timestamp: moment(message.createdAt).valueOf() // æœåŠ¡ç«¯æ—¶é—´æˆ³æ¯«ç§’æ•°ã€‚æ³¨æ„ï¼šå¦‚æžœä½ è¿”å›žçš„æ˜¯æ ‡å‡†çš„ unix æ—¶é—´æˆ³ï¼Œè®°å¾—è¦ *1000
        })
      }
    })
    // stompè¿žæŽ¥
    bus.$on(constants.EVENT_BUS_STOMP_CONNECTION_STATUS, message => {
      let connectionStatus = message
      if (this.electron) {
        ipcRenderer.send(connectionStatus, '')
      }
    })
    // é€€å‡ºç™»å½•ï¼Œè·³è½¬åˆ°ç™»å½•é¡µé¢
    bus.$on(constants.EVENT_BUS_LOGOUT, message => {
      // 401ã€403çš„æƒ…å†µä¸‹ï¼Œéœ€è¦æ¸…ç©ºä¸‹storage
      this.clearToken()
      this.$router.replace('/login')
      window.location.reload()
      // Electron éšè—trayå›¾æ ‡
      if (constants.IS_ELECTRON) {
        ipcRenderer.send('hide-tray', '')
      }
    })
    // ç›‘å¬æµè§ˆå™¨åˆ‡æ¢é¡µé¢
    document.addEventListener('visibilitychange', function () {
      if (!document.hidden) {
        // console.log('é¡µé¢å¯è§')
      } else {
        // console.log('é¡µé¢ä¸å¯è§')
      }
    })
    // åˆ·æ–°æˆ–å…³é—­çª—å£ä¹‹å‰
    // FIXME: å½±å“æ‰‹åŠ¨é€€å‡ºç™»å½•
    // window.onbeforeunload = () => {
    //   return 'ç¡®å®šè¦ç¦»å¼€ï¼Ÿ'
    // }
  }
}
</script>

