<template>
  <div id='bd-department'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" plain>å¸®åŠ©æ–‡æ¡£</el-button>
    <el-button size="mini" type="primary" style="float: left;" @click="addWorkGroupDialog()">æ·»åŠ éƒ¨é—¨</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="workGroups.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="workGroups.content" style="width: 100%">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="name" label="éƒ¨é—¨åç§°" width="150">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" height="15px" width="15px"/>
          <span>{{ scope.row.nickname }}</span>
        </template>
      </el-table-column>
      <el-table-column label="éƒ¨é—¨æˆå‘˜"></el-table-column>
      <el-table-column fixed="right" label="æ“ä½œ" width="150px">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
          <el-button v-if="!scope.row.defaulted" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="workGroups.totalElements">
    </el-pagination>
    <el-dialog
      title="æ·»åŠ éƒ¨é—¨"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="140px">
        <el-form-item label="éƒ¨é—¨åç§°" prop="name">
          <el-input v-model="form.nickname"></el-input>
        </el-form-item>
        <el-form-item label="LOGO" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 10px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">ç‚¹å‡»ä¸Šä¼ </el-button>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="addWorkGroup()">ç¡® å®š</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-department',
  data () {
    return {
      currentPage: 1,
      dialogVisible: false,
      isEdit: false,
      workTimeIndex: 0,
      form: {
        id: 0,
        wid: '',
        name: '',
        avatar: constants.DEFAULT_WORKGROUP_AVATAR,
        defaultRobot: false,
        offlineRobot: false,
        forceRate: false,
        routeType: 'robin',
        workTimes: [{
          startTime: new Date(2018, 8, 10, 8, 30),
          endTime: new Date(2018, 8, 10, 11, 30)
        }],
        users: [],
        welcomeTip: 'æ­£åœ¨ä¸ºæ‚¨åˆ†é…å®¢æœï¼Œè¯·ç¨åŽ',
        acceptTip: 'æ‚¨å¥½æœ‰ä»€ä¹ˆå¯ä»¥å¸®æ‚¨çš„?',
        offlineTip: 'å½“å‰å®¢æœä¸åœ¨çº¿ï¼Œè¯·ç•™è¨€'
      },
      workTimes: [{
        id: 0,
        value: [new Date(2018, 8, 10, 8, 30), new Date(2018, 8, 10, 11, 30)]
      }],
      rules: {
        name: [
          { required: true, message: 'è¯·è¾“å…¥æ˜µç§°', trigger: 'blur' },
          { min: 2, message: 'é•¿åº¦æœ€å°‘2ä¸ªå­—ç¬¦', trigger: 'blur' }
        ],
        workGroups: [
          { type: 'array', required: true, message: 'è¯·è‡³å°‘é€‰æ‹©å·¥ä½œç»„', trigger: 'change' }
        ],
        defaultRobot: [
          { required: true, message: 'è¯·é€‰æ‹©é»˜è®¤æœºå™¨äºº', trigger: 'change' }
        ],
        offlineRobot: [
          { required: true, message: 'è¯·é€‰æ‹©ç¦»çº¿æœºå™¨äºº', trigger: 'change' }
        ],
        forceRate: [
          { required: true, message: 'è¯·é€‰æ‹©å¼ºåˆ¶è¯„ä»·', trigger: 'change' }
        ],
        routeType: [
          { required: true, message: 'è¯·é€‰æ‹©å¼ºåˆ¶è¯„ä»·', trigger: 'change' }
        ],
        welcomeTip: [
          { required: true, message: 'è¯·å¡«å†™é»˜è®¤æ¬¢è¿Žè¯­', trigger: 'blur' }
        ],
        acceptTip: [
          { required: true, message: 'è¯·å¡«å†™æŽ¥å…¥æ¬¢è¿Žè¯­', trigger: 'blur' }
        ],
        offlineTip: [
          { required: true, message: 'è¯·å¡«å†™ç¦»çº¿æç¤ºè¯­', trigger: 'blur' }
        ],
        users: [
          { type: 'array', required: true, message: 'è¯·è‡³å°‘é€‰æ‹©ä¸€ä¸ªå®¢æœ', trigger: 'change' }
        ]
      },
      // ä¸Šä¼ å›¾ç‰‡ç›¸å…³å‚æ•°
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL
    }
  },
  computed: {
    ...mapGetters([ 'agents', 'workGroups' ])
  },
  methods: {
    ...mapActions([ 'createWorkgroup', 'updateWorkgroup', 'deleteWorkgroup', 'getWorkGroups' ]),
    routeType (type) {
      return type === 'robin' ? 'è½®è¯¢' : (type === 'pubsub' ? 'å¹¿æ’­' : type)
    },
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('ä¸Šä¼ å›¾ç‰‡é”™è¯¯ï¼Œè¯·ç¨åŽé‡è¯•...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // éªŒè¯å›¾ç‰‡æ ¼å¼
      if (!isJPGorPNG) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡åªèƒ½æ˜¯ jpg æˆ– png æ ¼å¼!')
      }
      // éªŒè¯å›¾ç‰‡å¤§å°
      if (!isLt2M) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡ 2MB!')
      }
      // è®¾ç½®æ–‡ä»¶å
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // è¿”å›ž
      return isJPGorPNG && isLt2M
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = {
        page: this.currentPage - 1,
        size: 20
      }
      this.getWorkGroups(payload)
    },
    addWorkTime () {
      this.workTimeIndex++
      this.workTimes.push({
        id: this.workTimeIndex,
        value: [new Date(2018, 8, 10, 13, 30), new Date(2018, 8, 10, 18, 30)]
      })
    },
    transforWorkTimes () {
      let localWorkTimes = []
      for (let i = 0; i < this.workTimes.length; i++) {
        const workTime = this.workTimes[i]
        let startTime = moment(workTime.value[0]).format('HH:mm:ss')
        let endTime = moment(workTime.value[1]).format('HH:mm:ss')
        console.log(startTime, endTime)
        localWorkTimes.push({'startTime': startTime, 'endTime': endTime})
      }
      this.form.workTimes = localWorkTimes
    },
    removeWorkTime (id) {
      for (let index = 0; index < this.workTimes.length; index++) {
        const workTime = this.workTimes[index]
        if (workTime.id === id) {
          this.workTimes.splice(index, 1)
        }
      }
    },
    addWorkGroupDialog () {
      this.isEdit = false
      this.dialogVisible = true
    },
    addWorkGroup () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          this.transforWorkTimes()
          if (this.isEdit) {
            this.updateWorkgroup(this.form)
          } else {
            this.createWorkgroup(this.form)
          }
        } else {
          return false
        }
      })
    },
    handleCode (index, row) {},
    handleEdit (index, row) {
      console.log(index, row)
      this.isEdit = true
      this.form.id = row.id
      this.form.wid = row.wid
      this.form.nickname = row.nickname
      this.form.avatar = row.avatar
      this.form.defaultRobot = row.defaultRobot
      this.form.offlineRobot = row.offlineRobot
      this.form.forceRate = row.forceRate
      this.form.routeType = row.routeType
      this.form.welcomeTip = row.welcomeTip
      this.form.acceptTip = row.acceptTip
      this.form.offlineTip = row.offlineTip
      this.form.workTimes = row.workTimes
      this.form.users = row.users
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteWorkgroup(row.id)
      }).catch(() => {
        //
      })
    }
  }
}
</script>

<style scoped>
#bd-department {
  height: 100%;
  text-align: left;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>



