function requireAuth (to, from, next) {
  if (!store.getters.connected) {
    next({
      path: '/login',
      query: { redirect: to.fullPath }
    })
  } else {
    next()
  }
}

// åœ¨ç™»å½•ç•Œé¢å’Œæ³¨å†Œç•Œé¢ï¼Œæ£€æŸ¥æ˜¯å¦å·²ç»ç™»å½•ï¼Œå¦‚æžœå·²ç»ç™»å½•ï¼Œåˆ™ç›´æŽ¥è·³è½¬/admin
function beforeAuth (to, from, next) {
  if (!store.getters.connected) {
    next()
  } else {
    next({
      path: '/admin',
      query: { redirect: to.fullPath }
    })
  }
}

