<template>
  <div id='bd-member'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" plain>å¸®åŠ©æ–‡æ¡£</el-button>
    <el-button size="mini" type="primary" style="float: left;" @click="addAgentDialog()">æ·»åŠ è´¦å·</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="agents.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="agents.content" style="width: 100%">
      <el-table-column type="index" width="20"></el-table-column>
      <el-table-column prop="subDomain" label="ä¼ä¸šå·" width="100"></el-table-column>
      <el-table-column prop="username" label="ç”¨æˆ·å(ç”¨äºŽç™»å½•)">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" height="15px" width="15px"/>
          <span>{{ username(scope.row.username) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="realName" label="çœŸå"></el-table-column>
      <el-table-column prop="workGroups" label="éƒ¨é—¨">
        <template slot-scope="scope">
          <el-tag v-for="workGroup in scope.row.workGroups" :key="workGroup.id" size="mini" style="margin-right:5px;">{{ workGroup.nickname }}</el-tag>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="name" label="åœ¨çº¿çŠ¶æ€" sortable></el-table-column> -->
      <el-table-column fixed="right" label="æ“ä½œ" width="150">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
          <el-button v-if="!isSelf(scope.row.username)" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="agents.totalElements">
    </el-pagination>
    <el-dialog
      title="æ·»åŠ å®¢æœè´¦å·"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="120px">
        <el-form-item label="ç™»å½•ç”¨æˆ·å" prop="username">
          <el-input v-model="form.username" placeholder="ç”¨äºŽç™»å½•å®¢æœåŽå°"></el-input>
        </el-form-item>
        <el-form-item label="å¯†ç " prop="password">
          <el-input v-model="form.password" auto-complete="off" placeholder="å¯†ç "></el-input>
        </el-form-item>
        <el-form-item label="é‚®ç®±" prop="email">
          <el-input v-model="form.email" placeholder="é‚®ç®±"></el-input>
        </el-form-item>
        <el-form-item label="å¤´åƒ" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 10px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">ç‚¹å‡»ä¸Šä¼ </el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="çœŸå" prop="realName">
          <el-input v-model="form.realName"></el-input>
        </el-form-item>
        <el-form-item label="æ‰€å±žéƒ¨é—¨" prop="workGroups">
          <el-radio-group v-model="form.workGroups">
            <el-radio v-for="workGroup in workGroups.content" :key="workGroup.id" :label="workGroup.wid">{{ workGroup.nickname }}</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="addAgent()">ç¡® å®š</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
// import * as https from '@/apis/http'

export default {
  name: 'bd-member',
  data () {
    // var validateUsername = (rule, value, callback) => {
    //   https.agentnameValidate(this.form.username, this.accessToken, response => {
    //     console.log(response)
    //     if (response.status_code === 200) {
    //       callback()
    //     } else {
    //       callback(new Error('ç”¨æˆ·åå·²ç»å­˜åœ¨ï¼Œè¯·é‡æ–°å¡«å†™'))
    //     }
    //   }, error => {
    //     console.log(error)
    //   })
    // }
    return {
      currentPage: 1,
      isEdit: false,
      dialogVisible: false,
      form: {
        id: 0,
        username: '',
        password: '',
        email: '',
        avatar: constants.DEFAULT_AVATAR,
        realName: '',
        name: '',
        maxThreadCount: 10,
        workGroups: ''
      },
      rules: {
        username: [
          { required: true, message: 'è¯·è¾“å…¥ç”¨æˆ·å', trigger: 'blur' },
          { min: 5, message: 'é•¿åº¦æœ€å°‘5ä¸ªå­—ç¬¦', trigger: 'blur' }
          // { validator: validateUsername, trigger: 'blur' }
        ],
        email: [
          { type: 'email', message: 'è¯·è¾“å…¥æ­£ç¡®çš„é‚®ç®±åœ°å€', trigger: 'change' }
        ],
        realName: [
          { required: true, message: 'è¯·è¾“å…¥å¯†ç ', trigger: 'blur' }
        ],
        name: [
          { required: true, message: 'è¯·è¾“å…¥å¯†ç ', trigger: 'blur' }
        ],
        workGroups: [
          { required: true, message: 'è¯·è‡³å°‘é€‰æ‹©ä¸€ä¸ªå·¥ä½œç»„', trigger: 'change' }
        ]
      },
      // ä¸Šä¼ å›¾ç‰‡ç›¸å…³å‚æ•°
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'accessToken', 'agents', 'workGroups', 'roles' ])
  },
  methods: {
    ...mapActions([ 'createAgent', 'updateAgent', 'deleteAgent', 'getAgents' ]),
    username (usernameAtSubDomain) {
      return usernameAtSubDomain.split('@')[0]
    },
    isSelf (username) {
      return username === this.userInfo.username
    },
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('ä¸Šä¼ å›¾ç‰‡é”™è¯¯ï¼Œè¯·ç¨åŽé‡è¯•...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // éªŒè¯å›¾ç‰‡æ ¼å¼
      if (!isJPGorPNG) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡åªèƒ½æ˜¯ jpg æˆ– png æ ¼å¼!')
      }
      // éªŒè¯å›¾ç‰‡å¤§å°
      if (!isLt2M) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡ 2MB!')
      }
      // è®¾ç½®æ–‡ä»¶å
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // è¿”å›ž
      return isJPGorPNG && isLt2M
    },
    handleMaxThreadCountChange (value) {
      // console.log(value)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = {
        page: this.currentPage - 1,
        size: 20
      }
      this.getAgents(payload)
    },
    addAgentDialog () {
      this.isEdit = false
      //
      this.form.id = ''
      this.form.username = ''
      this.form.password = ''
      this.form.email = ''
      this.form.avatar = constants.DEFAULT_AVATAR
      this.form.realName = ''
      this.form.nickname = ''
      this.form.maxThreadCount = 10
      this.form.workGroups = []
      this.form.role = ''
      //
      this.dialogVisible = true
    },
    addAgent () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          if (this.isEdit) {
            this.updateAgent(this.form)
          } else {
            this.createAgent(this.form)
          }
        } else {
          return false
        }
      })
    },
    handleEdit (index, row) {
      // console.log(index, row)
      this.isEdit = true
      this.form.id = row.id
      this.form.username = row.username.split('@')[0]
      this.form.password = ''
      this.form.email = row.email
      this.form.avatar = row.avatar
      this.form.realName = row.realName
      this.form.nickname = row.nickname
      this.form.maxThreadCount = row.maxThreadCount
      this.form.email = row.email
      this.form.mobile = row.mobile
      //
      this.form.workGroups = []
      for (let i = 0; i < row.workGroups.length; i++) {
        const workGroup = row.workGroups[i]
        this.form.workGroups.push(workGroup.wid)
      }
      // TODO: æ˜¯å¦åº”è¯¥ä½¿ç”¨æ•°ç»„ï¼Ÿ
      let roleValue = ''
      for (let i = 0; i < row.roles.length; i++) {
        const role = row.roles[i]
        if (role.value !== constants.ROLE_FREE) {
          roleValue = (role.value)
        }
      }
      this.form.role = roleValue
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteAgent(row.id)
      }).catch(() => {
        //
      })
    }
  }

}
</script>

<style scoped>
#bd-member {
  height: 100%;
  text-align: left;
}
</style>



