<template>
  <div id='bd-input'>
    <div id='bd-toolbar'>
      <li id='bd-emoji' class='iconfont icon-emoji' @click="switchEmoji"></li>
      <transition name="showbox">
        <div class="bd-emoji-box" v-show="showEmoji">
          <li v-for="item in emojis" :key="item.file">
            <img :src="emotionUrl(item.file)" :data="item.title" @click="emotionClicked(item.title)">
          </li>
        </div>
      </transition>
      <el-upload class='bd-upload-image'
        :on-preview='handlePreview'
        :on-remove='handleRemove'
        :before-remove='beforeRemove'
        :on-exceed='handleExceed'
        :file-list='uploadedImageList'
        :data='upload_data'
        :name='upload_name'
        :action='uploadImageServerUrl'
        :show-file-list='false'
        :on-success='handleImageUploadSuccess'
        :before-upload='beforeImageUpload'
        :disabled="disabled">
          <li class='iconfont icon-image'></li>
      </el-upload>
      <el-button size="mini" type="text" @click="publishNotification" :disabled="disabled" style="float: left; margin-left: 17px; margin-top: 5px;">å‘å…¬å‘Š</el-button>
      <el-button size="mini" type="text" @click="showHistoryMessages" :disabled="disabled" style="float: right; margin-top: 3px; margin-right: 15px;">åŽ†å²æ¶ˆæ¯</el-button>
    </div>
    <div id='bd-inputbox'>
      <el-input v-focus type='textarea' :rows='4' placeholder='è¯·è¾“å…¥å†…å®¹' v-model='inputContent' @keyup.enter.native="onKeyUp"></el-input>
      <!--FIXME: buttonä½ç½®æœ‰å¾…ä¼˜åŒ–-->
      <el-button class="sendbutton" @click="sendMessage" :disabled="sendButtonDisabled">å‘ é€</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex'
import BdMessage from '@/components/dashboard/main/chat/children/thread/middle/BdMessage'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-input',
  data () {
    return {
      show_emoji: false,
      inputContent: '',
      // ä¸Šä¼ å›¾ç‰‡ç›¸å…³å‚æ•°
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      // æŽ¥æ”¶å›¾ç‰‡æœåŠ¡å™¨åœ°å€
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL,
      // ä¸Šä¼ å›¾ç‰‡ç»“æžœé›†, æ ¼å¼ï¼š{name: '', url: ''}
      uploadedImageList: [],
      recordDialogVisible: false,
      messageDialogVisible: false,
      searchMessage: ''
    }
  },
  components: { BdMessage },
  computed: {
    ...mapGetters([ 'userThreads', 'userCurrentThread' ]),
    ...mapState([
      'emojis'
    ]),
    showEmoji () {
      return !this.disabled && this.show_emoji
    },
    disabled () {
      return this.userCurrentThread.id === 0
    },
    sendButtonDisabled () {
      return this.disabled || this.inputContent.trim().length === 0
    },
    prePageButtonDisabled () {
      return this.userCurrentThread.first
    },
    nextPageButtonDisabled () {
      return this.userCurrentThread.last
    }
  },
  methods: {
    ...mapActions([ 'updateCurrentThread', 'sendTextMessage', 'sendImageMessage', 'getMessages' ]),
    switchEmoji () {
      if (!this.disabled) {
        this.show_emoji = !this.show_emoji
      }
    },
    emotionUrl (file) {
      return constants.EMOJI_BASE_URL + file
    },
    emotionClicked (emotion) {
      this.inputContent += emotion
      this.show_emoji = false
    },
    /**
     * ä¸Šä¼ å›¾ç‰‡ç›¸å…³å‡½æ•°
     */
    handleRemove (file, uploadedImageList) {
      console.log(file, uploadedImageList)
      // åˆ é™¤
      for (var i = 0; i < this.uploadedImageList.length; i++) {
        if (this.uploadedImageList[i].url === file.url) {
          this.uploadedImageList.splice(i, 1)
        }
      }
    },
    handlePreview (file) {
      console.log(file)
    },
    handleExceed (files, uploadedImageList) {
      this.$message.warning(`å½“å‰é™åˆ¶é€‰æ‹© 3 ä¸ªæ–‡ä»¶ï¼Œæœ¬æ¬¡é€‰æ‹©äº† ${files.length} ä¸ªæ–‡ä»¶ï¼Œå…±é€‰æ‹©äº† ${files.length + uploadedImageList.length} ä¸ªæ–‡ä»¶`)
    },
    beforeRemove (file, uploadedImageList) {
      return this.$confirm(`ç¡®å®šç§»é™¤ ${file.name}ï¼Ÿ`)
    },
    handleImageUploadSuccess (result, file) {
      console.log('upload image success result:', result, ' file:', file)
      if (result.status_code === 200) {
        // å‘é€æ¶ˆæ¯
        let url = result.data
        this.sendImageMessage(url)
      } else {
        this.$message.error('ä¸Šä¼ å›¾ç‰‡é”™è¯¯ï¼Œè¯·ç¨åŽé‡è¯•...')
      }
    },
    beforeImageUpload (file) {
      // console.log('before image upload file:', file)
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // éªŒè¯å›¾ç‰‡æ ¼å¼
      if (!isJPGorPNG) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡åªèƒ½æ˜¯ jpg æˆ– png æ ¼å¼!')
      }
      // éªŒè¯å›¾ç‰‡å¤§å°
      if (!isLt2M) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡ 2MB!')
      }
      // è®¾ç½®æ–‡ä»¶å
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // è¿”å›ž
      return isJPGorPNG && isLt2M
    },
    publishNotification () {
      console.log('å‘å…¬å‘Š')
    },
    showHistoryMessages () {
      this.messageDialogVisible = true
      // bus.$emit(constants.EVENT_BUS_MESSAGE_HISTORY)
    },
    /**
     * å‘é€æ¶ˆæ¯
     */
    onKeyUp (e) {
      if (e.keyCode === 13 && this.inputContent.length > 0) {
        this.sendMessage()
      }
    },
    sendMessage () {
      this.show_emoji = false
      if (this.inputContent.trim().length > 0) {
        this.sendTextMessage(this.inputContent)
        this.inputContent = ''
      }
    },
    handleSearch () {
      console.log('searchMessage:', this.searchMessage)
    },
    prePage () {},
    nextPage () {
      let size = 20
      this.getMessages(size)
    }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  mounted () {
    bus.$on(constants.EVENT_BUS_SELECT_CUW, cuw => {
      if (this.disabled) {
        return
      }
      this.inputContent += cuw
    })
  }
}
</script>

<style scoped>
#bd-input {
  width: 100%;
  height: 160px;
  background: #fff;
  border-top:solid 0.1px #dddddd;
}
#bd-emoji {
  float: left;
  margin-top: 11px;
  margin-left: 20px;
  cursor: pointer;
  list-style: none;
}
.bd-emoji-box {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  margin-top: -295px;
  width: 280px;
  height: 280px;
  padding: 3px 6px 6px 14px;
  background-color: #fff;
  border: 1px solid #d1d1d1;
  border-radius: 2px;
  box-shadow:0 1px 2px 1px #d1d1d1;
}
.bd-emoji-box li {
  list-style: none;
  width: 24px;
  margin-top: 15px;
  margin-left: 5px;
  border: none;
  margin-bottom: 0px;
}
.bd-emoji-box.showbox-enter-active, .bd-emoji-box.showbox-leave-active {
  transition: all .5s;
}
.bd-emoji-box.showbox-enter, .bd-emoji-box.showbox-leave-active {
  opacity: 0;
}
.bd-upload-image{
  font-size: 25px;
  color: gray;
  margin-left: 20px;
  cursor: pointer;
  float: left;
  list-style: none;
}
.sendbutton {
  position: absolute;
  bottom: 35px;
  right: 20px;
}
</style>



