<template>
  <div id='bd-browse'>
    <!-- FIXME:ä¸€ä¸ªç”¨æˆ·IDä»…æ˜¾ç¤ºä¸€è¡Œè®°å½• -->
    <!-- TODO: è·Ÿä¼šè¯åˆ—è¡¨å¯¹æ¯”ï¼Œå¦‚æžœæ­¤ç”¨æˆ·å·²ç»æŽ¥å…¥ä¼šè¯ï¼Œåˆ™ä¸æ˜¾ç¤º -->
    <!-- TODO: æ ¹æ®ipæ˜¾ç¤ºæ‰€åœ¨åŸŽå¸‚ç­‰ä½ç½®ä¿¡æ¯ -->
    <el-button icon="el-icon-refresh" size="mini" style="float: left; margin-bottom: 10px;" circle @click="refresh"></el-button>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" plain>å¸®åŠ©æ–‡æ¡£</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      small
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="userBrowses.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="userBrowses.content" style="width: 100%;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="visitor.nickname" label="è®¿å®¢æ˜µç§°" width="140"></el-table-column>
      <!-- <el-table-column prop="referrer" label="IP"></el-table-column> -->
      <!-- <el-table-column prop="referrer" label="ä½ç½®"></el-table-column> -->
      <el-table-column prop="url" label="å½“å‰é¡µé¢ç½‘å€">
        <template slot-scope="scope">
          <span v-if="scope.row.url" style="cursor: pointer; color: #3a8ee6;" @click="handleUrlClick(scope.row.url.url)">{{ scope.row.url.url }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="title" label="å½“å‰é¡µé¢æ ‡é¢˜">
        <template slot-scope="scope">
          <span v-if="scope.row.url" style="cursor: pointer; color: #3a8ee6;" @click="handleUrlClick(scope.row.url.url)">{{ scope.row.url.title }}</span>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="referrer" label="æ¥æºåœ°å€">
        <template slot-scope="scope">
          <span v-if="scope.row.referrer" style="cursor: pointer; color: #3a8ee6;" @click="handleUrlClick(scope.row.referrer.url)">{{ scope.row.referrer.url }}</span>
        </template>
      </el-table-column> -->
      <el-table-column label="åœç•™æ—¶é•¿" width="140">
        <template slot-scope="scope">
          <span>{{ timeInterval(scope.row.createdAt) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="createdAt" label="è¿›å…¥æ—¶é—´" width="140"></el-table-column>
      <el-table-column prop="referrer" label="æ“ä½œç³»ç»Ÿ" width="80">
        <template slot-scope="scope">
          <span>{{ getOs(scope.row) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="referrer" label="æµè§ˆå™¨" width="80">
        <template slot-scope="scope">
          <span>{{ getBrowser(scope.row) }}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="æ“ä½œ" width="80">
        <template slot-scope="scope">
          <el-button size="mini" @click="invite(scope.$index, scope.row)">é‚€è¯·</el-button>
          <!-- <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="userBrowses.totalElements">
    </el-pagination>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as https from '@/apis/http'

export default {
  name: 'bd-browsing',
  data () {
    return {
      pageSize: 20,
      currentPage: 1,
      dialogVisible: false,
      isEdit: false
    }
  },
  computed: {
    ...mapGetters([ 'userBrowses', 'accessToken' ])
  },
  methods: {
    ...mapActions([ 'getBrowse' ]),
    refresh () {
      let payload = { page: 0, size: 20 }
      this.getBrowse(payload)
    },
    timeInterval (then) {
      let ms = moment().diff(then, 'YYYY-MM-DD HH:mm:ss')
      var day = moment.duration(ms)
      var hour = Math.floor(day.asHours())
      var min = moment.utc(ms).format('mm')
      return hour + 'å°æ—¶' + min + 'åˆ†é’Ÿ'
    },
    getOs (row) {
      let fingerPrint2s = row.visitor.fingerPrint2s
      for (let i = 0; i < fingerPrint2s.length; i++) {
        const fingerPrint = row.visitor.fingerPrint2s[i]
        if (fingerPrint.key === 'os-name') {
          return fingerPrint.value
        }
      }
    },
    getBrowser (row) {
      let fingerPrint2s = row.visitor.fingerPrint2s
      for (let i = 0; i < fingerPrint2s.length; i++) {
        const fingerPrint = row.visitor.fingerPrint2s[i]
        if (fingerPrint.key === 'browser-name') {
          return fingerPrint.value
        }
      }
    },
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getBrowse(payload)
    },
    handleCurrentChange (val) {
      // console.log(val)
      this.currentPage = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getBrowse(payload)
    },
    handleUrlClick (url) {
      window.open(url)
    },
    invite (index, row) {
      // console.log(index, row)
      https.inviteBrowse(row.bid, this.accessToken, response => {
        console.log('invite:', response)
        this.$message.success('é‚€è¯·ä¼šè¯æˆåŠŸ')
      }, error => {
        console.log(error)
        this.$message.error('é‚€è¯·ä¼šè¯é”™è¯¯')
      })
    },
    handleDelete (index, row) {
      console.log(index, row)
    }
  }
}
</script>

<style scoped>
#bd-browse {
  height: 100%;
  text-align: left;
}
</style>



