        <el-dropdown-item command="all">å…¨éƒ¨æ¥æº</el-dropdown-item>
        <el-dropdown-item command="web">ç½‘ç«™</el-dropdown-item>
        <el-dropdown-item command="android">å®‰å“</el-dropdown-item>
        <el-dropdown-item command="ios">è‹¹æžœ</el-dropdown-item>
        <el-dropdown-item command="mp">å…¬ä¼—å·</el-dropdown-item>
