      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="visitor.nickname" label="è®¿å®¢æ˜µç§°"></el-table-column>
      <el-table-column prop="score" label="è¯„åˆ†" sortable></el-table-column>
      <!-- <el-table-column prop="score" label="æ»¡æ„åº¦" sortable></el-table-column> -->
