<script>
import { mapActions, mapGetters } from 'vuex'
import CmMessage from '@/components/common/CmMessage'
import * as constants from '@/util/constants'

export default {
  name: 'bd-rate',
  data () {
    return {
      pickerOptions: {
        shortcuts: [{
          text: 'æœ€è¿‘ä¸€å‘¨',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: 'æœ€è¿‘ä¸€ä¸ªæœˆ',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: 'æœ€è¿‘ä¸‰ä¸ªæœˆ',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      createdAtInterval: [ '', '' ],
      nickname: '',
      pageSize: 20,
      currentPage: 1,
      dialogVisible: false,
      isEdit: false,
      currentWorkGroup: 'å…¨éƒ¨å·¥ä½œç»„',
      currentAgent: 'å…¨éƒ¨å®¢æœ',
      currentClient: 'å…¨éƒ¨æ¥æº',
      currentClientCommand: '',
      messageDialogVisible: false,
      viewThreadTid: '',
      electron: constants.IS_ELECTRON
    }
  },
  components: { CmMessage },
  computed: {
    ...mapGetters([ 'rates', 'userInfo', 'agents', 'workGroups',
      'filterWorkGroups', 'filterWorkGroupMembers', 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getRates', 'filterRate' ]),
    timeInterval (start, end) {
      return moment(start, 'YYYY-MM-DD HH:mm:ss').diff(end, 'YYYY-MM-DD HH:mm:ss')
    },
    formatClient (client) {
      // TODO: åŒºåˆ†ç”µè„‘ã€æ‰‹æœºç½‘ç«™
      if (client === constants.CLIENT_WEB) {
        return 'ç½‘ç«™'
      } else if (client === constants.CLIENT_WAP) {
        return 'æ‰‹æœºç½‘ç«™'
      } else if (client === constants.CLIENT_ANDROID) {
        return 'å®‰å“App'
      } else if (client === constants.CLIENT_IOS) {
        return 'è‹¹æžœApp'
      } else if (client === constants.CLIENT_WECHAT_MP) {
        return 'å…¬ä¼—å·'
      } else if (client === constants.CLIENT_WECHAT_MINI) {
        return 'å°ç¨‹åº'
      }
    },
    gotoHelpdoc () {
      let helpUrl = constants.HELP_BASE_URL + '201809042103521'
      if (this.electron) {
        ipcRenderer.send('open-external-url', helpUrl)
      } else {
        window.open(helpUrl)
      }
    },
    handleWorkGroupCommand (command) {
      console.log(command)
      if (command === 'all') {
        this.currentWorkGroup = 'å…¨éƒ¨å·¥ä½œç»„'
      } else {
        this.currentWorkGroup = command
      }
    },
    handleAgentCommand (command) {
      console.log(command)
      if (command === 'all') {
        this.currentAgent = 'å…¨éƒ¨å®¢æœ'
      } else {
        this.currentAgent = command
      }
    },
    handleClientCommand (command) {
      console.log(command)
      this.currentClientCommand = command
      if (command === 'all') {
        this.currentClient = 'å…¨éƒ¨æ¥æº'
      } else if (command === 'web') {
        this.currentClient = 'ç½‘ç«™'
      } else if (command === 'android') {
        this.currentClient = 'å®‰å“'
      } else if (command === 'ios') {
        this.currentClient = 'è‹¹æžœ'
      } else if (command === 'mp') {
        this.currentClient = 'å…¬ä¼—å·'
      } else if (command === 'mini') {
        this.currentClient = 'å°ç¨‹åº'
      }
    },
    handleQuery () {
      // å¦‚æžœæ‰€æœ‰é€‰é¡¹éƒ½æ˜¯é»˜è®¤çš„ï¼Œé‚£ä¹ˆç›´æŽ¥è°ƒç”¨åˆ†é¡µèŽ·å–æŽ¥å£ï¼Œä¸éœ€è¦è°ƒç”¨æœç´¢æŽ¥å£
      if (this.createdAtInterval[0] === '' && this.createdAtInterval[1] === '' && this.nickname === '' &&
        this.pageSize === 20 && this.currentPage === 1 && this.currentWorkGroup === 'å…¨éƒ¨å·¥ä½œç»„' &&
        this.currentAgent === 'å…¨éƒ¨å®¢æœ' && this.currentClient === 'å…¨éƒ¨æ¥æº') {
        let payload = { page: 0, size: 20 }
        this.getRates(payload)
        return
      }
      // éžç®¡ç†å‘˜åªèƒ½æŸ¥è¯¢è‡ªå·±çš„åŽ†å²è®°å½•
      // TODO: å®¢æœç»„é•¿å¯ä»¥æŸ¥è¯¢ç»„å†…æ•°æ®
      if (!this.showAuthority.admin) {
        console.log('not admin')
        this.currentAgent = this.userInfo.realName
      } else {
        console.log('admin')
      }
      //
      if (this.createdAtInterval === null) {
        this.createdAtInterval = [ '', '' ]
      }
      let payload = {
        page: this.currentPage - 1,
        size: this.pageSize,
        nickname: this.nickname,
        createdAtStart: moment(this.createdAtInterval[0]).format('YYYY-MM-DD HH:mm:ss'),
        createdAtEnd: moment(this.createdAtInterval[1]).format('YYYY-MM-DD HH:mm:ss'),
        workGroupNickname: this.currentWorkGroup === 'å…¨éƒ¨å·¥ä½œç»„' ? 'all' : this.currentWorkGroup,
        agentRealName: this.currentAgent === 'å…¨éƒ¨å®¢æœ' ? 'all' : this.currentAgent,
        client: this.currentClientCommand
      }
      console.log('query', payload)
      this.filterRate(payload)
    },
    handleExport () {
      console.log('export')
    },
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getRates(payload)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getRates(payload)
    },
    handleView (index, row) {
      console.log(index, row)
      this.viewThreadTid = row.thread.tid
      this.messageDialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
    }
  },
  created () {
    // console.log('created histories')
  },
  mounted () {
    // console.log('mounted histories')
  }

}
</script>

<style scoped>
#bd-rate {
