<template>
  <!-- TODO: å¯¹æŽ¥å·¥ä½œæµç³»ç»Ÿ -->
  <div id='bd-ticket'>
    <el-tabs v-model="activeName" @tab-click="handleClick" style="height: 100%;" @edit="handleTabsEdit">
      <el-tab-pane label="å·¥å•ç³»ç»Ÿ" name="all" style="height: 100%;">
        <bd-all></bd-all>
      </el-tab-pane>
      <el-tab-pane label="é‚®ä»¶æ¨¡æ¿" name="email" style="height: 100%;">
        <bd-email></bd-email>
      </el-tab-pane>
      <el-tab-pane label="çŸ­ä¿¡æ¨¡æ¿" name="sms" style="height: 100%;">
        <bd-sms></bd-sms>
      </el-tab-pane>
      <el-tab-pane label="æ„è§åé¦ˆ" name="feedback" style="height: 100%;">
        <bd-feedback></bd-feedback>
      </el-tab-pane>
      <el-tab-pane closable style="height: 100%;"
        :key="item.name"
        v-for="item in editableTabs"
        :label="item.title"
        :name="item.name">
        <bd-new v-if="!item.edit"></bd-new>
        <bd-edit v-if="item.edit" :article="item.article"></bd-edit>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdAll from '@/components/dashboard/main/ticket/children/BdAll'
import BdEmail from '@/components/dashboard/main/ticket/children/BdEmail'
import BdSms from '@/components/dashboard/main/ticket/children/BdSms'
import BdFeedback from '@/components/dashboard/main/ticket/children/BdFeedback'
import BdNew from '@/components/dashboard/main/ticket/children/BdNew'
import BdEdit from '@/components/dashboard/main/ticket/children/BdEdit'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-ticket',
  components: { BdAll, BdEmail, BdSms, BdFeedback, BdNew, BdEdit },
  data () {
    return {
      activeName: 'all',
      tabIndex: 1,
      editableTabs: []
    }
  },
  computed: {
    ...mapGetters([ 'showAuthority' ])
  },
  methods: {
    ...mapActions([]),
    handleClick (tab, event) {
      //
    },
    handleTabsEdit (targetName, action) {
      // console.log(targetName, action)
      if (action === 'remove') {
        let tabs = this.editableTabs
        let activeName = 'all'
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1]
              if (nextTab) {
                activeName = nextTab.name
              }
            }
          })
        }
        this.activeName = 'all'
        this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      }
    }
  },
  mounted () {
    //
    bus.$on(constants.EVENT_BUS_SUPPORT_NEW_TICKET, message => {
      this.editableTabs.push({
        title: 'åˆ›å»ºæ–°å·¥å•' + this.tabIndex,
        name: '' + this.tabIndex,
        edit: false
      })
      this.activeName = '' + this.tabIndex
      this.tabIndex++
    })
    bus.$on(constants.EVENT_BUS_SUPPORT_EDIT_TICKET, message => {
      //
    })
  }
}
</script>

<style scoped>

</style>



