<template>
  <div id='bd-thread-middle'>
    <bd-top></bd-top>
    <bd-message></bd-message>
    <bd-input></bd-input>
  </div>
</template>

<script>
import BdTop from '@/components/dashboard/main/chat/children/thread/middle/BdTop'
import BdMessage from '@/components/dashboard/main/chat/children/thread/middle/BdMessage'
import BdInput from '@/components/dashboard/main/chat/children/thread/middle/BdInput'

export default {
  name: 'bd-thread-middle',
  components: { BdTop, BdMessage, BdInput },
  data () {
    return {}
  }
}
</script>

<style scoped>
#bd-thread-middle {
  height: 100%;
}
</style>



