<template>
  <!-- TODO: å¢žåŠ table loadingæ•ˆæžœ -->
  <div id='bd-all'>
    <el-row>
      <el-col :span="4" id="left-col">
        <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 15px;" @click="gotoHelpdoc()" plain>å¸®åŠ©æ–‡æ¡£</el-button>
        <el-button size="mini" type="success" style="float: left; margin-bottom: 15px;" @click="addArticle">æ·»åŠ æ–‡ç« </el-button>
        <el-input size="mini" placeholder="æœç´¢åˆ†ç±»" v-model="filterText"></el-input>
        <el-tree class="filter-tree"
          ref="cateTree"
          default-expand-all
          :data="supportCategories"
          :props="defaultProps"
          :filter-node-method="filterNode"
          @node-click="handleNodeClick">
        </el-tree>
        <el-button size="mini" type="success" @click="addCategory">æ·»åŠ </el-button>
        <el-button size="mini" type="primary" @click="editCategory">ç¼–è¾‘</el-button>
        <el-button size="mini" type="danger" @click="removeCategory">åˆ é™¤</el-button>
      </el-col>
      <el-col :span="20" id="right-col">
        <el-pagination style="float: right;"
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="supportArticles.totalElements">
        </el-pagination>
        <el-table stripe size="mini" height="calc(100% - 80px)"
          :data="supportArticles.content" style="width: 100%; text-align: left;">
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column prop="title" label="æ ‡é¢˜"></el-table-column>
          <!-- <el-table-column prop="content" label="å†…å®¹"></el-table-column> -->
          <el-table-column prop="recommend" label="é¦–é¡µæŽ¨è" width="100" sortable>
            <template slot-scope="scope">
              <span v-if="scope.row.recommend">æ˜¯</span>
              <span v-if="!scope.row.recommend">å¦</span>
            </template>
          </el-table-column>
          <el-table-column prop="readCount" label="é˜…è¯»æ•°ç›®" width="100" sortable></el-table-column>
          <el-table-column prop="rateHelpful" label="æœ‰å¸®åŠ©/æ€»æ•°" width="120" sortable>
            <template slot-scope="scope">
              <span>{{ scope.row.rateHelpful }}/{{ rateTotal(scope.row) }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="createdAt" label="åˆ›å»ºæ—¶é—´" width="150" sortable></el-table-column>
          <el-table-column prop="updatedAt" label="æ›´æ–°æ—¶é—´" width="150" sortable></el-table-column>
          <el-table-column fixed="right" label="æ“ä½œ" width="220">
            <template slot-scope="scope">
              <el-button size="mini" type="success" @click="handleView(scope.$index, scope.row)">é¢„è§ˆ</el-button>
              <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination style="float: right; margin-top: 10px;"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[20, 100, 200, 300, 400]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="supportArticles.totalElements">
        </el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

let id = 1000

export default {
  name: 'bd-all',
  data () {
    return {
      currentCategory: {
        id: 0,
        name: '',
        children: []
      },
      loading: false,
      pageSize: 20,
      currentPage: 1,
      filterText: '',
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      helpUrl: constants.HELP_URL,
      electron: constants.IS_ELECTRON
    }
  },
  computed: {
    ...mapGetters([ 'supportCategories', 'supportArticles' ])
  },
  watch: {
    filterText (val) {
      this.$refs.cateTree.filter(val)
    }
  },
  methods: {
    ...mapActions([ 'getCategories', 'createCategory', 'updateCategory', 'deleteCategory',
      'getSupportArticles', 'createSupportArticle', 'updateSupportArticle', 'deleteSupportArticle' ]),
    gotoHelpdoc () {
      if (this.electron) {
        ipcRenderer.send('open-external-url', this.helpUrl)
      } else {
        window.open(this.helpUrl)
      }
    },
    gotoArticle (aid) {
      let url = constants.HELP_BASE_URL + aid
      if (this.electron) {
        ipcRenderer.send('open-external-url', url)
      } else {
        window.open(url)
      }
    },
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    rateTotal (row) {
      return row.rateHelpful + row.rateUseless
    },
    handleNodeClick (data) {
      this.currentCategory.id = data.id
      this.currentCategory.name = data.name
      this.currentCategory.children = data.children
      let payload = {
        categoryId: this.currentCategory.id,
        page: 0,
        size: 20
      }
      this.getSupportArticles(payload)
    },
    append (data) {
      const newChild = { id: id++, label: 'testtest', children: [] }
      if (!data.children) {
        this.$set(data, 'children', [])
      }
      data.children.push(newChild)
    },
    remove (node, data) {
      const parent = node.parent
      const children = parent.data.children || parent.data
      const index = children.findIndex(d => d.id === data.id)
      children.splice(index, 1)
    },
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
      //
      let payload = {
        categoryId: this.currentCategory.id,
        page: this.currentPage - 1,
        size: this.pageSize
      }
      this.getSupportArticles(payload)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = {
        categoryId: this.currentCategory.id,
        page: this.currentPage - 1,
        size: this.pageSize
      }
      this.getSupportArticles(payload)
    },
    addArticle () {
      bus.$emit(constants.EVENT_BUS_SUPPORT_NEW_ARTICLE)
    },
    addCategory () {
      this.$prompt('è¯·è¾“å…¥ç±»åˆ«', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ'
      }).then(({ value }) => {
        console.log(value)
        if (value !== undefined && value !== null && value.length > 0) {
          let payload = {
            parent_id: this.currentCategory.id,
            name: value,
            type: constants.CATEGORY_TYPE_SUPPORT
          }
          this.createCategory(payload)
        }
      }).catch(() => {
        // å–æ¶ˆ
      })
    },
    editCategory () {
      if (this.currentCategory.id === 0) {
        this.$message.error('è¯·é€‰æ‹©è¦ç¼–è¾‘çš„ç±»åˆ«')
      } else {
        this.$prompt('ç¼–è¾‘ç±»åˆ«', 'æç¤º', {
          confirmButtonText: 'ç¡®å®š',
          cancelButtonText: 'å–æ¶ˆ',
          inputValue: this.currentCategory.name
        }).then(({ value }) => {
          console.log(value)
          if (value !== undefined && value !== null && value.length > 0) {
            let payload = {
              category_id: this.currentCategory.id,
              name: value
            }
            this.updateCategory(payload)
          } else {
            this.$message.error('ä¸èƒ½ä¸ºç©º')
          }
        }).catch(() => {
          // å–æ¶ˆ
        })
      }
    },
    removeCategory () {
      if (this.currentCategory.id === 0) {
        this.$message.error('ä¸å…è®¸åˆ é™¤')
        return
      }
      if (this.currentCategory.children.length > 0) {
        this.$message.error('è¯·é¦–å…ˆåˆ é™¤å­åˆ†ç±»')
        return
      }
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteCategory(this.currentCategory.id)
        this.currentCategory.id = 0
      }).catch(() => {
      })
    },
    handleView (index, row) {
      this.gotoArticle(row.aid)
    },
    handleEdit (index, row) {
      // console.log('bd-all:', index, row)
      bus.$emit(constants.EVENT_BUS_SUPPORT_EDIT_ARTICLE, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteSupportArticle(row.id)
      }).catch(() => {
        //
      })
    }
  },
  created () {
  }
}
</script>

<style scoped>
#bd-all {
  height: 100%;
  overflow-y: auto;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
  /* border: 1px solid gainsboro; */
}
#right-col{
  height: 100%;
  /* border: 1px solid gainsboro; */
}
.filter-tree {
  margin-top: 10px;
  margin-bottom: 10px;
  height: calc(100% - 130px);
  overflow-y: auto;
}
</style>



