<template>
  <div id='bd-support'>
    <el-tabs v-model="activeName" @tab-click="handleClick" style="height: 100%;" @edit="handleTabsEdit">
      <el-tab-pane label="å¤–éƒ¨å¸®åŠ©ä¸­å¿ƒ" name="all" style="height: 100%;">
        <bd-all></bd-all>
      </el-tab-pane>
      <el-tab-pane label="å†…éƒ¨çŸ¥è¯†åº“" name="knowledge" style="height: 100%;" v-if="showAuthority.debug">
        <bd-knowledge></bd-knowledge>
      </el-tab-pane>
      <el-tab-pane label="ç»Ÿè®¡æ•°æ®" name="statistic" style="height: 100%;" v-if="showAuthority.debug">
        <bd-statistic></bd-statistic>
      </el-tab-pane>
      <el-tab-pane closable style="height: 100%;"
        :key="item.name"
        v-for="item in editableTabs"
        :label="item.title"
        :name="item.name">
        <bd-new v-if="!item.edit"></bd-new>
        <bd-edit v-if="item.edit" :article="item.article"></bd-edit>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdAll from '@/components/dashboard/main/support/children/BdAll'
import BdNew from '@/components/dashboard/main/support/children/BdNew'
import BdEdit from '@/components/dashboard/main/support/children/BdEdit'
import BdStatistic from '@/components/dashboard/main/support/children/BdStatistic'
import BdKnowledge from '@/components/dashboard/main/support/children/BdKnowledge'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-support',
  components: { BdAll, BdNew, BdEdit, BdStatistic, BdKnowledge },
  data () {
    return {
      activeName: 'all',
      tabIndex: 1,
      editableTabs: []
    }
  },
  computed: {
    ...mapGetters([ 'supportCategories', 'supportArticles', 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getCategories', 'createCategory', 'deleteCategory',
      'getSupportArticles', 'createSupportArticle', 'updateSupportArticle',
      'deleteSupportArticle', 'changeCurrentMainView' ]),
    handleClick (tab, event) {
      if (tab.name === 'all') {
        this.getCategories()
        let payload = {
          categoryId: 0,
          page: 0,
          size: 20
        }
        this.getSupportArticles(payload)
      }
    },
    handleTabsEdit (targetName, action) {
      // console.log(targetName, action)
      if (action === 'remove') {
        let tabs = this.editableTabs
        let activeName = 'all'
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1]
              if (nextTab) {
                activeName = nextTab.name
              }
            }
          })
        }
        this.activeName = 'all'
        this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      }
    }
  },
  created () {
    this.getCategories()
    let payload = {
      categoryId: 0,
      page: 0,
      size: 20
    }
    this.getSupportArticles(payload)
  },
  mounted () {
    this.changeCurrentMainView('support')
    bus.$on(constants.EVENT_BUS_SUPPORT_NEW_ARTICLE, message => {
      this.editableTabs.push({
        title: 'åˆ›å»ºæ–°æ–‡ç« ' + this.tabIndex,
        name: '' + this.tabIndex,
        edit: false
      })
      this.activeName = '' + this.tabIndex
      this.tabIndex++
    })
    bus.$on(constants.EVENT_BUS_SUPPORT_EDIT_ARTICLE, message => {
      // console.log('on receive:', message)
      // è½¬æ¢ç±»åˆ«
      let transformCategories = []
      for (let i = 0; i < message.categories.length; i++) {
        const category = message.categories[i]
        transformCategories.push(category.id)
      }
      let article = {
        id: message.id,
        title: message.title,
        content: message.content,
        recommend: message.recommend,
        markdown: message.markdown,
        published: message.published,
        summary: message.summary,
        categories: transformCategories
      }
      //
      this.editableTabs.push({
        title: 'ç¼–è¾‘æ–‡ç« ' + this.tabIndex,
        name: '' + this.tabIndex,
        edit: true,
        article: article
      })
      this.activeName = '' + this.tabIndex
      this.tabIndex++
    })
  }
}
</script>

<style scoped>
#bd-support {
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
  /* background-color: antiquewhite */
}
#right-col{
  height: 100%;
}
</style>



