<template>
    <div id='bd-chat'>
      <el-tabs v-model="activeName" @tab-click="handleClick" style="height: 100%;">
        <el-tab-pane v-if="showAuthority.thread" label="å½“å‰ä¼šè¯" name="thread" style="height: 100%;">
          <bd-thread></bd-thread>
        </el-tab-pane>
        <el-tab-pane v-if="showAuthority.debug" label="åœ¨çº¿è®¿å®¢" name="visitor" style="height: 100%;">
          <bd-visitor></bd-visitor>
        </el-tab-pane>
        <el-tab-pane v-if="showAuthority.history" label="åŽ†å²è®°å½•" name="history" style="height: 100%;">
          <bd-history></bd-history>
        </el-tab-pane>
        <el-tab-pane v-if="showAuthority.debug" label="ä¼šè¯ç›‘æŽ§" name="monitor" style="height: 100%;">
          <bd-monitor></bd-monitor>
        </el-tab-pane>
        <el-tab-pane v-if="showAuthority.debug" label="è´¨é‡æ£€æŸ¥" name="quality" style="height: 100%;">
          <bd-quality></bd-quality>
        </el-tab-pane>
      </el-tabs>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdThread from '@/components/dashboard/main/chat/children/BdThread'
import BdVisitor from '@/components/dashboard/main/chat/children/BdVisitor'
import BdHistory from '@/components/dashboard/main/chat/children/BdHistory'
import BdMonitor from '@/components/dashboard/main/chat/children/BdMonitor'
import BdQuality from '@/components/dashboard/main/chat/children/BdQuality'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-chat',
  components: { BdThread, BdVisitor, BdHistory, BdMonitor, BdQuality },
  data () {
    return {
      activeName: 'thread'
    }
  },
  computed: {
    ...mapGetters([ 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getCuws', 'getThread', 'getQueue', 'getBrowse',
      'getHistoryThread', 'getMonitor', 'getQuality', 'getWorkGroups',
      'getAllWorkGroups', 'getWorkGroupMembers',
      'getRoles', 'changeCurrentMainView' ]),
    handleClick (tab, event) {
      let payload = { page: 0, size: 20 }
      if (tab.name === 'thread') {
        this.getThread()
        this.getCuws()
        this.getQueue(payload)
      } else if (tab.name === 'visitor') {
        this.getBrowse(payload)
      } else if (tab.name === 'history') {
        this.getHistoryThread(payload)
        this.getAllWorkGroups()
        this.getWorkGroupMembers()
      } else if (tab.name === 'monitor') {
        this.getMonitor(payload)
      } else if (tab.name === 'quality') {
        this.getQuality(payload)
      }
    }
  },
  created () {
    bus.$on(constants.EVENT_BUS_MESSAGE_HISTORY, message => {
      console.log('add message history tab')
    })
  },
  mounted () {
    // console.log('chat mounted')
    this.changeCurrentMainView('chat')
  }
}
</script>

<style scoped>
#bd-chat {
 height: 100%;
}
</style>



