<template>
  <div id='bd-thread-left'>
    <div class="thread-search">
      <!-- TODO:è¿œç¨‹æœç´¢ -->
      <el-input size="mini" placeholder="æœç´¢" suffix-icon="el-icon-search" v-model="search" clearable></el-input>
    </div>
    <!-- TODO: å¯¹ç¦»çº¿ç”¨æˆ·æ·»åŠ æ›´æ˜¾è‘—çš„æ ‡è®° -->
    <!-- TODO: åˆ†ç»„åŠ¨æ€æ˜¾ç¤ºï¼Œåªæœ‰ä¸€ä¸ªåˆ†ç»„åˆ™ä¸æ˜¾ç¤ºå­åˆ†ç»„, ä¼˜åŒ– -->
    <div v-if="showThread" style="height: calc(100% - 100px); overflow-y: auto;">
      <el-collapse v-model="activeNames" @change="handleCollapseChange">
        <el-collapse-item name="chatting">
          <template slot="title">
            <div style="margin-left: 20px;">
              {{ chattingThreadTitle }}
            </div>
          </template>
          <el-collapse>
            <li v-for="workGroup in transformChattingThread" :key="workGroup.wid">
              <el-collapse-item v-if="workGroup.group" :name="workGroup.wid">
                <template slot="title">
                  <div style="margin-left: 40px;">
                    {{ workGroupTitle(workGroup) }}
                  </div>
                </template>
                <li v-for="thread in workGroup.children" :key="thread.tid" class="list-item"
                  :class="{ ' active': thread.tid === userCurrentThread.tid }"
                  @click="selectThread(thread)">
                  <div class="list-left">
                    <img class="avatar" width="20" height="20" :alt="thread.visitor.nickname" :src="thread.visitor.avatar">
                  </div>
                  <div class="list-right">
                    <div class="top">
                      <span class="nickname">{{ thread.visitor.nickname }}</span>
                      <span class="timestamp">
                        {{ thread.timestamp | time}}
                        <span v-if="thread.unreadCount !== 0" class="unreadCount">{{ thread.unreadCount }}</span>
                      </span>
                    </div>
                    <div class="bottom">
                      <span class="content">{{ thread.content }}</span>
                    </div>
                  </div>
                </li>
              </el-collapse-item>
            </li>
          </el-collapse>
        </el-collapse-item>
        <el-collapse-item name="queuing">
          <template slot="title">
            <div style="margin-left: 20px;">
              {{ queuingTitle }}
            </div>
          </template>
          <li v-for="queue in filterQueues" :key="queue.qid" class="list-item">
            <div class="list-left">
              <img class="avatar" width="20" height="20" :alt="queue.visitor.nickname" :src="queue.visitor.avatar">
            </div>
            <div class="list-right">
              <div class="top">
                <span class="nickname">{{ queue.visitor.nickname }}</span>
                <span class="timestamp">{{ queue.createdAt | time}}</span>
              </div>
              <div class="bottom-queue">
                <el-button size="mini" style="float: right;" @click="doAcceptQueue(queue)" plain>æŽ¥å…¥</el-button>
              </div>
            </div>
          </li>
        </el-collapse-item>
        <el-collapse-item name="closed">
          <template slot="title">
            <div style="margin-left: 20px;">
              {{ closedThreadTitle }}
            </div>
          </template>
          <el-collapse accordion v-for="workGroup in transformClosedThread" :key="workGroup.wid">
            <el-collapse-item v-if="workGroup.group" :name="workGroup.wid">
              <template slot="title">
                  <div style="margin-left: 40px;">
                    {{ workGroupTitle(workGroup) }}
                  </div>
                </template>
              <li v-for="thread in workGroup.children" :key="thread.tid" class="list-item"
                :class="{ ' active': thread.tid === userCurrentThread.tid }"
                @click="selectThread(thread)">
                <div class="list-left">
                  <img class="avatar" width="20" height="20" :alt="thread.visitor.nickname" :src="thread.visitor.avatar">
                </div>
                <div class="list-right">
                  <div class="top">
                    <span class="nickname">{{ thread.visitor.nickname }}</span>
                    <span class="timestamp">
                      {{ thread.timestamp | time}}
                      <span v-if="thread.unreadCount !== 0" class="unreadCount">{{ thread.unreadCount }}</span>
                    </span>
                  </div>
                  <div class="bottom">
                    <span class="content">{{ thread.content }}</span>
                  </div>
                </div>
              </li>
            </el-collapse-item>
          </el-collapse>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-thread-left',
  data () {
    return {
      type: 'ä¼šè¯',
      search: '',
      pageSize: 20,
      currentContactPage: 1,
      currentGroupPage: 1,
      activeNames: [ 'chatting', 'queuing', 'closed' ]
    }
  },
  computed: {
    ...mapGetters([ 'userThreads', 'userChattingThreads', 'userClosedThreads',
      'userCurrentThread', 'userCurrentContact', 'userCurrentGroup', 'userQueues',
      'showAuthority' ]),
    filterdChattingThreads () {
      return this.userChattingThreads.filter(thread => {
        return thread.visitor.nickname.includes(this.search) || thread.content.includes(this.search)
      })
    },
    filterQueues () {
      return this.userQueues.content.filter(queue => {
        return queue.visitor.nickname.includes(this.search) || queue.content.includes(this.search)
      })
    },
    filterdClosedThreads () {
      return this.userClosedThreads.filter(thread => {
        return thread.visitor.nickname.includes(this.search) || thread.content.includes(this.search)
      })
    },
    transformChattingThread () {
      let chattingThreadTree = [{ id: constants.THREAD_TYPE_APPOINTED, group: true, label: 'æ— åˆ†ç»„', children: [] }]
      for (let i = 0; i < this.filterdChattingThreads.length; i++) {
        const thread = this.filterdChattingThreads[i]
        let contains = false
        // chattingThreadTree åˆ†ç»„
        for (let j = 0; j < chattingThreadTree.length; j++) {
          const group = chattingThreadTree[j]
          // æŒ‡å®šå®¢æœä¼šè¯
          if (thread.workGroup === null || thread.workGroup === undefined) {
            chattingThreadTree[0].children.push(thread)
            contains = true
          } else if (group.id === thread.workGroup.wid) {
            chattingThreadTree[j].children.push(thread)
            contains = true
          }
        }
        if (!contains) {
          chattingThreadTree.push({ id: thread.workGroup.wid, group: true, label: thread.workGroup.nickname, children: [thread] })
        }
      }
      return chattingThreadTree
    },
    transformClosedThread () {
      let closedThreadTree = [{ id: constants.THREAD_TYPE_APPOINTED, group: true, label: 'æ— åˆ†ç»„', children: [] }]
      for (let i = 0; i < this.filterdClosedThreads.length; i++) {
        const thread = this.filterdClosedThreads[i]
        let contains = false
        for (let j = 0; j < closedThreadTree.length; j++) {
          const group = closedThreadTree[j]
          if (thread.workGroup === null || thread.workGroup === undefined) {
            closedThreadTree[0].children.push(thread)
            contains = true
          } else if (group.id === thread.workGroup.wid) {
            closedThreadTree[j].children.push(thread)
            contains = true
          }
        }
        if (!contains) {
          closedThreadTree.push({ id: thread.workGroup.wid, group: true, label: thread.workGroup.nickname, children: [thread] })
        }
      }
      return closedThreadTree
    },
    showChatType () {
      return !constants.IS_PRODUCTION || this.showAuthority.debug
    },
    showThread () {
      return this.type === 'ä¼šè¯'
    },
    showContacts () {
      return this.type === 'åŒäº‹'
    },
    showGroups () {
      return this.type === 'ç¾¤èŠ'
    },
    chattingThreadTitle () {
      // åœ¨çº¿ / æ€»æ•°
      return 'ä¼šè¯ä¸­ï¼ˆ' + this.filterdChattingThreads.length + 'ï¼‰'
    },
    queuingTitle () {
      // åœ¨çº¿ / æ€»æ•°
      return 'æŽ’é˜Ÿä¸­ï¼ˆ' + this.filterQueues.length + 'ï¼‰'
    },
    closedThreadTitle () {
      // åœ¨çº¿ / æ€»æ•°
      return 'ä¼šè¯ç»“æŸï¼ˆ' + this.filterdClosedThreads.length + 'ï¼‰'
    }
  },
  methods: {
    ...mapActions([ 'updateCurrentThread', 'updateContact', 'updateGroup', 'updateChatType',
      'getThread', 'getQueue', 'acceptQueue', 'getGroups', 'getAgents' ]),
    workGroupTitle (workGroup) {
      return workGroup.label + ' (' + workGroup.children.length + ') '
    },
    handleCollapseChange (val) {
      // console.log(val)
    },
    handleCurrentContactChange (val) {
      // console.log(val)
      this.currentContactPage = val
      let payload = { page: this.currentContactPage - 1, size: 20 }
      this.getAgents(payload)
    },
    handleCurrentGroupChange (val) {
      // console.log(val)
      this.currentGroupPage = val
      let payload = { page: this.currentGroupPage - 1, size: 20 }
      this.getGroups(payload)
    },
    selectThread (thread) {
      this.updateCurrentThread(thread)
    },
    selectContact (contact) {
      // console.log('selectContact', contact)
      this.updateContact(contact)
    },
    selectGroup (group) {
      // console.log('selectGroup', group)
      this.updateGroup(group)
    },
    selectQueue (queue) {
      // console.log('selectQueue')
    },
    doAcceptQueue (queue) {
      // console.log('doAcceptQueue')
      this.acceptQueue(queue.qid)
    },
    onTypeChange (type) {
      // console.log(type)
      let payload = { page: 0, size: 20 }
      if (type === 'åŒäº‹') {
        this.updateChatType(constants.CHAT_TYPE_CONTACT)
        this.getAgents(payload)
      } else if (type === 'ç¾¤èŠ') {
        this.updateChatType(constants.CHAT_TYPE_GROUP)
        this.getGroups(payload)
      } else if (type === 'ä¼šè¯') {
        this.updateChatType(constants.CHAT_TYPE_THREAD)
        this.getThread()
        this.getQueue(payload)
      }
    }
  },
  filters: {
    // å°†æ—¥æœŸè¿‡æ»¤ä¸º hour:minutes
    time (value) {
      if (typeof value === 'string') {
        return moment(value).format('MM-DD HH:mm')
      } else {
        return value
      }
    }
  }
}
</script>

<style scoped>
#bd-thread-left {
  width: 100%;
  height: 100%;
  float: left;
}
.thread-search {
  padding: 12px 12px 12px 12px;
}
.list-item {
  padding: 5px 5px 5px 5px;
  margin-bottom: 0px;
  border-bottom: 1px solid beige;
  height: 50px;
}
.list-item:hover {
  background-color: rgb(220,220,220)
}
.active {
  background-color:darkkhaki
}
.list-item .avatar {
  border-radius: 2px;
  margin-right: 2px;
}
li {
  list-style: none;
}
.list-left {
  margin-top: 4px;
  margin-left: 2px;
  width: 25px;
  height: 25px;
  float: left;
}
.list-right {
  margin-left: 32px;
}
.list-right .top {
  margin-top: 2px;
  text-align: left;
}
span.nickname {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
  width: calc(100% - 100px);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
span.timestamp {
  float: right;
  color:gray;
  font-size: 12px;
  vertical-align: top;
  width: 68px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.list-right .bottom {
  width: 0%;
  text-align: left;
}
.list-right .bottom-queue {
  width: 90%;
  text-align: left;
}
.list-right .bottom .content{
  font-size: 12px;
  width: 150px;
  height: 100%;
  color: gray;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.list-right .unreadCount {
    margin-top: 4px;
    float: right;
    font-size: 10px;
    width: 12px;
    height: 12px;
    line-height: 12px;
    text-align: center;
    border-radius:5px;
    color: white;
    background-color: red;
}
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  padding-right: 8px;
}
</style>



