<template>
  <div id='bd-internal-left'>
    <div style="margin-top: 10px">
      <el-radio-group v-model="type" size="mini" @change="onTypeChange">
        <el-radio-button label="ä¼šè¯"></el-radio-button>
        <el-radio-button label="åŒäº‹"></el-radio-button>
        <el-radio-button label="ç¾¤èŠ"></el-radio-button>
      </el-radio-group>
    </div>
    <div class="thread-search">
      <el-input size="mini" placeholder="æœç´¢" suffix-icon="el-icon-search" v-model="search"></el-input>
    </div>
    <li v-if="showThread" v-for="thread in filterdThreads" :key="thread.id" class="list-item"
      :class="{ ' active': thread.id === userCurrentThread.id }"
      @click="selectThread(thread)">
      <div class="list-left">
        <img class="avatar" width="30" height="30" :alt="thread.visitor.nickname" :src="thread.visitor.avatar">
      </div>
      <div class="list-right">
        <div class="top">
          <span class="name">{{ thread.visitor.nickname }}</span>
          <span class="timestamp">{{ thread.timestamp | time}}</span>
        </div>
        <div class="bottom">
          <span class="content">{{ thread.content }}</span>
          <span v-if="thread.unreadCount !== 0" class="unreadCount">{{ thread.unreadCount }}</span>
        </div>
      </div>
    </li>
    <li v-if="showContacts" v-for="agent in agents.content" :key="agent.id" class="list-item">
      <div class="list-left">
        <img class="avatar" width="30" height="30" :alt="agent.nickname" :src="agent.avatar">
      </div>
      <div class="list-right">
        <div class="top">
          <span class="name">{{ agent.nickname }}</span>
        </div>
        <div class="bottom">
          <span class="content">{{ agent.description }}</span>
        </div>
      </div>
    </li>
    <li v-if="showGroups" v-for="workGroup in workGroups.content" :key="workGroup.id" class="list-item">
      <div class="list-left">
        <img class="avatar" width="30" height="30" :alt="workGroup.nickname" :src="workGroup.avatar">
      </div>
      <div class="list-right">
        <div class="top">
          <span class="name">{{ workGroup.nickname }}</span>
        </div>
        <div class="bottom">
          <span class="content">{{ workGroup.description }}</span>
        </div>
      </div>
    </li>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'bd-internal-left',
  data () {
    return {
      type: 'ä¼šè¯',
      search: ''
    }
  },
  computed: {
    ...mapGetters([ 'userThreads', 'userCurrentThread', 'agents', 'workGroups' ]),
    filterdThreads () {
      return this.userThreads.filter(thread => {
        return thread.visitor.nickname.includes(this.search) || thread.content.includes(this.search)
      })
    },
    showThread () {
      return this.type === 'ä¼šè¯'
    },
    showContacts () {
      return this.type === 'åŒäº‹'
    },
    showGroups () {
      return this.type === 'ç¾¤èŠ'
    }
  },
  methods: {
    ...mapActions([ 'updateCurrentThread', 'getWorkGroups', 'getAgents' ]),
    selectThread (thread) {
      this.updateCurrentThread(thread)
    },
    selectContact () {},
    selectWorkGroup () {},
    onTypeChange (type) {
      console.log(type)
      let payload = { page: 0, size: 20 }
      if (type === 'åŒäº‹') {
        this.getAgents(payload)
      } else if (type === 'ç¾¤èŠ') {
        this.getWorkGroups(payload)
      }
    }
  },
  filters: {
    // å°†æ—¥æœŸè¿‡æ»¤ä¸º hour:minutes
    time (value) {
      if (typeof value === 'string') {
        let date = new Date(value)
        if (date.getMinutes() < 10) {
          return date.getHours() + ':0' + date.getMinutes()
        } else {
          return date.getHours() + ':' + date.getMinutes()
        }
      } else {
        return value
      }
    }
  }
}
</script>

<style scoped>
#bd-internal-left {
  width: 100%;
  height: 100%;
  float: left;
  overflow-y: auto;
}
.thread-search {
  padding: 12px 12px 12px 12px;
}
.list-item {
  padding: 5px 5px 5px 5px;
  margin-left: 10px;
  margin-bottom: 0px;
  border-bottom: 1px solid beige;
  height: 40px;
}
.list-item:hover {
  background-color: rgb(220,220,220)
}
.active {
  background-color:darkkhaki
}
.list-item .avatar {
  border-radius: 2px;
  margin-right: 12px;
}
li {
  list-style: none;
}
.list-left {
  margin-top: 6px;
  width: 42px;
  height: 42px;
  float: left;
}
.list-right {
  margin-left: 32px;
}
.list-right .top {
  margin-top: 2px;
  text-align: left;
}
span.name {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
}
span.timestamp {
  float: right;
  color:gray;
  font-size: 12px;
  vertical-align: top;
}
.list-right .bottom {
  margin-top: -2px;
  text-align: left;
}
.list-right .bottom .content{
  font-size: 12px;
  width: 130px;
  height: 15px;
  line-height: 15px;
  color: gray;
  bottom: 4px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.list-right .bottom .unreadcount {
  margin-top: 5px;
  float: right;
  font-size: 10px;
  background-color: red;
  width: 12px;
  height: 12px;
  text-align: center;
  border-radius:5px;
}
</style>



