/**
 * åŠ è½½æ›´å¤šèŠå¤©è®°å½•
 * @param {*} param0 commit, state
 */
export const getMessages = ({ commit, state }, size) => {
  let uid = state.user.current_thread.visitor.uid
  let page = state.user.current_thread.page
  let accessToken = state.passport.token.access_token
  https.getMessages(uid, page, size, accessToken, response => {
    console.log(response)
    // æŒä¹…åŒ–èŠå¤©è®°å½•
    response.data.content.forEach(message => {
      commit(types.ADD_USER_MESSAGE, { message })
    })
    // æ›´æ–°åŠ è½½æ›´å¤šä¿¡æ¯
    let first = response.data.first
    let last = response.data.last
    let totalElements = response.data.totalElements
    commit(types.UPDATE_LOAD_MESSAGE_PAGE, { uid, page, first, last, totalElements })
    // èŠå¤©è®°å½•æ»šåŠ¨åˆ°åº•éƒ¨
    bus.$emit(constants.EVENT_BUS_MESSAGE_SCROLL_TO_BOTTOM)
  }, error => {
    console.log(error)
    bus.$emit(constants.EVENT_BUS_NOTIFICATION_ERROR, error)
  })
}

