/**
 * å‘é€åŒæ­¥æ¶ˆæ¯ï¼Œhttp rest api
 * @param {*} param0 commit, state
 */
export const sendMessageSync = ({ commit, state }, payload) => {
  //
  let tid = payload.tid
  let type = payload.type
  let content = payload.content
  let sessionType = payload.sessionType
  //
  let localId = util.guid()
  let accessToken = state.passport.token.access_token
  //
  https.sendMessageSync(tid, type, content, localId, sessionType, accessToken, response => {
    console.log(response)
    //
    if (response.status_code === 200) {
      //
      let message = response.data
      commit(types.ADD_USER_MESSAGE, { message })
    } else {
      // æŠ¥é”™
      bus.$emit(constants.EVENT_BUS_NOTIFICATION_ERROR, response.message)
    }
  }, error => {
    console.log(error)
    bus.$emit(constants.EVENT_BUS_NOTIFICATION_ERROR, error)
  })
}

