/**
 * å–æ¶ˆè®¢é˜…ï¼Œå¿…é¡»æ·»åŠ å‰ç¼€ '/topic/'
 * https://stomp-js.github.io/stomp-websocket/codo/class/Client.html#unsubscribe-dynamic
 * @param topic
 */
export const unsubscribeTopic = ({ commit, state }, topic) => {
  // stompClient.unsubscribe('/topic/' + topic)
  for (let i = state.user.subscribed_topics.length - 1; i >= 0; i--) {
    const object = state.user.subscribed_topics[i]
    if (object.topic === topic) {
      let subscription = object.subscription
      subscription.unsubscribe()
    }
  }
  //
  commit(types.UNSUBSCRIBE_TOPIC, { topic })
}

