<template>
  <div id='bd-status'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-right: 10px;" @click="gotoHelpdoc()" plain>å¸®åŠ©æ–‡æ¡£</el-button>
    <!-- TODO: æ¸…ç©ºè¾“å…¥æ¡† å’Œ æ—¶é—´èŒƒå›´ è§¦å‘è‡ªåŠ¨é‡æ–°æŸ¥è¯¢ -->
    <span style="font-size: 12px;">æ—¶é—´ï¼š</span>
    <el-date-picker style="width: 345px;"
      size="mini"
      v-model="createdAtInterval"
      type="datetimerange"
      :picker-options="pickerOptions"
      range-separator="è‡³"
      start-placeholder="å¼€å§‹æ—¥æœŸ"
      end-placeholder="ç»“æŸæ—¥æœŸ"
      align="right">
    </el-date-picker>
    <el-dropdown @command="handleAgentCommand" v-if="showAuthority.admin">
      <span class="el-dropdown-link" style="margin-left: 10px; font-size: 12px;">
        {{ currentAgent }}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="all">å…¨éƒ¨å®¢æœ</el-dropdown-item>
        <el-dropdown-item v-for="agent in filterWorkGroupMembers" :key="agent.id" :command="agent.realName">{{ agent.realName }}</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dropdown @command="handleClientCommand">
      <span class="el-dropdown-link" style="margin-left: 10px; font-size: 12px;">
        {{ currentClient }}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="all">å…¨éƒ¨æ¥æº</el-dropdown-item>
        <el-dropdown-item command="web">ç½‘ç«™</el-dropdown-item>
        <el-dropdown-item command="android">å®‰å“</el-dropdown-item>
        <el-dropdown-item command="ios">è‹¹æžœ</el-dropdown-item>
        <el-dropdown-item command="mp">å…¬ä¼—å·</el-dropdown-item>
        <el-dropdown-item command="mini">å°ç¨‹åº</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-button size="mini" type="primary" icon="el-icon-search" style="margin-left: 10px;" @click="handleQuery">æŸ¥è¯¢</el-button>
    <el-button size="mini" type="primary" icon="el-icon-download" style="margin-left: 10px;" @click="handleExport" v-if="showAuthority.debug">å¯¼å‡º</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      small
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="statuses.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="statuses.content"
      style="width: 100%; margin-top: 10px;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="user.realName" label="å®¢æœçœŸå"></el-table-column>
      <el-table-column prop="status" label="çŠ¶æ€" sortable></el-table-column>
      <el-table-column prop="createdAt" label="æ—¶é—´" sortable></el-table-column>
      <el-table-column prop="client" label="æ¥æº" width="80"></el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="statuses.totalElements">
    </el-pagination>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-status',
  data () {
    return {
      pickerOptions: {
        shortcuts: [{
          text: 'æœ€è¿‘ä¸€å‘¨',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: 'æœ€è¿‘ä¸€ä¸ªæœˆ',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: 'æœ€è¿‘ä¸‰ä¸ªæœˆ',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      createdAtInterval: [ '', '' ],
      pageSize: 20,
      currentPage: 1,
      dialogVisible: false,
      isEdit: false,
      currentAgent: 'å…¨éƒ¨å®¢æœ',
      currentClient: 'å…¨éƒ¨æ¥æº',
      currentClientCommand: '',
      electron: constants.IS_ELECTRON
    }
  },
  computed: {
    ...mapGetters([ 'statuses', 'agents', 'workGroups', 'filterWorkGroups', 'filterWorkGroupMembers', 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getHistoryStatus', 'filterStatus' ]),
    timeInterval (start, end) {
      return moment(start, 'YYYY-MM-DD HH:mm:ss').diff(end, 'YYYY-MM-DD HH:mm:ss')
    },
    gotoHelpdoc () {
      let helpUrl = constants.HELP_BASE_URL + '201809042103521'
      if (this.electron) {
        ipcRenderer.send('open-external-url', helpUrl)
      } else {
        window.open(helpUrl)
      }
    },
    handleAgentCommand (command) {
      console.log(command)
      if (command === 'all') {
        this.currentAgent = 'å…¨éƒ¨å®¢æœ'
      } else {
        this.currentAgent = command
      }
    },
    handleClientCommand (command) {
      console.log(command)
      this.currentClientCommand = command
      if (command === 'all') {
        this.currentClient = 'å…¨éƒ¨æ¥æº'
      } else if (command === 'web') {
        this.currentClient = 'ç½‘ç«™'
      } else if (command === 'android') {
        this.currentClient = 'å®‰å“'
      } else if (command === 'ios') {
        this.currentClient = 'è‹¹æžœ'
      } else if (command === 'mp') {
        this.currentClient = 'å…¬ä¼—å·'
      } else if (command === 'mini') {
        this.currentClient = 'å°ç¨‹åº'
      }
    },
    handleQuery () {
      if (this.createdAtInterval === null) {
        this.createdAtInterval = [ '', '' ]
      }
      let payload = {
        page: this.currentPage - 1,
        size: this.pageSize,
        createdAtStart: moment(this.createdAtInterval[0]).format('YYYY-MM-DD HH:mm:ss'),
        createdAtEnd: moment(this.createdAtInterval[1]).format('YYYY-MM-DD HH:mm:ss'),
        agentRealName: this.currentAgent === 'å…¨éƒ¨å®¢æœ' ? 'all' : this.currentAgent,
        client: this.currentClientCommand
      }
      console.log('query', payload)
      this.filterStatus(payload)
    },
    handleExport () {
      console.log('export')
    },
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getHistoryStatus(payload)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getHistoryStatus(payload)
    },
    handleEdit (index, row) {
      console.log(index, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
    }
  },
  created () {
    // console.log('created histories')
  },
  mounted () {
    // console.log('mounted histories')
  }

}
</script>

<style scoped>
#bd-status {
  height: 100%;
  text-align: left;
}
</style>



