<template>
  <div id='bd-wenjuan'>
    <!-- TODO: å®¢æœè€ƒè¯•é¢˜ -->
    <el-tabs v-model="activeName" @tab-click="handleClick" style="height: 100%;" @edit="handleTabsEdit">
      <el-tab-pane label="è°ƒæŸ¥é—®å·" name="all" style="height: 100%;">
        <bd-all></bd-all>
      </el-tab-pane>
      <el-tab-pane closable style="height: 100%;"
        :key="item.name"
        v-for="item in editableTabs"
        :label="item.title"
        :name="item.name">
        <bd-new></bd-new>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
// import { mapActions, mapGetters } from 'vuex'
import BdAll from '@/components/dashboard/main/wenjuan/children/BdAll'
import BdNew from '@/components/dashboard/main/wenjuan/children/BdNew'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-wenjuan',
  components: { BdAll, BdNew },
  data () {
    return {
      activeName: 'all',
      tabIndex: 1,
      editableTabs: []
    }
  },
  methods: {
    handleClick (tab, event) {
      //
    },
    handleTabsEdit (targetName, action) {
      // console.log(targetName, action)
      if (action === 'remove') {
        let tabs = this.editableTabs
        let activeName = 'all'
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1]
              if (nextTab) {
                activeName = nextTab.name
              }
            }
          })
        }
        this.activeName = 'all'
        this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      }
    }
  },
  mounted () {
    bus.$on(constants.EVENT_BUS_NEW_QUESTIONNAIRE, message => {
      console.log('new questionnaire')
      this.editableTabs.push({
        title: 'åˆ›å»ºé—®å·' + this.tabIndex,
        name: '' + this.tabIndex
      })
      this.activeName = '' + this.tabIndex
      this.tabIndex++
    })
  }

}
</script>

<style scoped>
#bd-wenjuan {
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
}
#right-col{
  height: 100%;
}
</style>



