<template>
  <!-- TODO: å¢žåŠ æœç´¢ã€å¤šè¯­è¨€é€‰æ‹© -->
  <div id='cm-header'>
    <el-menu style="padding-left:  25px; margin-left: -20px; margin-right: -20px; padding-right:30px;"
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#007bff"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item index="home">
        <template slot="title">{{ title }}</template>
      </el-menu-item>
      <el-menu-item index="login">
        <template slot="title">ç™»å½•</template>
      </el-menu-item>
      <el-menu-item index="register">
        <template slot="title">æ³¨å†Œ</template>
      </el-menu-item>
      <el-menu-item index="forget">
        <template slot="title">æ‰¾å›žå¯†ç </template>
      </el-menu-item>
      <el-menu-item index="download" style="float: right;">
        <template slot="title">ä¸‹è½½å®¢æˆ·ç«¯</template>
      </el-menu-item>
      <el-menu-item index="help" style="float: right;">
        <template slot="title">å¸®åŠ©æ–‡æ¡£</template>
      </el-menu-item>
      <el-menu-item index="chat" style="float: right;">
        <template slot="title">è”ç³»å®¢æœ</template>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
import * as constants from '@/util/constants'

export default {
  name: 'cm-header',
  data () {
    return {
      activeIndex: '0',
      homeUrl: constants.HOME_URL,
      chatUrl: constants.CHAT_URL,
      helpUrl: constants.HELP_URL,
      downloadUrl: constants.DOWNLOAD_URL,
      title: constants.DEFAULT_TITLE,
      electron: constants.IS_ELECTRON
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      // console.log(key, keyPath)
      if (key === 'home') {
        if (this.electron) {
          ipcRenderer.send('open-external-url', this.homeUrl)
        } else {
          window.open(this.homeUrl)
        }
      } else if (key === 'login') {
        this.$router.push('/login')
      } else if (key === 'register') {
        this.$router.push('/register')
      } else if (key === 'forget') {
        this.$router.push('/forget')
      } else if (key === 'chat') {
        if (this.electron) {
          ipcRenderer.send('open-external-url', this.chatUrl)
        } else {
          window.open(this.chatUrl)
        }
      } else if (key === 'help') {
        if (this.electron) {
          ipcRenderer.send('open-external-url', this.helpUrl)
        } else {
          window.open(this.helpUrl)
        }
      } else if (key === 'download') {
        if (this.electron) {
          ipcRenderer.send('open-external-url', this.downloadUrl)
        } else {
          window.open(this.downloadUrl)
        }
      }
    }
  }
}
</script>

<style scoped>

</style>



