<template>
  <div id='bd-robot' >
    <!-- TODO: å¢žåŠ ç›¸å…³é—®é¢˜ -->
    <el-tabs v-model="activeName" @tab-click="handleClick" style="height: 100%;">
      <el-tab-pane label="å¸¸è§é—®é¢˜" name="qa" style="height: 100%;">
        <bd-qa></bd-qa>
      </el-tab-pane>
      <el-tab-pane label="ç›¸ä¼¼è¯" name="synonym" style="height: 100%;">
        <bd-synonym></bd-synonym>
      </el-tab-pane>
      <el-tab-pane label="é—®é¢˜ç»Ÿè®¡" name="stat" style="height: 100%;" v-if="showAuthority.debug">
        <bd-stat></bd-stat>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdQa from '@/components/dashboard/main/robot/children/BdQa'
import BdSynonym from '@/components/dashboard/main/robot/children/BdSynonym'
import BdStat from '@/components/dashboard/main/robot/children/BdStat'

export default {
  name: 'bd-robot',
  components: { BdQa, BdSynonym, BdStat },
  data () {
    return {
      activeName: 'qa',
      loading: true
    }
  },
  computed: {
    ...mapGetters([ 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getCategories', 'getRobotAnswers', 'getRobotSynonyms', 'changeCurrentMainView' ]),
    handleClick (tab, event) {
      if (tab.name === 'qa') {
        this.getCategories()
        let payload = { categoryId: 0, page: 0, size: 20 }
        this.getRobotAnswers(payload)
      } else if (tab.name === 'synonym') {
        let payload = { page: 0, size: 20 }
        this.getRobotSynonyms(payload)
      } else if (tab.name === 'stat') {
        //
      }
    }
  },
  created () {
    this.getCategories()
    let payload = { categoryId: 0, page: 0, size: 20 }
    this.getRobotAnswers(payload)
  },
  mounted () {
    // console.log('robot mounted')
    this.changeCurrentMainView('robot')
  }
}
</script>

<style scoped>
#bd-robot {
  height: 100%;
}
</style>



