<template>
  <div id='bd-web-admin'>
    <el-row>
      <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>å¸®åŠ©æ–‡æ¡£</el-button>
      <el-button size="mini" type="primary" style="float: left;" @click="showAddDialog">æ·»åŠ ç½‘ç«™</el-button>
    </el-row>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="webs.content" style="width: 100%;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="aid" label="å”¯ä¸€ID"></el-table-column>
      <el-table-column prop="name" label="ç½‘ç«™å"></el-table-column>
      <el-table-column prop="url" label="ç½‘å€"></el-table-column>
      <!-- <el-table-column prop="status" label="çŠ¶æ€"></el-table-column> -->
      <el-table-column prop="description" label="æè¿°"></el-table-column>
      <el-table-column fixed="right" label="æ“ä½œ" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
          <el-button v-if="!scope.row.defaulted" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="æ·»åŠ åº”ç”¨"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="ç½‘ç«™åç§°" prop="name">
          <el-input v-model="form.name" style="width: 350px;"></el-input>
        </el-form-item>
        <!--
          TODO: 1. ä¸¥æ ¼è¦æ±‚å¡«å†™URL(https/http + ç«¯å£å·)ï¼Œå¯ä»¥å¡«å†™å¤šä¸ªURL
          TODO: 2. urlåŒ¹é…å¡«å†™é”™è¯¯çš„ï¼Œæ— æ³•è®¿é—®å®¢æœ
         -->
        <el-form-item label="ç½‘ç«™ç½‘å€" prop="url">
          <el-input v-model="form.url" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="ç½‘å€æè¿°" prop="description">
          <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 10}" v-model="form.description" style="width: 350px;"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="addApp">ç¡® å®š</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-web-admin',
  data () {
    return {
      dialogVisible: false,
      isEdit: false,
      form: {
        aid: '',
        name: '',
        url: '',
        description: ''
      },
      rules: {
        name: [
          { required: true, message: 'è¯·è¾“å…¥ç½‘ç«™åç§°', trigger: 'blur' }
        ],
        url: [
          { required: true, message: 'è¯·è¾“å…¥ç½‘å€', trigger: 'blur' }
        ],
        description: [
          { required: true, message: 'è¯·è¾“å…¥ç½‘ç«™ç®€ä»‹', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([ 'webs' ])
  },
  methods: {
    ...mapActions([ 'getWebs', 'createWeb', 'updateWeb', 'deleteWeb' ]),
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201808252119041')
    },
    showAddDialog () {
      this.dialogVisible = true
      this.isEdit = false
      //
      this.form.aid = ''
      this.form.name = ''
      this.form.url = ''
      this.form.description = ''
    },
    addApp () {
      this.dialogVisible = false
      if (this.isEdit) {
        this.updateWeb(this.form)
      } else {
        this.createWeb(this.form)
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.form.aid = row.aid
      this.form.name = row.name
      this.form.url = row.url
      this.form.description = row.description
      //
      this.isEdit = true
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteWeb(row.aid)
      }).catch(() => {
        //
      })
    }
  },
  created () {
    let payload = { page: 0, size: 20 }
    this.getWebs(payload)
  }

}
</script>

<style scoped>
#bd-web-admin {
  height: 100%;
  text-align: left;
}
</style>



