<template>
  <div id='bd-record'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left;" plain>å¸®åŠ©æ–‡æ¡£</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="supportArticles.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="supportArticles.content" style="width: 100%; text-align: left;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="title" label="å†…å®¹"></el-table-column>
      <el-table-column prop="title" label="å›¾ç‰‡"></el-table-column>
      <el-table-column prop="title" label="æ‰‹æœº"></el-table-column>
      <el-table-column prop="title" label="é‚®ç®±"></el-table-column>
      <el-table-column prop="createdAt" label="åé¦ˆæ—¶é—´" width="150" sortable></el-table-column>
      <el-table-column fixed="right" label="æ“ä½œ" width="80">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="supportArticles.totalElements">
    </el-pagination>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-record',
  data () {
    return {
      currentCategoryId: 0,
      currentPage: 1,
      filterText: '',
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  computed: {
    ...mapGetters([ 'supportCategories', 'supportArticles' ])
  },
  watch: {
    filterText (val) {
      this.$refs.cateTree.filter(val)
    }
  },
  methods: {
    ...mapActions([ 'getCategories', 'createCategory', 'deleteCategory',
      'getSupportArticles', 'createSupportArticle', 'updateSupportArticle', 'deleteSupportArticle' ]),
    gotoHelpdoc () {
      window.open('http://localhost:8000/support?uid=201808221551193')
    },
    gotoArticle (aid) {
      window.open('http://localhost:8000/support/article?aid=' + aid)
    },
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    handleNodeClick (data) {
      console.log(data)
      this.currentCategoryId = data.id
      let payload = {
        categoryId: this.currentCategoryId,
        page: 0,
        size: 20
      }
      this.getSupportArticles(payload)
    },
    handleNodeContextMenuClick (event, data, node) {
      console.log(event, data, node)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = {
        categoryId: this.currentCategoryId,
        page: this.currentPage - 1,
        size: 20
      }
      this.getSupportArticles(payload)
    },
    addArticles () {
      console.log('settings')
    },
    editCategory () {
    },
    addCategory () {
      this.$prompt('è¯·è¾“å…¥ç±»åˆ«', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ'
      }).then(({ value }) => {
        console.log(value)
        if (value !== undefined && value !== null && value.length > 0) {
          let payload = {
            parent_id: this.currentCategoryId,
            name: value
          }
          this.createCategory(payload)
        }
      }).catch(() => {
        // å–æ¶ˆ
      })
    },
    handleView (index, row) {
      this.gotoArticle(row.aid)
    },
    handleEdit (index, row) {
      // console.log('bd-all:', index, row)
      bus.$emit(constants.EVENT_BUS_SUPPORT_EDIT_ARTICLE, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteSupportArticle(row.id)
      }).catch(() => {
        //
      })
    }
  },
  created () {
  }
}
</script>

<style scoped>
#bd-record {
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
  /* background-color: antiquewhite */
}
#right-col{
  height: 100%;
}
</style>



