<template>
  <!-- TODO: å¢žåŠ æœç´¢ã€å¤šè¯­è¨€é€‰æ‹© -->
  <!-- TODO: ç™»å½•ä¹‹åŽåŒæ­¥æœåŠ¡å™¨è‡ªåŠ¨å›žå¤ã€æŽ¥å¾…çŠ¶æ€ï¼ŒåŒ…æ‹¬ï¼šå®‰å“+ios -->
  <!-- TODO: å¢žåŠ æœºå™¨äººæŽ¥ç®¡åœ¨çº¿çŠ¶æ€ -->
  <!-- TODO: å¢žåŠ è¿å…¥çŠ¶æ€ï¼šé»˜è®¤ç™»å½•ä¹‹åŽï¼Œåªæœ‰è®¾ç½®è¿å…¥çŠ¶æ€ä¹‹åŽæ‰èƒ½å¤ŸæŽ¥å…¥è®¿å®¢ -->
  <div id='bd-header'>
    <el-menu style="padding-left:  25px; margin-left: -20px; margin-right: -20px; padding-right:30px;"
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        :background-color="headerColor"
        text-color="#fff"
        active-text-color="#ffd04b">
        <el-menu-item index="home">
          {{ title }}
        </el-menu-item>
        <el-menu-item index="1">
          <i class="el-icon-menu"></i>
        </el-menu-item>
        <el-menu-item index="2" style="float: right;">
          <el-color-picker
            v-model="headerColor"
            size="mini"
            show-alpha
            @change="onChange"
            @active-change="onActiveChange"
            :predefine="predefineColors">
          </el-color-picker>
        </el-menu-item>
        <el-submenu index="2-1" style="float: right;" v-if="showAuthority.debug">
            <template slot="title">
              <span>ä¸­æ–‡</span>
            </template>
            <el-menu-item index="zh_cn">ä¸­æ–‡</el-menu-item>
            <el-menu-item index="en">English</el-menu-item>
        </el-submenu>
        <el-submenu index="3" style="float: right;">
            <template slot="title">
              <span>{{ userInfo.realName }}</span>
            </template>
            <el-menu-item index="profile">ä¸ªäººèµ„æ–™</el-menu-item>
            <el-menu-item index="password">ä¿®æ”¹å¯†ç </el-menu-item>
            <el-menu-item index="logout">é€€å‡ºç™»å½•</el-menu-item>
        </el-submenu>
        <!-- TODO: åŒæ­¥è¿žæŽ¥çŠ¶æ€ -->
        <el-submenu index="4" style="float: right;">
            <template slot="title">{{ onlineStatus }}</template>
            <el-menu-item index="online">ä¸Šçº¿</el-menu-item>
            <el-menu-item index="busy">å¿™çº¿</el-menu-item>
            <el-menu-item index="rest">å°ä¼‘</el-menu-item>
        </el-submenu>
        <el-submenu index="5" style="float: right;">
            <template slot="title">{{ userInfo.autoReplyContent }}</template>
            <el-menu-item index="noreply">{{ noAuto }}</el-menu-item>
            <el-menu-item index="eat">{{ autoReplyEat }}</el-menu-item>
            <el-menu-item index="leave">{{ autoReplyLeave }}</el-menu-item>
            <el-menu-item index="back">{{ autoReplyBack }}</el-menu-item>
            <el-menu-item index="phone">{{ autoReplyPhone }}</el-menu-item>
            <el-menu-item index="self">{{ autoReplySelf }}</el-menu-item>
        </el-submenu>
        <el-submenu index="6" style="float: right;" v-if="showAuthority.debug">
            <template slot="title">è´¹ç”¨</template>
            <el-menu-item index="recharge">å……å€¼</el-menu-item>
            <el-menu-item index="detail">æ˜Žç»†</el-menu-item>
        </el-submenu>
        <el-menu-item index="7" style="float: right;" v-if="showAuthority.debug">
          <el-button type="success" size="mini" @click="onPayVip">æˆä¸ºVIPç”¨æˆ·</el-button>
        </el-menu-item>
        <el-menu-item index="8" style="float: right;" v-if="showAuthority.free">
          <el-button type="warning" size="mini" @click="onApplyTry">ç”³è¯·30å¤©è¯•ç”¨</el-button>
        </el-menu-item>
        <el-menu-item index="9" v-if="showAuthority.debug">
          <el-input size="mini" placeholder="è¯·è¾“å…¥å†…å®¹" prefix-icon="el-icon-search" v-model="searchContent"></el-input>
        </el-menu-item>
    </el-menu>

    <el-dialog
      title="è‡ªå®šä¹‰"
      :visible.sync="autoReplyDialogVisible"
      width="30%"
      center>
      <el-input type="textarea"
        :autosize="{ minRows: 4, maxRows: 8}"
        placeholder="è¯·è¾“å…¥è‡ªåŠ¨å›žå¤å†…å®¹"
        v-model="autoReplySelfContent">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="autoReplyDialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="doSelfAutoReply">ä¿ å­˜</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="ä¿®æ”¹å¯†ç "
      :visible.sync="changePasswordDialogVisible"
      width="30%"
      center>
      <el-form ref="form" :rules="rules" :model="form" label-width="80px" style="margin-top: 20px;">
        <el-form-item label="åŽŸå¯†ç " prop="old">
          <el-input type="password" v-model="form.old" auto-complete="off" placeholder="åŽŸå¯†ç " style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="æ–°å¯†ç " prop="password">
          <el-input type="password" v-model="form.password" auto-complete="off" placeholder="æ–°å¯†ç " style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="ç¡®è®¤å¯†ç " prop="rePassword">
          <el-input type="password" v-model="form.rePassword" auto-complete="off" placeholder="ç¡®è®¤å¯†ç " style="width: 250px;"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="changePasswordDialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="doChangePassword">ä¿ å­˜</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import * as https from '@/apis/http'
import bus from '@/util/eventbus'

export default {
  name: 'bd-header',
  data () {
    var validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('è¯·è¾“å…¥å¯†ç '))
      } else {
        if (this.form.rePassword !== '') {
          this.$refs.form.validateField('rePassword')
        }
        callback()
      }
    }
    var validateRePassword = (rule, value, callback) => {
      if (value !== this.form.password) {
        callback(new Error('ä¸¤æ¬¡è¾“å…¥å¯†ç ä¸ä¸€è‡´!'))
      } else {
        callback()
      }
    }
    return {
      activeIndex: '0',
      isCollapse: true,
      changePasswordDialogVisible: false,
      searchContent: '',
      form: {
        old: '',
        password: '',
        rePassword: ''
      },
      rules: {
        old: [
          { required: true, message: 'è¯·è¾“å…¥å¯†ç ', trigger: 'blur' },
          { min: 6, max: 16, message: 'é•¿åº¦åœ¨ 6 åˆ° 16 ä¸ªå­—ç¬¦ä¹‹é—´', trigger: 'blur' }
        ],
        password: [
          { required: true, message: 'è¯·è¾“å…¥å¯†ç ', trigger: 'blur' },
          { min: 6, max: 16, message: 'é•¿åº¦åœ¨ 6 åˆ° 16 ä¸ªå­—ç¬¦ä¹‹é—´', trigger: 'blur' },
          { validator: validatePassword, trigger: 'blur' }
        ],
        rePassword: [
          { required: true, message: 'è¯·è¾“å…¥ç¡®è®¤å¯†ç ', trigger: 'blur' },
          { validator: validateRePassword, trigger: 'blur' }
        ]
      },
      autoReplyContent: constants.AUTO_REPLY_NO,
      noAuto: constants.AUTO_REPLY_NO,
      autoReplyEat: constants.AUTO_REPLY_EAT,
      autoReplyLeave: constants.AUTO_REPLY_LEAVE,
      autoReplyBack: constants.AUTO_REPLY_BACK,
      autoReplyPhone: constants.AUTO_REPLY_PHONE,
      autoReplySelf: constants.AUTO_REPLY_SELF,
      autoReplyDialogVisible: false,
      autoReplySelfContent: '',
      title: constants.DEFAULT_TITLE,
      // rgba(0, 123, 255, 1)
      headerColor: '#007bff',
      predefineColors: [
        '#ffd700',
        'rgb(255, 120, 0)',
        '#ff4500',
        '#c71585',
        '#90ee90',
        '#00ced1',
        '#1e90ff',
        'rgba(0, 123, 255, 1)'
      ]
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'accessToken', 'status', 'showAuthority' ]),
    onlineStatus () {
      if (this.status === constants.USER_STATUS_ONLINE) {
        return 'åœ¨çº¿'
      } else if (this.status === constants.USER_STATUS_BUSY) {
        return 'å¿™çº¿'
      } else {
        return 'å°ä¼‘'
      }
    }
  },
  methods: {
    ...mapActions([ 'setAcceptStatus', 'applyTry', 'logout', 'updateAutoReply' ]),
    onApplyTry () {
      // console.log('apply try')
      // TODO: é¦–å…ˆæ£€æŸ¥æ‰‹æœºå·æ˜¯å¦ä¸ºç©ºï¼Œå¦åˆ™éœ€è¦æç¤ºå¡«å†™æ‰‹æœºå·
      this.$prompt('è¯·è¾“å…¥æ‰‹æœºå·ç ', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ'
      }).then(({ value }) => {
        let mobile = value
        this.applyTry(mobile)
      }).catch(() => {
      })
    },
    onPayVip () {
      console.log('pay vip')
    },
    onChange (value) {
      // ç‚¹å‡»ç¡®å®šæŒ‰é’®ä¹‹åŽè§¦å‘
      // console.log('onChange:', value)
      localStorage.headerColor = value
    },
    onActiveChange (value) {
      // æ¯æ¬¡é€‰æ‹©é¢œè‰²ä¹‹åŽè§¦å‘
      // console.log('onActiveChange:', value)
    },
    doSelfAutoReply () {
      this.autoReplyDialogVisible = false
      //
      let payload = {
        isAutoReply: true,
        content: this.autoReplySelfContent
      }
      this.updateAutoReply(payload)
    },
    doChangePassword () {
      //
      this.$refs['form'].validate((valid) => {
        if (valid) {
          let password = this.form.password
          https.changePassword2(password, this.accessToken, response => {
            console.log(response)
            this.changePasswordDialogVisible = false
          }, error => {
            console.log(error)
          })
        }
      })
    },
    handleSelect (key, keyPath) {
      // console.log(key, keyPath)
      if (key === 'home') {
        this.$router.replace('/admin')
      } else if (key === '1') {
        this.isCollapse = !this.isCollapse
        bus.$emit('collapse', this.isCollapse)
      } else if (key === 'profile') {
        this.$router.replace('/admin/setting/profile')
      } else if (key === 'password') {
        this.changePasswordDialogVisible = true
      } else if (key === 'logout') {
        this.logout()
      } else if (key === 'online') {
        this.setAcceptStatus(constants.USER_STATUS_ONLINE)
      } else if (key === 'busy') {
        this.setAcceptStatus(constants.USER_STATUS_BUSY)
      } else if (key === 'rest') {
        this.setAcceptStatus(constants.USER_STATUS_REST)
      } else if (key === 'recharge') {
        this.$router.replace('/admin/setting/pay')
      } else if (key === 'detail') {
        this.$router.replace('/admin/setting/pay')
      } else if (key === 'noreply') {
        this.autoReplyContent = this.noAuto
        let payload = {
          isAutoReply: false,
          content: this.noAuto
        }
        this.updateAutoReply(payload)
      } else if (key === 'eat') {
        this.autoReplyContent = this.autoReplyEat
        let payload = {
          isAutoReply: true,
          content: this.autoReplyEat
        }
        this.updateAutoReply(payload)
      } else if (key === 'leave') {
        this.autoReplyContent = this.autoReplyLeave
        let payload = {
          isAutoReply: true,
          content: this.autoReplyLeave
        }
        this.updateAutoReply(payload)
      } else if (key === 'back') {
        this.autoReplyContent = this.autoReplyBack
        let payload = {
          isAutoReply: true,
          content: this.autoReplyBack
        }
        this.updateAutoReply(payload)
      } else if (key === 'phone') {
        this.autoReplyContent = this.autoReplyPhone
        let payload = {
          isAutoReply: true,
          content: this.autoReplyPhone
        }
        this.updateAutoReply(payload)
      } else if (key === 'self') {
        this.autoReplyContent = this.autoReplySelf
        this.autoReplyDialogVisible = true
      }
    }
  },
  created () {
    let color = localStorage.headerColor
    // console.log('headerColor:', color)
    if (color !== null && color !== undefined) {
      this.headerColor = color
    }
  }
}
</script>

<style scoped>
#bd-header {
  background-color: #B3C0D1;
  color: #333;
  text-align: center;
  line-height: 60px;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
</style>



