<template>
  <div id="bd-auth">
    <el-container>
      <el-header>
        <cm-header></cm-header>
      </el-header>
      <el-main>
        <transition mode="out-in" enter-active-class="fadeIn" leave-active-class="fadeOut" appear>
          <router-view class="animated"></router-view>
        </transition>
      </el-main>
      <el-footer style="height: 150px;">
        <cm-footer></cm-footer>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import CmHeader from '@/components/common/CmHeader'
import CmFooter from '@/components/common/CmFooter'

export default {
  name: 'bd-auth',
  components: { CmHeader, CmFooter }
}
</script>

<style scoped>
#bd-auth {
  width: 100%;
  height: 100%;
}
.el-container {
  height: 100%;
}
.el-main {
  height: 100%;
}
.el-footer {
  background-color: #f9f9f9;
  text-align: center;
}
</style>



