          })
      } else {
        this.isRtcInitiator = false
        this.rtcDialogVisible = false
        this.isRtcStarted = false
      }
    },
    /**
     * å‚è€ƒç½‘å€:
     * https://codelabs.developers.google.com/codelabs/webrtc-web/#4
     * https://cloud.tencent.com/developer/article/1004661
     * å¼€å§‹è§†é¢‘ä¼šè¯ï¼š
     * 1. Aåˆå§‹åŒ–æœ¬åœ°æ‘„åƒå¤´
     * 2. (è‡ªå®šä¹‰åè®®æ­¥éª¤)å‘é€æ¶ˆæ¯ç»™å¯¹æ–¹Bï¼Œå…¶æ”¶åˆ°æ¶ˆæ¯ä¹‹åŽä¹Ÿå¼€å§‹åˆå§‹åŒ–è®¾å¤‡ä¿¡æ¯, å¹¶åˆ›å»ºpeerConnection
     * 3. Aåˆ›å»ºpeerConnection
     * 4. Aå‘é€candidateä¿¡æ¯ç»™å¯¹æ–¹Bï¼Œå³ï¼šè‡ªå·±çš„ipç­‰ä¿¡æ¯
     * 5. å¯¹æ–¹Bæ”¶åˆ°candidateä¿¡æ¯åŽï¼Œæ‰§è¡ŒaddIcecandidate
     * 6. Aå‘èµ·offer: é¦–å…ˆè°ƒç”¨setLocalDescriptionè®¾ç½®è‡ªå·±çš„SDPï¼Œç„¶åŽå°†SDPå‘é€ç»™B
     * 7. Bæ”¶åˆ°offerä¹‹åŽï¼Œé¦–å…ˆå°†Açš„SDPè°ƒç”¨setRemoteDescriptionä¿å­˜
     * 8. Bæ‰§è¡Œanswerï¼šé¦–å…ˆè°ƒç”¨setLocalDescriptionè®¾ç½®è‡ªå·±çš„SDPï¼Œç„¶åŽå°†SDPå‘é€ç»™A
     */
    startInviteVideoCall () {
      // this.rtcStartCallTimestamp = window.performance.now()
      this.isRtcInitiator = true
      this.sendInviteMessage()
      this.initLocalStream()
    },
    startAudioCall () {
      //
    },
