<template>
  <div id='bd-internal'>
    <el-row>
      <el-col :span="5" id="left-col">
        <bd-left></bd-left>
      </el-col>
      <el-col :span="19" id="middle-col">
        <bd-middle></bd-middle>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import BdLeft from '@/components/dashboard/main/internal/children/BdLeft'
import BdMiddle from '@/components/dashboard/main/internal/children/BdMiddle'

export default {
  name: 'bd-internal',
  components: { BdLeft, BdMiddle },
  methods: {
    ...mapActions([ 'changeCurrentMainView' ])
  },
  mounted () {
    this.changeCurrentMainView('internal')
  }
}
</script>

<style scoped>
#bd-internal {
  height: 97%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col{
  height: 100%;
  border: 1px solid gainsboro;
}
#middle-col {
  height: 100%;
  border-top: 1px solid gainsboro;
  border-right: 1px solid gainsboro;
  border-radius: 5px;
}
#right-col {
  height: 100%;
  border: 1px solid gainsboro;
}
</style>



