<template>
  <!-- TODO: å¢žåŠ table loadingæ•ˆæžœ -->
  <div id='bd-qa'>
    <el-row>
      <el-col :span="4" id="left-col">
        <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 15px;" @click="gotoHelpdoc()" plain>å¸®åŠ©æ–‡æ¡£</el-button>
        <el-button size="mini" type="success" style="float: left; margin-bottom: 15px;" @click="addQa">æ·»åŠ é—®ç­”</el-button>
        <!-- <el-button size="mini" type="success" style="float: left; margin-bottom: 15px;" @click="setRobotNameDialogVisible = true">æ˜µç§°</el-button> -->
        <!-- <el-button size="mini" type="primary" icon="el-icon-upload2" style="float: left; margin-bottom: 10px;">å¯¼å…¥</el-button> -->
        <el-input size="mini" placeholder="æœç´¢ç±»åˆ«" v-model="filterText"></el-input>
        <el-tree class="filter-tree"
          ref="cateTree"
          :data="robotCategories"
          :props="defaultProps"
          :filter-node-method="filterNode"
          @node-click="handleNodeClick">
        </el-tree>
        <el-button size="mini" type="success" @click="addCategory">æ·»åŠ </el-button>
        <el-button size="mini" type="primary" @click="editCategory">ç¼–è¾‘</el-button>
        <el-button size="mini" type="danger" @click="removeCategory">åˆ é™¤</el-button>
      </el-col>
      <el-col :span="20" id="right-col">
        <el-pagination style="float: right;"
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="robotAnswers.totalElements">
        </el-pagination>
        <el-table stripe size="mini" height="calc(100% - 80px)"
          :data="robotAnswers.content" style="width: 100%; text-align: left;">
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="ç›¸ä¼¼é—®é¢˜">
                  <span v-for="(related, index) in props.row.relateds" :key="related.id">
                    {{ related.question }} <span v-if="index < props.row.relateds.length-1">,</span>
                  </span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="question" label="é—®é¢˜"></el-table-column>
          <el-table-column prop="answer" label="ç­”æ¡ˆ"></el-table-column>
          <el-table-column prop="welcome" label="æ¬¢è¿Žè¯­" width="100" sortable>
            <template slot-scope="scope">
              <span v-if="scope.row.welcome">æ˜¯</span>
              <span v-if="!scope.row.welcome">å¦</span>
            </template>
          </el-table-column>
          <el-table-column prop="recommend" label="å³è¾¹æ æŽ¨è" width="120" sortable>
            <template slot-scope="scope">
              <span v-if="scope.row.recommend">æ˜¯</span>
              <span v-if="!scope.row.recommend">å¦</span>
            </template>
          </el-table-column>
          <el-table-column prop="queryCount" label="è¯·æ±‚æ¬¡æ•°" sortable></el-table-column>
          <el-table-column prop="rateHelpful" label="æœ‰å¸®åŠ©/æ€»æ•°" sortable>
            <template slot-scope="scope">
              <span>{{ scope.row.rateHelpful }}/{{ rateTotal(scope.row) }}</span>
            </template>
          </el-table-column>
          <!-- <el-table-column prop="code" label="ä»£ç "></el-table-column> -->
          <el-table-column fixed="right" label="æ“ä½œ" width="150">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination style="float: right; margin-top: 10px;"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[20, 100, 200, 300, 400]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="robotAnswers.totalElements">
        </el-pagination>
      </el-col>
    </el-row>
    <el-dialog
      title="æ·»åŠ é—®ç­”"
      :visible.sync="addQaDialogVisible"
      width="40%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="100px">
        <!-- TODO: å¾®ä¿¡ã€å°ç¨‹åºä¸“ç”¨code -->
        <!-- <el-form-item label="ä»£ç " prop="code">
          <el-input v-model="form.code" style="width: 350px;" placeholder="ç”¨äºŽå¾®ä¿¡å…¬ä¼—å·/å°ç¨‹åº(å¯é€‰)"></el-input>
        </el-form-item> -->
        <el-form-item label="æ ‡å‡†é—®é¢˜" prop="question">
          <el-input v-model="form.question" style="width: 350px;" placeholder="æ ‡å‡†é—®é¢˜"></el-input>
        </el-form-item>
        <el-form-item v-for="(related, index) in relateds" label="ç›¸ä¼¼é—®æ³•" :key="related.id">
          <el-input v-model="related.question" style="width: 350px;" placeholder="ç›¸ä¼¼é—®æ³•"></el-input>
          <el-button v-if="index > 0" @click.prevent="removeRelated(related.id)">åˆ é™¤</el-button>
          <el-button v-if="index === 0" @click.prevent="addRelated()">æ·»åŠ </el-button>
        </el-form-item>
        <!-- TODO: å¢žåŠ å›¾ç‰‡é™„ä»¶ -->
        <el-form-item label="æ ‡å‡†ç­”æ¡ˆ" prop="answer">
          <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 20}" v-model="form.answer" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="æ‰€å±žåˆ†ç±»" prop="categories">
          <el-checkbox-group v-model="form.categories">
            <el-checkbox v-if="category.id > 0" v-for="category in robotCategories" :key="category.id" :label="category.id">{{ category.name }}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addQaDialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="addAnswer()">ç¡® å®š</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="æ·»åŠ é—®ç­”"
      :visible.sync="setRobotNameDialogVisible"
      width="30%"
      center>
      <el-form ref="formRobot" :model="formRobot" label-width="100px">
        <el-form-item label="æœºå™¨äººæ˜µç§°" prop="robotName">
          <el-input v-model="form.robotName" style="width: 350px;" placeholder="æœºå™¨äººæ˜µç§°"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRobotNameDialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="setRobotNameDialogVisible = false">ç¡® å®š</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-qa',
  data () {
    return {
      currentCategory: {
        id: 0,
        name: '',
        children: []
      },
      loading: false,
      pageSize: 20,
      currentPage: 1,
      isEdit: false,
      inputVisible: false,
      inputValue: '',
      addQaDialogVisible: false,
      setRobotNameDialogVisible: false,
      relatedIndex: 0,
      filterText: '',
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      form: {
        aid: '',
        // code: '',
        question: '',
        answer: '',
        relateds: [],
        categories: []
      },
      relateds: [],
      rules: {
        question: [
          { required: true, message: 'è¯·è¾“å…¥æ ‡å‡†é—®é¢˜', trigger: 'blur' }
        ],
        answer: [
          { required: true, message: 'è¯·è¾“å…¥æ ‡å‡†ç­”æ¡ˆ', trigger: 'blur' }
        ],
        categories: [
          { type: 'array', required: true, message: 'è¯·è‡³å°‘é€‰æ‹©ä¸€ä¸ªç±»åˆ«', trigger: 'change' }
        ]
      },
      formRobot: {
        robotName: ''
      }
    }
  },
  computed: {
    ...mapGetters([ 'robotCategories', 'robotAnswers' ])
  },
  watch: {
    filterText (val) {
      this.$refs.cateTree.filter(val)
    }
  },
  methods: {
    ...mapActions([ 'getCategories', 'createCategory', 'updateCategory', 'deleteCategory',
      'getRobotAnswers', 'createRobotAnswer', 'updateRobotAnswer', 'deleteRobotAnswer' ]),
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201809042103521')
    },
    rateTotal (row) {
      return row.rateHelpful + row.rateUseless
    },
    handleNodeClick (data) {
      // console.log(data)
      this.currentCategory.id = data.id
      this.currentCategory.name = data.name
      this.currentCategory.children = data.children
      //
      let payload = {
        categoryId: this.currentCategory.id,
        page: 0,
        size: 20
      }
      this.getRobotAnswers(payload)
    },
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
    },
    addQa () {
      this.isEdit = false
      this.form.aid = ''
      this.form.question = ''
      this.form.answer = ''
      this.relateds = [{ id: 0, question: '' }]
      this.form.categories = []
      //
      this.addQaDialogVisible = true
    },
    addCategory () {
      this.$prompt('è¯·è¾“å…¥ç±»åˆ«', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ'
      }).then(({ value }) => {
        console.log(value)
        if (value !== undefined && value !== null && value.length > 0) {
          let payload = {
            parent_id: this.currentCategory.id,
            name: value,
            type: constants.CATEGORY_TYPE_ROBOT
          }
          this.createCategory(payload)
        }
      }).catch(() => {
        // å–æ¶ˆ
      })
    },
    editCategory () {
      if (this.currentCategory.id === 0) {
        this.$message.error('è¯·é€‰æ‹©è¦ç¼–è¾‘çš„ç±»åˆ«')
      } else {
        this.$prompt('ç¼–è¾‘ç±»åˆ«', 'æç¤º', {
          confirmButtonText: 'ç¡®å®š',
          cancelButtonText: 'å–æ¶ˆ',
          inputValue: this.currentCategory.name
        }).then(({ value }) => {
          // console.log(value)
          if (value !== undefined && value !== null && value.length > 0) {
            let payload = {
              category_id: this.currentCategory.id,
              name: value
            }
            this.updateCategory(payload)
          } else {
            this.$message.error('ä¸èƒ½ä¸ºç©º')
          }
        }).catch(() => {
          // å–æ¶ˆ
        })
      }
    },
    removeCategory () {
      if (this.currentCategory.id === 0) {
        this.$message.error('ä¸å…è®¸åˆ é™¤')
        return
      }
      if (this.currentCategory.children.length > 0) {
        this.$message.error('è¯·é¦–å…ˆåˆ é™¤å­åˆ†ç±»')
        return
      }
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteCategory(this.currentCategory.id)
        this.currentCategory.id = 0
      }).catch(() => {
      })
    },
    transformRelated () {
      let localRelateds = []
      for (let i = 0; i < this.relateds.length; i++) {
        const related = this.relateds[i]
        localRelateds.push(related.question)
      }
      this.form.relateds = localRelateds
    },
    addAnswer () {
      this.transformRelated()
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.addQaDialogVisible = false
          if (this.isEdit) {
            this.updateRobotAnswer(this.form)
          } else {
            this.createRobotAnswer(this.form)
          }
        } else {
          return false
        }
      })
    },
    addRelated () {
      this.relatedIndex++
      this.relateds.push({
        id: this.relatedIndex,
        question: ''
      })
    },
    removeRelated (id) {
      for (let index = 0; index < this.relateds.length; index++) {
        const related = this.relateds[index]
        if (related.id === id) {
          this.relateds.splice(index, 1)
        }
      }
    },
    handleEdit (index, row) {
      this.isEdit = true
      // console.log(index, row)
      this.form.aid = row.aid
      this.form.question = row.question
      this.form.answer = row.answer
      //
      this.addQaDialogVisible = true
      for (let i = 0; i < row.categories.length; i++) {
        const category = row.categories[i]
        this.form.categories.push(category.id)
      }
      if (row.relateds.length > 0) {
        this.relateds = []
      }
      for (let i = 0; i < row.relateds.length; i++) {
        const related = row.relateds[i]
        this.relateds.push({ id: i, question: related.question })
      }
    },
    handleDelete (index, row) {
      // console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        // åˆ é™¤
        this.deleteRobotAnswer(row.aid)
      }).catch(() => {
        //
      })
    }
  },
  created () {
  }
}
</script>

<style scoped>
#bd-qa {
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
  /* border: 1px solid gainsboro; */
}
#right-col{
  height: 100%;
  /* border: 1px solid gainsboro; */
}
.filter-tree {
  margin-top: 10px;
  margin-bottom: 10px;
  height: calc(100% - 130px);
  overflow-y: auto;
}
</style>



