<template>
  <div id='bd-agent-admin'>
    <el-tabs v-model="activeName" @tab-click="handleClick" style="height: 100%;" @edit="handleTabsEdit">
      <el-tab-pane label="å®¢æœè´¦å·" name="agent" style="height: 100%;">
        <bd-agent></bd-agent>
      </el-tab-pane>
      <el-tab-pane label="æŠ€èƒ½ç»„" name="workGroup" style="height: 100%;">
        <bd-workgroup></bd-workgroup>
      </el-tab-pane>
      <el-tab-pane label="åˆ†å…¬å¸" name="company" style="height: 100%;" v-if="showAuthority.debug">
        <bd-company></bd-company>
      </el-tab-pane>
      <el-tab-pane label="æƒé™ç®¡ç†" name="priviledge" style="height: 100%;" v-if="showAuthority.debug">
        <bd-priviledge></bd-priviledge>
      </el-tab-pane>
      <el-tab-pane closable style="height: 100%;"
        :key="item.name"
        v-for="item in editableTabs"
        :label="item.title"
        :name="item.name">
        <bd-code :type="item.type" :object="item.object"></bd-code>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdAgent from '@/components/dashboard/main/setting/children/agent/BdAgent'
import BdWorkgroup from '@/components/dashboard/main/setting/children/agent/BdWorkgroup'
import BdCode from '@/components/dashboard/main/setting/children/agent/BdCode'
import BdCompany from '@/components/dashboard/main/setting/children/agent/BdCompany'
import BdPriviledge from '@/components/dashboard/main/setting/children/agent/BdPriviledge'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-agent-admin',
  components: { BdAgent, BdWorkgroup, BdCode, BdCompany, BdPriviledge },
  data () {
    return {
      activeName: 'agent',
      tabIndex: 1,
      editableTabs: []
    }
  },
  computed: {
    ...mapGetters([ 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getWorkGroups', 'getAgents', 'getRoles', 'getRegions', 'getCompanies' ]),
    handleClick (tab, event) {
      let payload = { page: 0, size: 20 }
      if (tab.name === 'agent') {
        this.getAgents(payload)
      } else if (tab.name === 'workGroup') {
        this.getWorkGroups(payload)
      } else if (tab.name === 'company') {
        this.getCompanies(payload)
        this.getRegions()
      } else if (tab.name === 'priviledge') {
        // æƒé™ç®¡ç†
      }
    },
    handleTabsEdit (targetName, action) {
      // console.log(targetName, action)
      if (action === 'remove') {
        let tabs = this.editableTabs
        let activeName = 'agent'
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1]
              if (nextTab) {
                activeName = nextTab.name
              }
            }
          })
        }
        this.activeName = 'agent'
        this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      }
    }
  },
  created () {
    let payload = { page: 0, size: 20 }
    this.getWorkGroups(payload)
    this.getAgents(payload)
    //
    this.getRoles()
  },
  mounted () {
    // ç¼–è¾‘å®¢æœä»£ç 
    bus.$on(constants.EVENT_BUS_AGENT_CODE, agent => {
      // console.log('agent:', agent)
      this.editableTabs.push({
        title: 'å®¢æœ' + agent.nickname + 'ä»£ç ',
        name: '' + this.tabIndex,
        type: 'agent',
        object: agent
      })
      this.activeName = '' + this.tabIndex
      this.tabIndex++
    })
    // ç¼–è¾‘å·¥ä½œç»„ä»£ç 
    bus.$on(constants.EVENT_BUS_WORKGROUP_CODE, workGroup => {
      // console.log('workGroup:', workGroup)
      this.editableTabs.push({
        title: 'å·¥ä½œç»„' + workGroup.nickname + 'ä»£ç ',
        name: '' + this.tabIndex,
        type: 'workGroup',
        object: workGroup
      })
      this.activeName = '' + this.tabIndex
      this.tabIndex++
    })
  }

}
</script>

<style scoped>
#bd-agent-admin {
  height: 100%;
  padding-left: 5px;
  text-align: left;
}
</style>



