<template>
    <div :id="id" :style="style" style="margin-top: 10px;"></div>
</template>

<script>
// æ•™ç¨‹è¯´æ˜Ž: https://segmentfault.com/a/1190000013903264
// TODO: ä¼˜åŒ–å¤ç”¨

export default {
  name: 'bd-chart',
  data () {
    return {
      // echartså®žä¾‹
      chart: ''
    }
  },
  props: {
    // çˆ¶ç»„ä»¶éœ€è¦ä¼ é€’çš„å‚æ•°ï¼šidï¼Œwidthï¼Œheightï¼Œoption
    id: {
      type: String
    },
    title: {
      type: String
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '400px'
    },
    totalThreadCounts: {
      type: Array
    },
    totalQueueCounts: {
      type: Array
    },
    option: {
      type: Object,
      // Objectç±»åž‹çš„propå€¼ä¸€å®šè¦ç”¨å‡½æ•°returnå‡ºæ¥ï¼Œä¸ç„¶ä¼šæŠ¥é”™ã€‚åŽŸç†å’Œdataæ˜¯ä¸€æ ·çš„ï¼Œ
      // ä½¿ç”¨é—­åŒ…ä¿è¯ä¸€ä¸ªvueå®žä¾‹æ‹¥æœ‰è‡ªå·±çš„ä¸€ä»½props
      default () {
        return {
          title: {
            text: this.title
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['æŽ¥å¾…äººæ¬¡', 'æŽ’é˜Ÿäººæ¬¡', 'æ¶ˆæ¯æ¡æ•°']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          toolbox: {
            show: true,
            feature: {
              mark: { show: true },
              dataView: { show: true, readOnly: false },
              magicType: { show: true, type: ['line', 'bar'] },
              restore: { show: true },
              saveAsImage: { show: true }
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['00:00', '01:00', '02:00', '03:00', '04:00', '05:00', '06:00',
              '07:00', '08:00', '09:00', '10:00', '11:00', '12:00',
              '13:00', '14:00', '15:00', '16:00', '17:00', '18:00',
              '19:00', '20:00', '21:00', '22:00', '23:00']
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: 'æŽ¥å¾…äººæ¬¡',
              type: 'line',
              data: this.totalThreadCounts
            },
            {
              name: 'æŽ’é˜Ÿäººæ¬¡',
              type: 'line',
              data: this.totalQueueCounts
            },
            {
              name: 'æ¶ˆæ¯æ¡æ•°',
              type: 'line',
              data: this.totalQueueCounts
            }
          ]
        }
      }
    }
  },
  watch: {
    // è§‚å¯Ÿoptionçš„å˜åŒ–
    option: {
      handler (newVal, oldVal) {
        if (this.chart) {
          if (newVal) {
            this.chart.setOption(newVal)
          } else {
            this.chart.setOption(oldVal)
          }
        } else {
          this.init()
        }
      },
      deep: true // å¯¹è±¡å†…éƒ¨å±žæ€§çš„ç›‘å¬ï¼Œå…³é”®
    }
  },
  computed: {
    style () {
      return {
        height: this.height,
        width: this.width
      }
    }
  },
  methods: {
    init () {
      this.chart = this.$echarts.init(document.getElementById(this.id))
      this.chart.setOption(this.option)
      window.addEventListener('resize', this.chart.resize)
    }
  },
  mounted () {
    this.init()
  }
}
</script>



