<template>
  <!-- TODO: å¢žåŠ è®¾ç½®ç™»å½•çŠ¶æ€ï¼Œæ”¯æŒç™»å½•ä¹‹åŽé»˜è®¤ä¸æŽ¥å¾…çŠ¶æ€ -->
  <div id='bd-login'>
    <el-main>
      <el-row>
        <el-col :span="8"><div class="grid-content"></div></el-col>
        <el-col :span="6">
          <el-form :model="loginForm" status-icon :rules="rules" ref="loginForm" label-width="100px">
            <el-form-item label="ä¼ä¸šå·" prop="sub_domain">
              <el-input v-model="loginForm.sub_domain" placeholder="è¯·å¡«å†™ä¼ä¸šå·" style="width: 250px;" clearable></el-input>
            </el-form-item>
            <el-form-item label="ç”¨æˆ·å/é‚®ç®±" prop="email">
              <el-input v-model="loginForm.email" placeholder="ç”¨æˆ·å/é‚®ç®±" style="width: 250px;" clearable></el-input>
            </el-form-item>
            <el-form-item label="å¯†ç " prop="password">
              <el-input type="password" v-model="loginForm.password" @keyup.enter.native="onEnter" auto-complete="off" placeholder="å¯†ç " style="width: 250px;" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="login" :loading="logining">ç™»å½•</el-button>
              <el-button @click="register">æ³¨å†Œ</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="8"><div class="grid-content"></div></el-col>
      </el-row>
    </el-main>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as https from '@/apis/http'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-login',
  data () {
    return {
      activeIndex: '0',
      errorCode: 0,
      is400: false,
      isIp: constants.IS_IP,
      // æ˜¯å¦ç™»å½•ä¸­
      logining: false,
      loginForm: {
        sub_domain: location.hostname.split('.').length > 2 ? location.hostname.split('.')[0] : '',
        email: '',
        password: ''
      },
      rules: {
        sub_domain: [
          { required: true, message: 'è¯·è¾“å…¥ä¼ä¸šå·', trigger: 'blur' }
        ],
        email: [
          { required: true, message: 'è¯·è¾“å…¥ç”¨æˆ·å/é‚®ç®±', trigger: 'blur' },
          // { type: 'email', message: 'è¯·è¾“å…¥æ­£ç¡®çš„é‚®ç®±åœ°å€', trigger: 'change' },
          { min: 5, message: 'é•¿åº¦ä¸èƒ½å°‘äºŽ5ä¸ªå­—ç¬¦', trigger: 'blur' }
          // TODO: æ­¤å¤„å¢žåŠ éªŒè¯ç”¨æˆ·è´¦å·æ˜¯å¦å·²ç»è¢«ç¦ç”¨ï¼Œå¦‚å·²ç¦ç”¨ï¼Œåˆ™ç¦æ­¢ç™»å½•
        ],
        password: [
          { required: true, message: 'è¯·è¾“å…¥å¯†ç ', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([ 'mainHost' ]),
    subDomain () {
      // å¦‚æžœåŒ…å«äºŒçº§åŸŸåï¼Œé‚£ä¹ˆè¿”å›žäºŒçº§åŸŸåï¼Œå¦åˆ™è¿”å›žç©º
      return location.hostname.split('.').length > 2 ? location.hostname.split('.')[0] : ''
    }
  },
  methods: {
    ...mapActions([
      'updatePassportToken'
    ]),
    onEnter () {
      this.login()
    },
    login () {
      this.$refs['loginForm'].validate((valid) => {
        if (valid) {
          let username = this.loginForm.email.toLowerCase()
          // åˆ¤æ–­æ˜¯å¦æ˜¯emailï¼Œå¦‚æžœä¸æ˜¯emailï¼Œåˆ™æ·»åŠ åŽç¼€ â€˜@subDomainâ€™
          if (username.indexOf('@') < 0) {
            username = username + '@' + this.loginForm.sub_domain
          }
          this.logining = true
          https.login(username, this.loginForm.password,
            response => {
              console.log('login success:', response)
              this.logining = false
              if (response === null || response === undefined) {
                if (this.errorCode === 500) {
                  return
                }
                if (!this.is400) {
                  // éž400é”™è¯¯çš„æƒ…å†µä¸‹ï¼Œæ‰ä¼šæ˜¾ç¤ºè¶…æ—¶
                  bus.$emit(constants.EVENT_BUS_LOGIN_TIMEOUT, '')
                }
                return
              }
              // Electron æ˜¾ç¤ºtrayå›¾æ ‡
              if (constants.IS_ELECTRON) {
                ipcRenderer.send('show-tray', '')
              }
              // åŠ¡å¿…è®¾ç½®ä¸€ä¸‹
              localStorage.access_token = response.access_token
              this.updatePassportToken(response)
              // TODO: è·³è½¬äºŒçº§åŸŸå
              if (constants.IS_ELECTRON) {
                this.$router.replace('/admin')
              } else if (this.isIp) {
                // ç®¡ç†åŽå°ä¸ºipåœ°å€
                window.location.href = constants.ADMIN_URL
              } else {
                // ç®¡ç†åŽå°éœ€è¦ä½¿ç”¨äºŒçº§åŸŸå
                window.location.href = constants.HTTP_PREFIX + this.loginForm.sub_domain + '.' + this.mainHost + '/admin'
              }
            }, error => {
              console.log('login error:', error)
              this.logining = false
            })
        } else {
          return false
        }
      })
    },
    register () {
      this.$router.replace('/register')
    }
  },
  mounted () {
    // ç”¨æˆ·åæˆ–å¯†ç é”™è¯¯
    bus.$on(constants.EVENT_BUS_LOGIN_ERROR_400, message => {
      this.is400 = true
      this.errorCode = 400
      this.$message.error('ç”¨æˆ·åæˆ–å¯†ç é”™è¯¯')
    })
    //
    bus.$on(constants.EVENT_BUS_LOGIN_TIMEOUT, message => {
      this.$message.error('ç™»å½•è¶…æ—¶ï¼Œè¯·æ£€æŸ¥ç½‘ç»œæˆ–ç¨åŽé‡è¯•')
    })
    //
    bus.$on(constants.EVENT_BUS_SERVER_ERROR_500, message => {
      this.errorCode = 500
      this.$message.error('æœåŠ¡å™¨é”™è¯¯')
    })
  }
}
</script>

<style scoped>
#bd-login {
  margin-top: 150px;
}
.grid-content {
  min-height: 36px;
}
</style>



