<template>
  <div id='bd-thread'>
    <el-row>
        <el-col :span="5" id="left-col">
            <bd-left></bd-left>
        </el-col>
        <el-col :span="13" id="middle-col">
            <bd-middle></bd-middle>
        </el-col>
        <el-col :span="6" id="right-col">
            <bd-right></bd-right>
        </el-col>
    </el-row>
  </div>
</template>

<script>
import BdLeft from '@/components/dashboard/main/chat/children/thread/BdLeft'
import BdMiddle from '@/components/dashboard/main/chat/children/thread/BdMiddle'
import BdRight from '@/components/dashboard/main/chat/children/thread/BdRight'

export default {
  name: 'bd-thread',
  components: { BdLeft, BdMiddle, BdRight }
}
</script>

<style scoped>
#bd-thread {
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col{
  height: 100%;
  border: 1px solid gainsboro;
}
#middle-col {
  height: 100%;
  border-top: 1px solid gainsboro;
  border-radius: 5px;
}
#right-col {
  height: 100%;
  border: 1px solid gainsboro;
}
</style>



