<template>
  <div id='bd-top'>
    <img class="avatar" width="35" height="35" :alt="userCurrentThread.visitor.nickname" :src="userCurrentThread.visitor.avatar">
    <span class="nickname">{{ userCurrentThread.visitor.nickname }}</span>
    <span class="wrapper">
      <!-- TODO: å¢žåŠ ä¸€é”®æœºå™¨äººæŽ¥ç®¡ -->
      <!-- TODO: å®½åº¦ä¸å¤Ÿçš„æƒ…å†µä¸‹ï¼ŒæŒ‰é’®ä¸èƒ½ä¸€è¡Œå®‰æŽ’ï¼Œæœ‰å¾…ä¼˜åŒ–å¤„ç† -->
      <!-- <el-button v-if="showAuthority.debug" @click="addCrm()" type="success" size="mini" :disabled="disabled"><i class="iconfont icon-crm" style="font-size: 10px;"></i> CRM</el-button> -->
      <el-button v-if="showAuthority.debug" @click="addWorkorder()" type="warning" size="mini" :disabled="disabled"><i class="iconfont icon-workorder" style="font-size: 10px;"></i> å·¥å•</el-button>
      <!-- <el-button v-if="showAuthority.debug" @click="showInviteChat()" type="warning" size="mini" :disabled="disabled"><i class="iconfont icon-invite" style="font-size: 10px;"></i> é‚€è¯·</el-button> -->
      <el-button @click="showTransferChat()" type="success" size="mini" icon="el-icon-sort" :disabled="disabled">è½¬æŽ¥</el-button>
      <el-button @click="showNoteDialog()" type="info" size="mini" icon="el-icon-document" :disabled="disabled">å¤‡æ³¨</el-button>
      <!-- è¿ç§»åˆ°è¾“å…¥æ¡†ä¸Šæ–¹ -->
      <!-- <el-button @click="showInviteRate()" type="warning" size="mini" icon="el-icon-star-off" :disabled="disabled">é‚€è¯„</el-button> -->
      <el-button @click="showCloseChat()" type="danger" size="mini" icon="el-icon-close" :disabled="disabled">å…³é—­</el-button>
    </span>

    <el-dialog
      title="å¤‡æ³¨"
      :visible.sync="noteDialogVisible"
      width="30%"
      :before-close="handleClose"
      center>
      <el-input type="textarea"
        :autosize="{ minRows: 4, maxRows: 8}"
        placeholder="è¯·è¾“å…¥å¤‡æ³¨å†…å®¹"
        v-model="noteContent">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="noteDialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="doSaveNote">ç¡® å®š</el-button>
      </span>
    </el-dialog>

    <!-- TODO: è¿‡æ»¤æŽ‰ç¦»çº¿åå¸­ï¼Œå¢žåŠ ç¿»é¡µ -->
    <el-dialog style="text-align: left;"
        title="è½¬æŽ¥ä¼šè¯ç»™å½“å‰åœ¨çº¿å®¢æœäººå‘˜"
        width="30%"
        :visible.sync="transferDialogVisible"
        :before-close="handleClose" center>
        <el-table :data="contactTransferTableData" empty-text="æ— å®¢æœåœ¨çº¿">
          <el-table-column type="index"></el-table-column>
          <!-- <el-table-column property="avatar" label="å¤´åƒ"></el-table-column> -->
          <el-table-column property="realName" label="å®¢æœå"></el-table-column>
          <!-- <el-table-column property="status" label="åœ¨çº¿çŠ¶æ€"></el-table-column> -->
          <!-- <el-table-column property="name" label="å¿™é—²ç¨‹åº¦"></el-table-column> -->
          <el-table-column fixed="right" label="æ“ä½œ">
            <template slot-scope="scope">
              <el-button type="primary" size="small"
              :disabled="isTransferButtonDisabled(scope.row.status)"
              @click="doTransferChat(scope.$index, scope.row)">
                è½¬æŽ¥
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="transferDialogVisible = false">å– æ¶ˆ</el-button>
        </span>
    </el-dialog>

    <!-- TODO: è¿‡æ»¤æŽ‰ç¦»çº¿åå¸­, å¢žåŠ ç¿»é¡µ -->
    <el-dialog style="text-align: left;"
        title="é‚€è¯·å…¶ä»–åœ¨çº¿å®¢æœäººå‘˜åŠ å…¥ä¼šè¯"
        width="30%"
        :visible.sync="inviteDialogVisible"
        :before-close="handleClose" center>
        <div class="invite-dialog-body">
          <el-transfer
            v-model="invitedContactUserids"
            :titles="['å½“å‰åœ¨çº¿å®¢æœ', 'è¢«é‚€è¯·å®¢æœ']"
            :data="contactInviteData">
          </el-transfer>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="inviteDialogVisible = false">å– æ¶ˆ</el-button>
          <el-button type="primary" @click="doInviteChat()">ç¡® å®š</el-button>
        </span>
    </el-dialog>

  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
// import * as constants from '@/util/constants'
import * as https from '@/apis/http'

export default {
  name: 'bd-top',
  data () {
    return {
      noteDialogVisible: false,
      noteContent: '',
      transferDialogVisible: false,
      inviteDialogVisible: false,
      // å½“å‰å·²ç»åŠ å…¥ä¼šè¯çš„å®¢æœusername
      invitedContactUserids: []
    }
  },
  computed: {
    ...mapGetters([ 'accessToken', 'userCurrentChatType', 'userCurrentThread',
      'userCurrentContact', 'userCurrentGroup', 'userInfo', 'onlineAgents', 'showAuthority' ]),
    disabled () {
      return this.userCurrentThread.id === 0
    },
    // è½¬æŽ¥å®¢æœæ•°æ®
    contactTransferTableData () {
      // éžåœ¨çº¿çŠ¶æ€çš„å®¢æœä¸èƒ½è½¬æŽ¥
      var transferContact = []
      for (var i = this.onlineAgents.content.length - 1; i >= 0; i--) {
        let uid = this.onlineAgents.content[i].uid
        let robot = this.onlineAgents.content[i].robot
        // è¿‡æ»¤æŽ‰è‡ªå·±å’Œæœºå™¨äºº
        if (this.userInfo.uid === uid || robot) {
          continue
        }
        let realName = this.onlineAgents.content[i].realName
        let status = this.onlineAgents.content[i].acceptStatus
        let contact = {uid, realName, status}
        transferContact.push(contact)
      }
      return transferContact
    },
    // é‚€è¯·å®¢æœæ•°æ®
    contactInviteData () {
      // éžåœ¨çº¿çŠ¶æ€çš„å®¢æœä¸èƒ½é‚€è¯·
      var inviteContact = []
      for (var i = this.onlineAgents.content.length - 1; i >= 0; i--) {
        let key = this.onlineAgents.content[i].uid
        let robot = this.onlineAgents.content[i].robot
        if (this.userInfo.uid === key || robot) {
          continue
        }
        let label = this.onlineAgents.content[i].realName
        let disabled = false
        let contact = {key, label, disabled}
        inviteContact.push(contact)
      }
      return inviteContact
    }
  },
  methods: {
    ...mapActions([ 'inviteRate', 'closeThread', 'setNote', 'getOnlineAgents' ]),
    // è½¬æŽ¥ä¼šè¯
    showTransferChat () {
      this.transferDialogVisible = true
      //
      let payload = { page: 0, size: 100 }
      this.getOnlineAgents(payload)
    },
    isTransferButtonDisabled (status) {
      return false
    },
    doTransferChat (index, row) {
      // console.log('do transfer:', index, row)
      let uid = row.uid
      let tid = this.userCurrentThread.tid
      https.transfer(uid, tid, this.accessToken, response => {
        console.log(response)
        if (response.status_code === 200) {
          this.$message({ message: 'è½¬æŽ¥ä¼šè¯æˆåŠŸ', type: 'success' })
          this.transferDialogVisible = false
        } else {
          this.$message.error(response.message)
        }
      }, error => {
        console.log(error)
        this.$message.error('è½¬æŽ¥ä¼šè¯é”™è¯¯')
      })
    },
    showNoteDialog () {
      console.log('å¤‡æ³¨')
      this.noteDialogVisible = true
    },
    doSaveNote () {
      console.log('save note')
      let payload = { uid: this.userCurrentThread.visitor.uid, value: this.noteContent }
      this.setNote(payload)
      this.noteDialogVisible = false
    },
    // é‚€è¯·ä¼šè¯
    showInviteChat () {
      this.inviteDialogVisible = true
      //
      let payload = { page: 0, size: 100 }
      this.getOnlineAgents(payload)
    },
    doInviteChat () {
      // console.log('do invite')
      let tid = this.userCurrentThread.tid
      https.invite(this.invitedContactUserids, tid, this.accessToken, response => {
        console.log(response)
        if (response.status_code === 200) {
          this.$message({ message: 'é‚€è¯·ä¼šè¯æˆåŠŸ', type: 'success' })
          this.inviteDialogVisible = false
        } else {
          this.$message.error(response.message)
        }
      }, error => {
        console.log(error)
        this.$message.error('è½¬æŽ¥ä¼šè¯é”™è¯¯')
      })
    },
    // é‚€è¯·è¯„ä»·
    // TODO: æ”¶åˆ°è®¿å®¢è¯„ä»·ä¹‹åŽï¼Œç¦æ­¢é‡å¤é‚€è¯·
    showInviteRate () {
      this.inviteRate(this.userCurrentThread.tid)
    },
    showCloseChat () {
      this.$confirm('ç¡®å®šè¦å…³é—­ä¼šè¯?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        // TODO: é¦–å…ˆåˆ¤æ–­æ˜¯å¦æ˜¯é‚€è¯·åŠ å…¥çš„ä¼šè¯ï¼Œå¦‚æžœæ˜¯ï¼Œéœ€è¦è°ƒç”¨é€€å‡ºä¼šè¯æŽ¥å£ï¼Œå¦åˆ™è°ƒç”¨å…³é—­ä¼šè¯æŽ¥å£
        //
        // å…³é—­ä¼šè¯
        this.closeThread(this.userCurrentThread.tid)
      }).catch(() => {
      })
    },
    addWorkorder () {
      console.log('addWorkorder')
    },
    addCrm () {
      console.log('add crm')
    },
    handleClose (done) {
      done()
    }
  }

}
</script>

<style scoped>
#bd-top {
  width: 100%;
  height: 50px;
  border-bottom:solid 0.1px #dddddd;
}
#bd-top .avatar {
  margin-top: 7px;
  margin-left: 10px;
  float: left;
}
#bd-top .nickname {
  max-width: 100px;
  margin-left: 5px;
  margin-top: 12px;
  float: left;
  font-size: 12px;
}
#bd-top .wrapper {
  float: right;
  margin-right: 10px;
  margin-top: 10px;
  overflow: hidden;
  height: 40px;
}
</style>



