<template>
  <!-- TODO: å¢žåŠ ç¦»çº¿æ¶ˆæ¯æŽ¨é€ï¼Œä¸Šä¼ pemæ–‡ä»¶é¡µé¢ -->
  <div id='bd-app-admin'>
    <el-row>
      <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>å¸®åŠ©æ–‡æ¡£</el-button>
      <el-button size="mini" type="primary" style="float: left;" @click="showAddDialog">æ·»åŠ åº”ç”¨</el-button>
    </el-row>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="apps.content" style="width: 100%;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="aid" label="å”¯ä¸€ID(aid)"></el-table-column>
      <el-table-column prop="name" label="åº”ç”¨å"></el-table-column>
      <el-table-column prop="key" label="AppKey"></el-table-column>
      <el-table-column prop="version" label="ç‰ˆæœ¬å·"></el-table-column>
      <el-table-column prop="tip" label="å‡çº§å†…å®¹"></el-table-column>
      <el-table-column prop="url" label="ä¸‹è½½ç½‘å€"></el-table-column>
      <el-table-column prop="status" label="çŠ¶æ€">
        <template slot-scope="scope">
          <span>{{ formatStatus(scope.row.status) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="platform" label="å¹³å°">
        <template slot-scope="scope">
          <span>{{ formatPlatform(scope.row.platform) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="description" label="æè¿°"></el-table-column>
      <!-- <el-table-column prop="updatedAt" label="æ›´æ–°æ—¶é—´"></el-table-column> -->
      <el-table-column fixed="right" label="æ“ä½œ" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
          <el-button v-if="!scope.row.defaulted" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="æ·»åŠ åº”ç”¨"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="åº”ç”¨åç§°" prop="name">
          <el-input v-model="form.name" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="å¤´åƒ" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 10px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">ç‚¹å‡»ä¸Šä¼ </el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="çŠ¶æ€" prop="status">
          <el-radio v-model="form.status" v-for="status in statuses" :key="status.id" :label="status.value">{{ status.name }}</el-radio>
        </el-form-item>
        <el-form-item label="å¹³å°" prop="platform">
          <el-radio v-model="form.platform" v-for="platform in platforms" :key="platform.id" :label="platform.value">{{ platform.name }}</el-radio>
        </el-form-item>
        <el-form-item label="ç‰ˆæœ¬å·" prop="version">
          <el-input v-model="form.version" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="å‡çº§å†…å®¹" prop="tip">
          <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 5}" v-model="form.tip" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="ä¸‹è½½ç½‘å€" prop="url">
          <el-input v-model="form.url" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="åº”ç”¨æè¿°" prop="description">
          <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 10}" v-model="form.description" style="width: 350px;"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="addApp">ç¡® å®š</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-app-admin',
  data () {
    return {
      dialogVisible: false,
      isEdit: false,
      form: {
        aid: '',
        name: '',
        avatar: constants.DEFAULT_IOS_AVATAR_URL,
        version: '',
        tip: '',
        url: '',
        status: 'debug',
        platform: 'both',
        description: ''
      },
      rules: {
        name: [
          { required: true, message: 'è¯·è¾“å…¥ç½‘ç«™åç§°', trigger: 'blur' }
        ],
        // url: [
        //   { required: true, message: 'è¯·è¾“å…¥ç½‘å€', trigger: 'blur' }
        // ],
        description: [
          { required: true, message: 'è¯·è¾“å…¥ç½‘ç«™ç®€ä»‹', trigger: 'blur' }
        ]
      },
      statuses: [
        { id: 1, value: 'debug', name: 'æµ‹è¯•ä¸­' },
        { id: 2, value: 'release', name: 'å·²å‘å¸ƒ' },
        { id: 3, value: 'version', name: 'æ–°ç‰ˆæœ¬' }
      ],
      platforms: [
        { id: 1, value: 'android', name: 'å®‰å“' },
        { id: 2, value: 'ios', name: 'è‹¹æžœ' },
        { id: 3, value: 'both', name: 'å®‰å“+è‹¹æžœ' }
      ],
      // ä¸Šä¼ å›¾ç‰‡ç›¸å…³å‚æ•°
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL
    }
  },
  computed: {
    ...mapGetters([ 'apps' ])
  },
  methods: {
    ...mapActions([ 'getApps', 'createApp', 'updateApp', 'deleteApp' ]),
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('ä¸Šä¼ å›¾ç‰‡é”™è¯¯ï¼Œè¯·ç¨åŽé‡è¯•...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // éªŒè¯å›¾ç‰‡æ ¼å¼
      if (!isJPGorPNG) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡åªèƒ½æ˜¯ jpg æˆ– png æ ¼å¼!')
      }
      // éªŒè¯å›¾ç‰‡å¤§å°
      if (!isLt2M) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡ 2MB!')
      }
      // è®¾ç½®æ–‡ä»¶å
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // è¿”å›ž
      return isJPGorPNG && isLt2M
    },
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201808252118181')
    },
    formatStatus (status) {
      return status === 'debug' ? 'æµ‹è¯•ä¸­' : (status === 'release' ? 'å·²å‘å¸ƒ' : 'æ–°ç‰ˆæœ¬')
    },
    formatPlatform (platform) {
      return platform === 'both' ? 'å®‰å“+è‹¹æžœ' : (platform === 'android' ? 'å®‰å“' : 'è‹¹æžœ')
    },
    showAddDialog () {
      this.dialogVisible = true
      this.isEdit = false
      //
      this.form.aid = ''
      this.form.name = ''
      this.form.avatar = constants.DEFAULT_IOS_AVATAR_URL
      this.form.version = ''
      this.form.tip = ''
      this.form.url = ''
      this.form.status = 'debug'
      this.form.platform = 'both'
      this.form.description = ''
    },
    addApp () {
      this.dialogVisible = false
      if (this.isEdit) {
        this.updateApp(this.form)
      } else {
        this.createApp(this.form)
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      //
      this.form.aid = row.aid
      this.form.name = row.name
      this.form.avatar = row.avatar
      this.form.version = row.version
      this.form.tip = row.tip
      this.form.url = row.url
      this.form.status = row.status
      this.form.platform = row.platform
      this.form.description = row.description
      //
      this.isEdit = true
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteApp(row.aid)
      }).catch(() => {
        //
      })
    }
  }

}
</script>

<style scoped>
#bd-app-admin {
  height: 100%;
  text-align: left;
}
</style>



