<template>
  <div id='bd-synonym'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" plain>å¸®åŠ©æ–‡æ¡£</el-button>
    <el-button size="mini" type="success" style="float: left;" @click="addSynonym">æ·»åŠ ç›¸ä¼¼è¯</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="robotSynonyms.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="robotSynonyms.content" style="width: 100%">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="standard" label="è¯è¯­"></el-table-column>
      <el-table-column prop="synonyms" label="ç›¸ä¼¼è¯">
        <template slot-scope="scope">
          <span v-for="(synonym, index) in scope.row.synonyms" :key="synonym.id">
            {{ synonym.standard }} <span v-if="index < scope.row.synonyms.length-1">,</span>
          </span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="æ“ä½œ" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="robotSynonyms.totalElements">
    </el-pagination>
    <el-dialog
      title="æ·»åŠ ç›¸ä¼¼è¯"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="æ ‡å‡†è¯" prop="standard">
          <el-input v-model="form.standard" style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item
          v-for="(synonym, index) in mySynonyms"
          label="ç›¸ä¼¼è¯"
          :key="synonym.id">
          <el-input v-model="synonym.synonym" style="width: 250px;"></el-input>
          <el-button v-if="index > 0" @click.prevent="removeSynonym(synonym.id)">åˆ é™¤</el-button>
          <el-button v-if="index === 0" @click.prevent="addSynonyms()">æ·»åŠ </el-button>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="doAddSynonym()">ç¡® å®š</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'bd-synonym',
  data () {
    return {
      //
      pageSize: 20,
      currentPage: 1,
      //
      isEdit: false,
      dialogVisible: false,
      synonymIndex: 0,
      tableData: [],
      form: {
        sid: '',
        standard: '',
        synonyms: [{
          id: 0,
          synonym: ''
        }]
      },
      mySynonyms: [{
        id: 0,
        synonym: ''
      }],
      rules: {
        standard: [
          { required: true, message: 'è¯·è¾“å…¥æ ‡å‡†è¯', trigger: 'blur' }
        ],
        synonym: [
          { required: true, message: 'è¯·è¾“å…¥ç›¸ä¼¼è¯', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([ 'robotSynonyms' ])
  },
  methods: {
    ...mapActions([ 'createRobotSynonym', 'updateRobotSynonym', 'deleteRobotSynonym' ]),
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
    },
    addSynonyms () {
      this.synonymIndex++
      this.mySynonyms.push({
        id: this.synonymIndex,
        synonym: ''
      })
    },
    removeSynonym (id) {
      for (let i = 0; i < this.mySynonyms.length; i++) {
        const synonym = this.mySynonyms[i]
        if (synonym.id === id) {
          this.mySynonyms.splice(i, 1)
        }
      }
    },
    transformSynonyms () {
      let localSynonyms = []
      for (let i = 0; i < this.mySynonyms.length; i++) {
        const synonym = this.mySynonyms[i]
        localSynonyms.push(synonym.synonym)
      }
      this.form.synonyms = localSynonyms
    },
    addSynonym () {
      this.isEdit = false
      //
      this.form.sid = ''
      this.form.standard = ''
      this.mySynonyms = [{ id: 0, synonym: '' }]
      //
      this.dialogVisible = true
    },
    doAddSynonym () {
      this.transformSynonyms()
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          if (this.isEdit) {
            this.updateRobotSynonym(this.form)
          } else {
            this.createRobotSynonym(this.form)
          }
        } else {
          return false
        }
      })
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.isEdit = true
      //
      this.mySynonyms = []
      this.form.sid = row.sid
      this.form.standard = row.standard
      for (let i = 0; i < row.synonyms.length; i++) {
        const syn = row.synonyms[i]
        this.synonymIndex = i
        this.mySynonyms.push({ id: i, synonym: syn.standard })
      }
      //
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        //
        this.deleteRobotSynonym(row.sid)
      }).catch(() => {
        //
      })
    }
  }

}
</script>

<style scoped>
#bd-synonym {
  height: 100%;
  text-align: left;
}
</style>



