<template>
  <div id='bd-info'>
    <el-table size="mini" class="bd-info-table" :data="fingerPrints">
      <el-table-column prop="name" label="å‚æ•°"></el-table-column>
      <el-table-column prop="value" label="å€¼"></el-table-column>
    </el-table>
    <el-tag
      class="bd-user-tags"
      :key="tag.id"
      v-for="tag in tags"
      :closable="true"
      :close-transition="false"
      @close="handleTagDelete(tag.id)">
        {{tag.name}}
    </el-tag>
    <el-input
      class="bd-input-new-tag"
      v-if="inputVisible"
      v-model="inputValue"
      ref="saveTagInput"
      size="mini"
      placeholder="å›žè½¦è¾“å…¥"
      @keyup.enter.native="handleInputConfirm"
      @blur="handleInputConfirm">
    </el-input>
    <!-- <el-button v-else class="bd-new-tag" size="small" @click="showTagInput" :disabled="disabled">æ·»åŠ æ ‡ç­¾</el-button> -->
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'bd-info',
  data () {
    return {
      // ç”¨æˆ·ä¿¡æ¯
      tags: [], //
      //
      inputVisible: false,
      inputValue: ''
    }
  },
  computed: {
    ...mapGetters([ 'userCurrentThread', 'fingerPrints' ]),
    disabled () {
      return this.userCurrentThread.id === 0
    }
  },
  methods: {
    ...mapActions([ 'fingerPrint', 'userTag' ]),
    handleTagDelete (tagid) {},
    showTagInput () {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    handleInputConfirm () {
      let tagName = this.inputValue
      if (tagName) {}
    }
  }
}
</script>

<style scoped>
#bd-info {
  height: 100%;
  width: 348px;
  text-align: left;
}
.bd-info-table {
  margin-top: 10px;
  margin-bottom: 10px;
  height: 100%;
  overflow-y: scroll !important;
}
.bd-user-tags {
  margin-top: 20px;
  margin-right: 5px;
}
.bd-input-new-tag{
  width:78px;
  margin-top: 20px;
  margin-left: 5px;
  vertical-align:bottom;
}
.bd-new-tag {
  margin-top: 20px;
  margin-left:10px;
}
</style>



