      if (tab.name === 'thread') {
        this.getHistoryThread(payload)
      } else if (tab.name === 'rate') {
        this.getRates(payload)
      } else if (tab.name === 'message') {
        this.getHistoryMessages(payload)
      } else if (tab.name === 'leavemsg') {
        this.getLeaveMessages(payload)
      } else if (tab.name === 'status') {
