<template>
  <div id='bd-cuw'>
    <el-input size="mini" placeholder="æœç´¢" suffix-icon="el-icon-search" v-model="filterCUW"></el-input>
    <el-tree
      class="filter-tree"
      :highlight-current="true"
      :data="allCuws"
      :props="defaultProps"
      default-expand-all
      :filter-node-method="filterCUWNode"
      @node-click="handleCUWNodeClick"
      ref="treeCUW">
    </el-tree>
    <div style="margin-top: 15px; float: left;">
      <el-dropdown style="margin-right: 5px; float: left;">
        <el-button type="success" size="mini">
          æ·»åŠ <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="addCategory">åˆ†ç»„</el-dropdown-item>
          <el-dropdown-item @click.native="addCUW">å¸¸ç”¨è¯­</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button @click="editCUW()" type="primary" size="mini" style="margin-right: -5px; float: left;" :disabled="editButtonDisabled">ç¼–è¾‘</el-button>
      <el-button @click="deleteCUW()" type="danger" size="mini" style="float: left;" :disabled="deleteButtonDisabled">åˆ é™¤</el-button>
    </div>
    <el-dialog
      title="æ·»åŠ å¸¸ç”¨è¯­"
      :visible.sync="addCuwDialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="æ ‡é¢˜" prop="name">
          <el-input v-model="form.name" placeholder="æ ‡é¢˜"></el-input>
        </el-form-item>
        <el-form-item label="å†…å®¹" prop="content">
          <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 20}" v-model="form.content"></el-input>
        </el-form-item>
        <el-form-item label="æ‰€å±žåˆ†ç»„" prop="categoryId" v-if="!isEdit">
          <span v-if="allCuws[0].cuwChildren.length === 0">è¯·é¦–å…ˆåˆ›å»ºè‡³å°‘ä¸€ä¸ªåˆ†ç»„</span>
          <el-radio-group v-model="form.categoryId">
            <el-radio v-if="category.id > 0" v-for="category in allCuws[0].cuwChildren" :key="category.id" :label="category.id">{{ category.name }}</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCuwDialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="doAddCuw()">ç¡® å®š</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-cuw',
  data () {
    return {
      isEdit: false,
      addCuwDialogVisible: false,
      currentSelectedNote: {
        id: 0,
        name: '',
        content: '',
        type: '',
        categoryId: 0,
        cuwChildren: []
      },
      form: {
        id: 0,
        name: '',
        content: '',
        categoryId: '',
        cuwChildren: []
      },
      rules: {
        name: [
          { required: true, message: 'è¯·è¾“å…¥æ ‡é¢˜', trigger: 'blur' }
        ],
        content: [
          { required: true, message: 'è¯·è¾“å…¥å†…å®¹', trigger: 'blur' }
        ],
        categoryId: [
          { required: true, message: 'è¯·è‡³å°‘é€‰æ‹©ä¸€ä¸ªåˆ†ç»„', trigger: 'change' }
        ]
      },
      emptyText: 'æš‚æ— æ•°æ®',
      filterCUW: '',
      defaultProps: {
        children: 'cuwChildren',
        label: 'name'
      }
    }
  },
  computed: {
    ...mapGetters([ 'allCuws' ]),
    editButtonDisabled () {
      for (let i = 0; i < this.allCuws[0].cuwChildren.length; i++) {
        const category = this.allCuws[0].cuwChildren[i]
        if (category.id === this.currentSelectedNote.id) {
          return false
        }
        for (let j = 0; j < category.cuwChildren.length; j++) {
          const cuw = category.cuwChildren[j]
          if (cuw.id === this.currentSelectedNote.id) {
            return false
          }
        }
      }
      return true
    },
    deleteButtonDisabled () {
      for (let i = 0; i < this.allCuws[0].cuwChildren.length; i++) {
        const category = this.allCuws[0].cuwChildren[i]
        if (category.id === this.currentSelectedNote.id) {
          if (category.cuwChildren.length > 0) {
            return true
          }
          return false
        }
        for (let j = 0; j < category.cuwChildren.length; j++) {
          const cuw = category.cuwChildren[j]
          if (cuw.id === this.currentSelectedNote.id) {
            return false
          }
        }
      }
      return true
    }
  },
  watch: {
    filterCUW (val) {
      this.$refs.treeCUW.filter(val)
    }
  },
  methods: {
    ...mapActions([ 'createCategory', 'updateCategory', 'deleteCategory',
      'cuwCreate', 'cuwUpdate', 'cuwDelete' ]),
    filterCUWNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    handleCUWNodeClick (data) {
      // console.log(data)
      if (data.id === 0 || data.id === -1 || data.id === -2) {
        this.currentSelectedNote.id = 0
        return
      }
      //
      this.currentSelectedNote.id = data.id
      this.currentSelectedNote.name = data.name
      this.currentSelectedNote.type = data.type
      if (data.type === constants.CATEGORY_TYPE_CUW_MINE) {
        this.currentSelectedNote.cuwChildren = data.cuwChildren
      } else {
        // å¸¸ç”¨è¯­
        // FIXME: ç›´æŽ¥åœ¨ç¼–è¾‘å¯¹è¯æ¡†ä¿®æ”¹åˆ†ç»„
        // this.currentSelectedNote.categoryId = data.category.id
        this.currentSelectedNote.content = data.content
      }
      // äº‹ä»¶æ€»çº¿å¹¿æ’­äº‹ä»¶
      if (data.content !== null && data.content !== undefined) {
        bus.$emit(constants.EVENT_BUS_SELECT_CUW, data.content)
      }
    },
    // åˆ†ç±»
    addCategory () {
      this.$prompt('è¯·è¾“å…¥åˆ†ç»„', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ'
      }).then(({ value }) => {
        console.log(value)
        if (value !== undefined && value !== null && value.length > 0) {
          let payload = {
            parent_id: 0,
            name: value,
            type: constants.CATEGORY_TYPE_CUW_MINE
          }
          this.createCategory(payload)
        }
      }).catch(() => {
        // å–æ¶ˆ
      })
    },
    addCUW () {
      this.isEdit = false
      this.addCuwDialogVisible = true
    },
    doAddCuw () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.addCuwDialogVisible = false
          if (this.isEdit) {
            //
            let payload = { id: this.form.id, name: this.form.name, content: this.form.content }
            this.cuwUpdate(payload)
            this.currentSelectedNote.id = 0
          } else {
            //
            let payload = { categoryId: this.form.categoryId, name: this.form.name, content: this.form.content }
            this.cuwCreate(payload)
          }
        } else {
          return false
        }
      })
    },
    editCUW () {
      this.isEdit = true
      //
      this.form.id = this.currentSelectedNote.id
      this.form.name = this.currentSelectedNote.name
      if (this.currentSelectedNote.type === constants.CATEGORY_TYPE_CUW_MINE) {
        // åˆ†ç»„
        this.$prompt('ç¼–è¾‘åˆ†ç»„', 'æç¤º', {
          confirmButtonText: 'ç¡®å®š',
          cancelButtonText: 'å–æ¶ˆ',
          inputValue: this.form.name
        }).then(({ value }) => {
          // console.log(value)
          if (value !== undefined && value !== null && value.length > 0) {
            let payload = { category_id: this.form.id, name: value }
            this.updateCategory(payload)
            this.currentSelectedNote.id = 0
          }
        }).catch(() => {
          // å–æ¶ˆ
        })
      } else {
        // å¸¸ç”¨è¯­
        this.form.categoryId = this.currentSelectedNote.categoryId
        this.form.content = this.currentSelectedNote.content
        this.addCuwDialogVisible = true
      }
    },
    deleteCUW () {
      if (this.currentSelectedNote.cuwChildren.length > 0) {
        this.$message({
          message: 'è¯·é¦–å…ˆåˆ é™¤å­å¸¸ç”¨è¯­',
          type: 'warning'
        })
        return
      }
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        if (this.currentSelectedNote.type === constants.CATEGORY_TYPE_CUW_MINE) {
          // åˆ†ç»„
          this.deleteCategory(this.currentSelectedNote.id)
        } else {
          // å¸¸ç”¨è¯­
          this.cuwDelete(this.currentSelectedNote.id)
        }
        this.currentSelectedNote.id = 0
      }).catch(() => {
      })
    }
  },
  created () {
  },
  mounted () {
  }
}
</script>

<style scoped>
#bd-cuw{
  height: 100%;
  width: 100%;
}
.filter-tree {
  margin-top: 10px;
  margin-bottom: 10px;
  height: 100%;
  overflow-y: scroll !important;
}
/* .el-tree-node__label {
  font-size: 12px !important;
} */
</style>



