<template>
  <div id='bd-register'>
    <el-main>
      <el-row v-if="firstStep">
        <el-col :span="8"><div class="grid-content"></div></el-col>
        <el-col :span="6">
          <el-form :model="emailForm" status-icon :rules="registerRoles" ref="emailForm" label-width="100px">
            <el-form-item label="é‚®ç®±" prop="email">
              <el-input type="email" placeholder="è¯·è¾“å…¥é‚®ç®±" v-model="emailForm.email" clearable style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item label="å¯†ç " prop="password">
              <el-input type="password" v-model="emailForm.password" auto-complete="off" style="width: 300px;" clearable></el-input>
            </el-form-item>
            <el-form-item label="ç¡®è®¤å¯†ç " prop="rePassword">
              <el-input type="password" v-model="emailForm.rePassword" auto-complete="off" style="width: 300px;" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button @click="routeLogin">ç™»å½•</el-button>
              <el-button type="primary" @click="register" :loading="logining">æ³¨å†Œ</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="8"><div class="grid-content"></div></el-col>
      </el-row>
      <!-- TODO: å¢žåŠ å…¬å¸åã€å…¬å¸ç½‘ç«™ã€å…¬å¸äººæ•° è”ç³»ç”µè¯ ç­‰æ³¨å†Œä¿¡æ¯ -->
      <el-row v-if="secondStep">
        <el-col :span="10"><div class="grid-content"></div></el-col>
        <el-col :span="4">
          <el-form label-width="150px">
            <el-form-item label="è¯·ç‰¢è®°æ‚¨çš„ä¼ä¸šå·ï¼š">{{ this.subDomainForm.sub_domain }}</el-form-item>
            <el-form-item>
              <el-button @click="routeLogin">åŽ»ç™»å½•</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="10"><div class="grid-content"></div></el-col>
      </el-row>
    </el-main>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as https from '@/apis/http'
import * as constants from '@/util/constants'

export default {
  name: 'bd-register',
  data () {
    var validateEmail = (rule, value, callback) => {
      // éªŒè¯é‚®ç®±å”¯ä¸€æ€§
      https.emailValidate(this.emailForm.email,
        response => {
          console.log(response)
          if (response.status_code === 200) {
            callback()
          } else {
            // é‚®ç®±å·²ç»å­˜åœ¨ï¼Œæç¤ºç”¨æˆ·é‡æ–°å¡«å†™
            callback(new Error('é‚®ç®±å·²ç»å­˜åœ¨ï¼Œè¯·é‡æ–°å¡«å†™'))
          }
        }, error => {
          console.log(error)
        })
    }
    var validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('è¯·è¾“å…¥å¯†ç '))
      } else {
        if (this.emailForm.rePassword !== '') {
          this.$refs.emailForm.validateField('rePassword')
        }
        callback()
      }
    }
    var validateRePassword = (rule, value, callback) => {
      if (value !== this.emailForm.password) {
        callback(new Error('ä¸¤æ¬¡è¾“å…¥å¯†ç ä¸ä¸€è‡´!'))
      } else {
        callback()
      }
    }
    return {
      // æ˜¯å¦ç™»å½•ä¸­
      logining: false,
      // models
      emailForm: {
        email: '',
        password: '',
        rePassword: ''
      },
      subDomainForm: {
        sub_domain: 'vip'
      },
      // éªŒè¯è§„åˆ™
      registerRoles: {
        email: [
          { required: true, message: 'è¯·è¾“å…¥é‚®ç®±åœ°å€', trigger: 'blur' },
          { type: 'email', message: 'è¯·è¾“å…¥æ­£ç¡®çš„é‚®ç®±åœ°å€', trigger: 'change' },
          { validator: validateEmail, trigger: 'blur' }
        ],
        password: [
          { required: true, message: 'è¯·è¾“å…¥å¯†ç ', trigger: 'blur' },
          { min: 6, max: 16, message: 'é•¿åº¦åœ¨ 6 åˆ° 16 ä¸ªå­—ç¬¦ä¹‹é—´', trigger: 'blur' },
          { validator: validatePassword, trigger: 'blur' }
        ],
        rePassword: [
          { required: true, message: 'è¯·è¾“å…¥ç¡®è®¤å¯†ç ', trigger: 'blur' },
          { validator: validateRePassword, trigger: 'blur' }
        ]
      },
      // éªŒè¯é‚®ç®±
      firstStep: true,
      // æ˜¾ç¤ºä¼ä¸šå·
      secondStep: false
    }
  },
  computed: {
    ...mapGetters([ 'mainHost' ])
  },
  methods: {
    ...mapActions([
      'updatePassportToken'
    ]),
    register () {
      // é¦–å…ˆéªŒè¯formæ˜¯å¦é€šè¿‡ï¼Œç„¶åŽè°ƒç”¨æ³¨å†ŒæŽ¥å£
      this.$refs['emailForm'].validate((valid) => {
        if (valid) {
          this.logining = true
          https.register(this.emailForm.email.toLowerCase(), this.emailForm.password,
            response => {
              console.log('register success:', response)
              this.subDomainForm.sub_domain = response.data.subDomain
              this.$message({message: 'æ³¨å†ŒæˆåŠŸ', type: 'success'})
              this.firstStep = false
              this.secondStep = true
              //
              this.logining = false
            }, error => {
              console.log('register error:', error)
              this.logining = false
            })
        } else {
          return false
        }
      })
    },
    routeLogin () {
      // è·³è½¬ç™»å½•é¡µé¢
      if (constants.IS_ELECTRON) {
        this.$router.replace('/login')
      } else {
        window.location.href = constants.HTTP_PREFIX + this.subDomainForm.sub_domain + '.' + this.mainHost + '/login'
      }
    }
  }

}
</script>

<style scoped>
#bd-register {
  margin-top: 150px;
}
.grid-content {
  min-height: 36px;
}
</style>



