<template>
  <!-- TODO: ç®¡ç†å‘˜æŸ¥çœ‹å…¨éƒ¨ã€å®¢æœç»„é•¿æŸ¥çœ‹ç»„å†…éƒ¨ã€å®¢æœäººå‘˜æŸ¥çœ‹è‡ªå·±çš„æ•°æ® -->
  <!-- TODO: è‡ªåŠ¨ç”Ÿæˆæ—¥æŠ¥ã€å‘¨æŠ¥ã€æœˆæŠ¥ç­‰ -->
  <div id='bd-statistic'>
    <el-tabs v-model="activeName" @tab-click="handleClick" style="height: 100%;" @edit="handleTabsEdit">
      <el-tab-pane label="æ•°æ®æ€»è§ˆ" name="all" style="height: 100%;">
        <bd-all></bd-all>
      </el-tab-pane>
      <el-tab-pane label="ä¼šè¯åˆ†æž" name="thread" style="height: 100%;" v-if="showAuthority.debug">
        <bd-thread></bd-thread>
      </el-tab-pane>
      <el-tab-pane label="æ»¡æ„åº¦åˆ†æž" name="rate" style="height: 100%;" v-if="showAuthority.debug">
        <bd-rate></bd-rate>
      </el-tab-pane>
      <!-- <el-tab-pane label="åå¸­åˆ†æž" name="agent" style="height: 100%;" v-if="showAuthority.debug">
        <bd-agent></bd-agent>
      </el-tab-pane>
      <el-tab-pane label="å·¥å•åˆ†æž" name="ticket" style="height: 100%;" v-if="showAuthority.debug">
        <bd-ticket></bd-ticket>
      </el-tab-pane> -->
      <el-tab-pane closable style="height: 100%;"
        :key="item.name"
        v-for="item in editableTabs"
        :label="item.title"
        :name="item.name">
        <!-- <bd-new v-if="!item.edit"></bd-new>
        <bd-edit v-if="item.edit" :article="item.article"></bd-edit> -->
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdAll from '@/components/dashboard/main/statistic/children/BdAll'
import BdRate from '@/components/dashboard/main/statistic/children/BdRate'
import BdThread from '@/components/dashboard/main/statistic/children/BdThread'
import BdTicket from '@/components/dashboard/main/statistic/children/BdTicket'
import BdAgent from '@/components/dashboard/main/statistic/children/BdAgent'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-statistic',
  components: { BdAll, BdAgent, BdRate, BdThread, BdTicket },
  data () {
    return {
      activeName: 'all',
      tabIndex: 1,
      editableTabs: []
    }
  },
  computed: {
    ...mapGetters([ 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getStatistics', 'getStatisticThread', 'getStatisticRate', 'changeCurrentMainView' ]),
    handleClick (tab, event) {
      let payload = { page: 0, size: 20 }
      if (tab.name === 'all') {
        this.getStatistics()
      } else if (tab.name === 'thread') {
        this.getStatisticThread(payload)
      } else if (tab.name === 'rate') {
        this.getStatisticRate(payload)
      } else if (tab.name === 'mini') {
        //
      }
    },
    handleTabsEdit (targetName, action) {
      // console.log(targetName, action)
      if (action === 'remove') {
        let tabs = this.editableTabs
        let activeName = 'all'
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1]
              if (nextTab) {
                activeName = nextTab.name
              }
            }
          })
        }
        this.activeName = 'all'
        this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      }
    }
  },
  mounted () {
    this.getStatistics()
    this.changeCurrentMainView('statistic')
    // æŸ¥çœ‹ç»Ÿè®¡è¯¦æƒ…
    bus.$on(constants.EVENT_BUS_STATISTIC_DETAIL, message => {
      console.log('on statisitc detail:', message)
      this.editableTabs.push({
        title: 'æ•°æ®è¯¦æƒ…TODO' + this.tabIndex,
        name: '' + this.tabIndex,
        value: message
      })
      this.activeName = '' + this.tabIndex
      this.tabIndex++
    })
  }
}
</script>

<style scoped>
#bd-statistic {
  height: 100%;
}
</style>



