<template>
  <div id='bd-forget'>
    <el-main>
        <el-row v-if="firstStep">
          <el-col :span="8"><div class="grid-content"></div></el-col>
          <el-col :span="6">
            <el-form :model="emailForm" status-icon :rules="forgetRoles" ref="emailForm" label-width="100px">
              <el-form-item label="é‚®ç®±" prop="email">
                <el-input type="email" placeholder="è¯·è¾“å…¥é‚®ç®±" v-model="emailForm.email" clearable style="width: 300px;">
                  <el-button slot="append" :loading="emailButtonCountingDown" @click="getEmailCode">{{ emailButtonText }}</el-button>
                </el-input>
              </el-form-item>
              <el-form-item label="éªŒè¯ç " prop="email_code">
                <el-input placeholder="è¯·è¾“å…¥éªŒè¯ç " v-model="emailForm.email_code" auto-complete="off" clearable style="width: 300px;"></el-input>
              </el-form-item>
              <el-form-item>
                <!-- <el-button @click="routeLogin">ç™»å½•</el-button> -->
                <el-button type="primary" @click="nextSecondStep">ä¸‹ä¸€æ­¥</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="8"><div class="grid-content"></div></el-col>
        </el-row>
        <el-row v-if="secondStep">
          <el-col :span="8"><div class="grid-content"></div></el-col>
          <el-col :span="6">
            <el-form :model="passwordForm" status-icon :rules="forgetRoles" ref="passwordForm" label-width="100px">
              <el-form-item label="å¯†ç " prop="password">
                <el-input type="password" v-model="passwordForm.password" auto-complete="off" style="width: 300px;" clearable></el-input>
              </el-form-item>
              <el-form-item label="ç¡®è®¤å¯†ç " prop="rePassword">
                <el-input type="password" v-model="passwordForm.rePassword" auto-complete="off" style="width: 300px;" clearable></el-input>
              </el-form-item>
              <el-form-item>
                <el-button @click="preSecondStep">ä¸Šä¸€æ­¥</el-button>
                <el-button type="primary" @click="changePassword">ä¿®æ”¹å¯†ç </el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="8"><div class="grid-content"></div></el-col>
        </el-row>
    </el-main>
  </div>
</template>

<script>
// import { mapActions } from 'vuex'
import * as https from '@/apis/http'

export default {
  name: 'bd-forget',
  data () {
    var validateEmail = (rule, value, callback) => {
      // éªŒè¯é‚®ç®±å”¯ä¸€æ€§
      https.emailForget(this.emailForm.email,
        response => {
          console.log(response)
          if (response.status_code === 200) {
            callback()
          } else {
            callback(new Error('é‚®ç®±ä¸å­˜åœ¨ï¼Œè¯·é‡æ–°å¡«å†™'))
          }
        }, error => {
          console.log(error)
        })
    }
    var validateEmailCode = (rule, value, callback) => {
      // éªŒè¯éªŒè¯ç çš„æœ‰æ•ˆæ€§
      if (parseInt(value) !== this.emailServerCode) {
        callback(new Error('éªŒè¯ç é”™è¯¯'))
      } else {
        callback()
      }
    }
    var validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('è¯·è¾“å…¥å¯†ç '))
      } else {
        if (this.passwordForm.rePassword !== '') {
          this.$refs.passwordForm.validateField('rePassword')
        }
        callback()
      }
    }
    var validateRePassword = (rule, value, callback) => {
      if (value !== this.passwordForm.password) {
        callback(new Error('ä¸¤æ¬¡è¾“å…¥å¯†ç ä¸ä¸€è‡´!'))
      } else {
        callback()
      }
    }
    return {
      emailForm: {
        email: '',
        email_code: ''
      },
      passwordForm: {
        password: '',
        rePassword: ''
      },
      // éªŒè¯è§„åˆ™
      forgetRoles: {
        email: [
          { required: true, message: 'è¯·è¾“å…¥é‚®ç®±åœ°å€', trigger: 'blur' },
          { type: 'email', message: 'è¯·è¾“å…¥æ­£ç¡®çš„é‚®ç®±åœ°å€', trigger: 'change' },
          { validator: validateEmail, trigger: 'blur' }
        ],
        email_code: [
          { required: true, message: 'è¯·è¾“å…¥éªŒè¯ç ', trigger: 'blur' },
          { validator: validateEmailCode, trigger: 'change' }
        ],
        password: [
          { required: true, message: 'è¯·è¾“å…¥å¯†ç ', trigger: 'blur' },
          { min: 6, max: 16, message: 'é•¿åº¦åœ¨ 6 åˆ° 16 ä¸ªå­—ç¬¦ä¹‹é—´', trigger: 'blur' },
          { validator: validatePassword, trigger: 'blur' }
        ],
        rePassword: [
          { required: true, message: 'è¯·è¾“å…¥ç¡®è®¤å¯†ç ', trigger: 'blur' },
          { validator: validateRePassword, trigger: 'blur' }
        ]
      },
      // é‚®ç®±éªŒè¯ç 
      emailServerCode: 0,
      // é‚®ç®±å‘é€éªŒè¯ç buttonæ˜¯å¦ç¦ç”¨
      emailButtonCountingDown: false,
      // 60ç§’å€’è®¡æ—¶
      emailCountDownTime: 0,
      // éªŒè¯é‚®ç®±
      firstStep: true,
      // éªŒè¯äºŒçº§åŸŸå
      secondStep: false
    }
  },
  computed: {
    emailButtonText () {
      // é‚®ç®±éªŒè¯ç buttonæ–‡å­—
      return this.emailCountDownTime > 0 ? this.emailCountDownTime + 'såŽé‡æ–°èŽ·å–' : 'èŽ·å–éªŒè¯ç '
    }
  },
  watch: {
    emailCountDownTime (value) {
      if (this.value === 0) {
        this.emailButtonCountingDown = false
      }
    }
  },
  methods: {
    // èŽ·å–éªŒè¯ç 
    getEmailCode () {
      // 1. é¦–å…ˆéªŒè¯è¾“å…¥æ¡†æ˜¯å¦é€šè¿‡éªŒè¯
      this.$refs['emailForm'].validateField('email', (error) => {
        console.log(error)
        if (!error) {
          // 2. è¯·æ±‚å‘é€éªŒè¯ç 
          https.getEmailCode(this.emailForm.email, response => {
            console.log(response)
            this.emailServerCode = response.data
            // å€’è®¡æ—¶
            this.emailButtonCountingDown = true
            this.emailCountDownTime = 60
            this.emailTimer()
            // æç¤º
            this.$message({message: 'å‘é€æˆåŠŸ', type: 'success'})
          }, error => {
            console.log(error)
            this.$message.error('å‘é€å¤±è´¥, è¯·é‡è¯•' + error)
          })
        } else {
          return false
        }
      })
    },
    emailTimer () {
      // å€’è®¡æ—¶
      if (this.emailCountDownTime > 0) {
        this.emailCountDownTime--
        setTimeout(this.emailTimer, 1000)
      }
    },
    nextSecondStep () {
      // è¾“å…¥éªŒè¯ç ï¼ŒéªŒè¯é€šè¿‡ï¼Œè¿™è·³è½¬åˆ°å¡«å†™äºŒçº§åŸŸåé¡µé¢
      this.$refs['emailForm'].validate((valid) => {
        if (valid) {
          this.firstStep = false
          this.secondStep = true
        } else {
          return false
        }
      })
    },
    preSecondStep () {
      this.firstStep = true
      this.secondStep = false
    },
    changePassword () {
      https.changePassword(this.emailForm.email, this.passwordForm.password,
        response => {
          console.log(response)
          if (response.status_code === 200) {
            this.$message({ message: 'ä¿®æ”¹æˆåŠŸ, è¯·ç™»å½•', type: 'success' })
            this.routeLogin()
          }
        }, error => {
          console.log(error)
        })
    },
    routeLogin () {
      // è·³è½¬ç™»å½•é¡µé¢
      this.$router.replace('/login')
    }
  }

}
</script>

<style scoped>
#bd-forget {
  margin-top: 150px;
}
.grid-content {
  min-height: 36px;
}
</style>



