<template>
  <div id='bd-company-admin'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>å¸®åŠ©æ–‡æ¡£</el-button>
    <el-button size="mini" type="primary" style="float: left;" @click="addCompanyDialog">æ·»åŠ åˆ†å…¬å¸</el-button>
    <el-input size="mini" v-model="companyName" placeholder="åˆ†å…¬å¸å" style="width: 120px; margin-left: 10px;" @input="handleQueryChange" clearable></el-input>
    <!-- <el-input size="mini" v-model="regionName" placeholder="æœåŠ¡åŸŽå¸‚å" style="width: 120px; margin-left: 10px;" @input="handleQueryChange" clearable></el-input> -->
    <el-button size="mini" type="primary" icon="el-icon-search" style="margin-left: 10px;" @click="handleQuery">æŸ¥è¯¢</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="companies.totalElements">
    </el-pagination>
    <el-table v-loading="loading" :data="companies.content" stripe size="mini" height="calc(100% - 80px)" style="width: 100%">
      <el-table-column type="index" width="35"></el-table-column>
      <el-table-column prop="name" label="åˆ†å…¬å¸å" width="200"></el-table-column>
      <el-table-column prop="regions" label="æœåŠ¡åŸŽå¸‚">
        <template slot-scope="scope">
          <el-tag v-for="region in scope.row.regions" :key="region.id" size="mini" style="margin-right:5px;">{{ displayRegionTagName(region) }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="description" label="å¤‡æ³¨" width="200"></el-table-column>
      <el-table-column fixed="right" label="æ“ä½œ" width="150">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="companies.totalElements">
    </el-pagination>
    <el-dialog
      title="æ·»åŠ åˆ†å…¬å¸"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="100px">
        <el-form-item label="åˆ†å…¬å¸å" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="æœåŠ¡åŸŽå¸‚" prop="regions">
          <el-tag v-for="region in form.regions" :key="region.id" size="mini" style="margin-right:5px;" closable :disable-transitions="false" @close="handleRemoveTag(region)">{{ displayRegionTagName(region) }}</el-tag>
          <el-autocomplete size="mini"
            v-if="searchRegionVisible"
            v-model="searchRegion"
            :fetch-suggestions="querySearchRegionAsync"
            ref="saveRegionInput"
            placeholder="è¯·è¾“å…¥å†…å®¹"
            @select="handleSelectRegion">
          </el-autocomplete>
          <el-button v-else size="mini" @click="showSearchRegion">æ·»åŠ æœåŠ¡åŸŽå¸‚</el-button>
        </el-form-item>
        <el-form-item label="æè¿°" prop="description">
          <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 20}" v-model="form.description"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="doAddCompany()">{{ dialogTitle }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
// import * as https from '@/apis/http'
import bus from '@/util/eventbus'

export default {
  name: 'bd-company-admin',
  data () {
    return {
      searchRegionVisible: false,
      searchRegion: '',
      loading: false,
      pageSize: 20,
      currentPage: 1,
      isEdit: false,
      dialogVisible: false,
      companyName: '',
      regionName: '',
      selectedRegions: [],
      form: {
        // id: 0,
        cid: '',
        name: '',
        description: '',
        regions: [],
        selectedRegions: []
      },
      rules: {
        name: [
          { required: true, message: 'è¯·è¾“å…¥å…¬å¸å', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([ 'companies', 'provinceCities', 'regions' ]),
    dialogTitle () {
      return this.isEdit ? 'æ›´æ–°' : 'åˆ›å»º'
    }
  },
  methods: {
    ...mapActions([ 'getCompanies', 'createCompany', 'updateCompany', 'deleteCompany', 'filterCompanies' ]),
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201809171923191')
    },
    displayRegionTagName (region) {
      // return region.name
      return (region.parent === undefined || region.parent === null) ? region.name : region.parent.name + region.name
    },
    querySearchRegionAsync (queryString, cb) {
      console.log(queryString)
      var regions = this.regions
      var results = queryString ? regions.filter(this.createStateFilter(queryString)) : regions

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 3000 * Math.random())
    },
    createStateFilter (queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelectRegion (item) {
      // console.log(item)
      let contains = this.form.regions.some(wg => {
        return wg.id === item.object.id
      })
      if (!contains) {
        this.form.regions.push(item.object)
      }
      //
      this.searchRegionVisible = false
      this.searchRegion = ''
    },
    showSearchRegion () {
      this.searchRegionVisible = true
      this.$nextTick(_ => {
        this.$refs.saveRegionInput.$refs.input.focus()
      })
    },
    handleRemoveTag (region) {
      console.log(region)
      this.form.regions.splice(this.form.regions.indexOf(region), 1)
    },
    handleSizeChange (val) {
      // console.log(val)
      this.pageSize = val
      //
      this.loading = true
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getCompanies(payload)
    },
    handleCurrentChange (val) {
      // console.log(val)
      this.currentPage = val
      //
      this.loading = true
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getCompanies(payload)
    },
    handleQueryChange (value) {
      // console.log(value)
      if (value.trim().length === 0) {
        this.loading = true
        let payload = { page: 0, size: 20 }
        this.getCompanies(payload)
      }
    },
    handleQuery () {
      // console.log(this.companyName)
      let payload = {page: 0, size: 20, name: this.companyName}
      this.filterCompanies(payload)
    },
    addCompanyDialog () {
      this.isEdit = false
      //
      this.form.cid = ''
      this.form.name = ''
      this.form.description = ''
      this.form.regions = []
      //
      this.dialogVisible = true
    },
    transformRegions () {
      let regions = this.form.regions
      this.form.selectedRegions = []
      for (let i = 0; i < regions.length; i++) {
        const region = regions[i]
        this.form.selectedRegions.push(region.code)
      }
    },
    doAddCompany () {
      this.transformRegions()
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          if (this.isEdit) {
            this.updateCompany(this.form)
          } else {
            this.createCompany(this.form)
          }
        } else {
          return false
        }
      })
    },
    handleChange (value) {
      console.log(value)
    },
    handleEdit (index, row) {
      // console.log(index, row)
      this.isEdit = true
      //
      this.form.cid = row.cid
      this.form.name = row.name
      this.form.description = row.description
      //
      this.form.regions = []
      for (let i = 0; i < row.regions.length; i++) {
        const region = row.regions[i]
        this.form.regions.push(region)
      }
      //
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteCompany(row.cid)
      }).catch(() => {
        //
      })
    }
  },
  mounted () {
    //
    bus.$on(constants.EVENT_BUS_COMPANY_LOAD_FINISHED, message => {
      this.loading = false
    })
  }

}
</script>

<style scoped>
#bd-company-admin {
  height: 100%;
  text-align: left;
}
</style>



