<template>
  <div id='bd-queue'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" plain>å¸®åŠ©æ–‡æ¡£</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="userQueues.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="userQueues.content" style="width: 100%">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="visitor.nickname" label="è®¿å®¢æ˜µç§°"></el-table-column>
      <el-table-column label="ç­‰å¾…æ—¶é•¿"></el-table-column>
      <el-table-column prop="workGroup.nickname" label="å·¥ä½œç»„"></el-table-column>
      <el-table-column prop="joined_at" label="è¿›å…¥æ—¶é—´"></el-table-column>
      <el-table-column prop="visitor.client" label="æ¥æº"></el-table-column>
      <el-table-column fixed="right" label="æ“ä½œ" width="120">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">æŽ¥å…¥</el-button>
          <!-- <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="userQueues.totalElements">
    </el-pagination>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'bd-queuing',
  data () {
    return {
      pageSize: 20,
      currentPage: 1,
      dialogVisible: false,
      isEdit: false
    }
  },
  computed: {
    ...mapGetters([ 'userQueues' ])
  },
  methods: {
    ...mapActions([ 'getQueue' ]),
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getQueue(payload)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getQueue(payload)
    },
    handleUrlClick (url) {
      window.open(url)
    },
    handleEdit (index, row) {
      console.log(index, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
    }
  }

}
</script>

<style scoped>
#bd-queue {
  height: 100%;
  text-align: left;
}
</style>



