<template>
  <!-- TODO: å¢žåŠ table loadingæ•ˆæžœ -->
  <!-- TODO: æ”¯æŒè‡ªå®šä¹‰å¯Œæ–‡æœ¬ï¼šæ–‡å­—é¢œè‰²ã€å­—ä½“ã€å­—å·ã€å›¾ç‰‡ -->
  <div id='bd-cuw-admin'>
    <el-row>
      <el-col :span="5" id="left-col">
        <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 15px;" @click="gotoHelpdoc()" plain>å¸®åŠ©æ–‡æ¡£</el-button>
        <el-button size="mini" type="success" style="float: left; margin-bottom: 15px;" @click="addCuw">æ·»åŠ å¸¸ç”¨è¯­</el-button>
        <!-- <el-button size="mini" type="primary" icon="el-icon-upload2" style="float: left; margin-bottom: 10px;">å¯¼å…¥</el-button> -->
        <el-input size="mini" placeholder="æœç´¢ç±»åˆ«" v-model="filterText"></el-input>
        <el-tree class="filter-tree"
          ref="cateTree"
          :empty-text="emptyText"
          :data="companyCuwCategories"
          :props="defaultProps"
          :filter-node-method="filterNode"
          @node-click="handleNodeClick">
        </el-tree>
        <el-button size="mini" type="success" @click="addCategory">æ·»åŠ </el-button>
        <el-button size="mini" type="primary" @click="editCategory" :disabled="editButtonDisabled">ç¼–è¾‘</el-button>
        <el-button size="mini" type="danger" @click="removeCategory" :disabled="deleteButtonDisabled">åˆ é™¤</el-button>
      </el-col>
      <el-col :span="19" id="right-col">
        <el-table stripe size="mini" height="calc(100% - 80px)"
          :data="companyCuws" style="width: 100%; text-align: left;">
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column prop="name" label="æ ‡é¢˜"></el-table-column>
          <el-table-column prop="content" label="å†…å®¹"></el-table-column>
          <el-table-column prop="count" label="ä½¿ç”¨æ¬¡æ•°" sortable></el-table-column>
          <el-table-column fixed="right" label="æ“ä½œ" width="150">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">åˆ é™¤</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <el-dialog
      title="æ·»åŠ å¸¸ç”¨è¯­"
      :visible.sync="addCuwDialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="æ ‡é¢˜" prop="name">
          <el-input v-model="form.name" placeholder="æ ‡é¢˜"></el-input>
        </el-form-item>
        <el-form-item label="å†…å®¹" prop="content">
          <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 20}" v-model="form.content"></el-input>
        </el-form-item>
        <el-form-item label="æ‰€å±žåˆ†ç±»" prop="categoryId" v-if="!isEdit">
          <span v-if="companyCuwCategories.length === 1">è¯·é¦–å…ˆåˆ›å»ºåˆ†ç±»</span>
          <el-radio-group v-model="form.categoryId">
            <el-radio v-if="category.id > 0" v-for="category in companyCuwCategories" :key="category.id" :label="category.id">
              {{ category.name }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCuwDialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="doAddCuw()">ç¡® å®š</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-cuw-admin',
  data () {
    return {
      emptyText: 'æš‚æ— åˆ†ç»„',
      currentCategory: {
        id: 0,
        name: '',
        cuwChildren: []
      },
      //
      pageSize: 20,
      currentPage: 1,
      //
      isEdit: false,
      inputVisible: false,
      inputValue: '',
      addCuwDialogVisible: false,
      setRobotNameDialogVisible: false,
      filterText: '',
      defaultProps: {
        // children: 'cuwChildren',
        label: 'name'
      },
      form: {
        id: 0,
        name: '',
        content: '',
        categoryId: ''
      },
      rules: {
        name: [
          { required: true, message: 'è¯·è¾“å…¥æ ‡é¢˜', trigger: 'blur' }
        ],
        content: [
          { required: true, message: 'è¯·è¾“å…¥å†…å®¹', trigger: 'blur' }
        ],
        categoryId: [
          { required: true, message: 'è¯·è‡³å°‘é€‰æ‹©ä¸€ä¸ªç±»åˆ«', trigger: 'change' }
        ]
      },
      formRobot: {
        robotName: ''
      }
    }
  },
  computed: {
    ...mapGetters([ 'companyCuwCategories', 'companyCuws' ]),
    editButtonDisabled () {
      return false
    },
    deleteButtonDisabled () {
      return false
    }
  },
  watch: {
    filterText (val) {
      this.$refs.cateTree.filter(val)
    }
  },
  methods: {
    ...mapActions([ 'getCategories', 'createCategory', 'updateCategory', 'deleteCategory',
      'getCompanyCuws', 'currentCompanyCuwCategoryId', 'getChildrenCuws', 'getCuws', 'cuwCreate', 'cuwUpdate', 'cuwDelete' ]),
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201809252249331')
    },
    rateTotal (row) {
      return row.rateHelpful + row.rateUseless
    },
    handleNodeClick (data) {
      console.log(data)
      //
      this.currentCategory.id = data.id
      this.currentCategory.name = data.name
      // this.currentCategory.children = data.children
      this.getChildrenCuws(data.id)
    },
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
    },
    addCuw () {
      this.isEdit = false
      this.form.id = 0
      this.form.name = ''
      this.form.content = ''
      this.form.categoryId = ''
      //
      this.addCuwDialogVisible = true
    },
    addCategory () {
      this.$prompt('è¯·è¾“å…¥åˆ†ç»„', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ'
      }).then(({ value }) => {
        // console.log(value)
        if (value !== undefined && value !== null && value.length > 0) {
          let payload = {
            parent_id: 0,
            name: value,
            type: constants.CATEGORY_TYPE_CUW_COMPANY
          }
          this.createCategory(payload)
        }
      }).catch(() => {
        // å–æ¶ˆ
      })
    },
    editCategory () {
      if (this.currentCategory.id === 0) {
        this.$message.error('è¯·é€‰æ‹©è¦ç¼–è¾‘çš„ç±»åˆ«')
      } else {
        this.$prompt('ç¼–è¾‘ç±»åˆ«', 'æç¤º', {
          confirmButtonText: 'ç¡®å®š',
          cancelButtonText: 'å–æ¶ˆ',
          inputValue: this.currentCategory.name
        }).then(({ value }) => {
          // console.log(value)
          if (value !== undefined && value !== null && value.length > 0) {
            let payload = {
              category_id: this.currentCategory.id,
              name: value
            }
            this.updateCategory(payload)
          } else {
            this.$message.error('ä¸èƒ½ä¸ºç©º')
          }
        }).catch(() => {
          // å–æ¶ˆ
        })
      }
    },
    removeCategory () {
      if (this.currentCategory.id === 0) {
        this.$message.error('ä¸å…è®¸åˆ é™¤')
        return
      }
      if (this.companyCuws.length > 0) {
        this.$message.error('è¯·é¦–å…ˆåˆ é™¤åˆ†ç±»å†…å®¹')
        return
      }
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.deleteCategory(this.currentCategory.id)
        // this.cuwDelete(this.currentCategory.id)
        this.currentCategory.id = 0
      }).catch(() => {
      })
    },
    doAddCuw () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.addCuwDialogVisible = false
          if (this.isEdit) {
            //
            let payload = { id: this.form.id, name: this.form.name, content: this.form.content }
            this.cuwUpdate(payload)
          } else {
            //
            let payload = { categoryId: this.form.categoryId, name: this.form.name, content: this.form.content }
            this.cuwCreate(payload)
          }
        } else {
          return false
        }
      })
    },
    handleEdit (index, row) {
      this.isEdit = true
      // console.log(index, row)
      this.form.id = row.id
      this.form.name = row.name
      this.form.content = row.content
      // FIXME: ç›´æŽ¥åœ¨ç¼–è¾‘å¯¹è¯æ¡†ä¿®æ”¹åˆ†ç»„
      // this.form.categoryId = row.category.id
      //
      this.addCuwDialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('ç¡®å®šè¦åˆ é™¤?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        // åˆ é™¤
        this.cuwDelete(row.id)
      }).catch(() => {
        //
      })
    }
  },
  created () {
    this.getCategories()
    this.getCuws()
  }
}
</script>

<style scoped>
#bd-cuw-admin {
  height: 100%;
  /* padding-top: 5px; */
}
.el-row {
  height: 100%;
  padding-top: 5px;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
}
#right-col{
  height: 100%;
}
.filter-tree {
  margin-top: 10px;
  margin-bottom: 10px;
  height: calc(100% - 130px);
  overflow-y: auto;
}
.el-radio+.el-radio {
  margin-left: 1px;
}
/* .el-radio__label {
  padding-left: 5px !important;
  padding-right: 5px !important;
} */
</style>



