<template>
  <div id='bd-profile-admin'>
    <el-form ref="form" :rules="rules" :model="form" label-width="120px" style="margin-top: 20px;">
      <el-form-item v-if="!electron" label="ç®¡ç†åŽå°ç½‘å€:">
          <el-input type="textarea" v-model="adminUrl" :autosize="{ minRows: 3, maxRows: 5}" style="width: 300px;"></el-input>
      </el-form-item>
      <el-form-item label="ä¼ä¸šå·" prop="subDomain">
          <el-input v-model="userInfo.subDomain" style="width: 300px;" :disabled="isSubDomainDiabled"></el-input>
      </el-form-item>
      <el-form-item label="ç”¨æˆ·å" prop="username">
          <el-input v-model="userInfo.username" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="æ˜µç§°" prop="nickname">
          <el-input v-model="userInfo.nickname" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="å¤´åƒ" prop="avatar">
          <img :src="userInfo.avatar" width="40px" height="40px" style="float: left; margin-right: 15px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary" :disabled="true">ç‚¹å‡»ä¸Šä¼ </el-button>
          </el-upload>
      </el-form-item>
      <el-form-item label="æ‰‹æœº" prop="mobile">
          <el-input v-model="userInfo.mobile" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="é‚®ç®±" prop="email">
          <el-input v-model="userInfo.email" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <!-- <el-form-item label="è§’è‰²" prop="email">
          <el-input v-model="form.email"></el-input>
      </el-form-item> -->
      <el-form-item label="ä¸ªæ€§ç­¾å" prop="description">
        <el-input type="textarea" :rows="2" placeholder="è¿™ä¸ªäººå¾ˆæ‡’,ä»€ä¹ˆä¹Ÿæ²¡æœ‰å†™" v-model="userInfo.description" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="æ¬¢è¿Žè¯­" prop="welcomeTip">
        <el-input type="textarea" :rows="2" placeholder="è¿™ä¸ªäººå¾ˆæ‡’,ä»€ä¹ˆä¹Ÿæ²¡æœ‰å†™" v-model="userInfo.welcomeTip" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item>
          <el-button type="primary" @click="editProfile">ç¼–è¾‘</el-button>
      </el-form-item>
    </el-form>
    <el-dialog
      title="ç¼–è¾‘ä¸ªäººèµ„æ–™"
      :visible.sync="editProfileDialogVisible"
      width="30%"
      center>
      <el-form ref="form" :rules="rules" :model="form" label-width="80px" style="margin-top: 20px;">
      <el-form-item label="ä¼ä¸šå·" prop="subDomain">
          <el-input v-model="form.subDomain" style="width: 200px;" :disabled="isSubDomainDiabled"></el-input>
      </el-form-item>
      <!-- FIXME: ä¸€ä¸ªç®¡ç†å‘˜è´¦å·ä¿®æ”¹ç”¨æˆ·ååŽä¸èƒ½ç™»å½•ï¼Œå› æ­¤æš‚ä¸”ç¦æ­¢ä¿®æ”¹ç”¨æˆ·å -->
      <el-form-item label="ç”¨æˆ·å" prop="username">
          <el-input v-model="form.username" style="width: 200px;" disabled></el-input>
      </el-form-item>
      <el-form-item label="æ–°å¯†ç " prop="password">
        <el-input v-model="form.password" auto-complete="off" placeholder="å¯†ç " style="width: 200px;"></el-input>
      </el-form-item>
      <el-form-item label="æ˜µç§°" prop="nickname">
          <el-input v-model="form.nickname" style="width: 200px;"></el-input>
      </el-form-item>
      <el-form-item label="å¤´åƒ" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 15px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">ç‚¹å‡»ä¸Šä¼ </el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="æ‰‹æœº" prop="mobile">
            <el-input v-model="form.mobile" style="width: 200px;"></el-input>
        </el-form-item>
        <el-form-item label="é‚®ç®±" prop="email">
            <el-input v-model="form.email" style="width: 200px;"></el-input>
        </el-form-item>
        <!-- <el-form-item label="è§’è‰²" prop="email">
            <el-input v-model="form.email"></el-input>
        </el-form-item> -->
        <el-form-item label="ä¸ªæ€§ç­¾å" prop="description">
          <el-input type="textarea" :rows="2" placeholder="è¿™ä¸ªäººå¾ˆæ‡’,ä»€ä¹ˆä¹Ÿæ²¡æœ‰å†™" v-model="form.description" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item label="æ¬¢è¿Žè¯­" prop="welcomeTip">
          <el-input type="textarea" :rows="2" placeholder="è¿™ä¸ªäººå¾ˆæ‡’,ä»€ä¹ˆä¹Ÿæ²¡æœ‰å†™" v-model="form.welcomeTip" style="width: 300px;"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editProfileDialogVisible = false">å– æ¶ˆ</el-button>
        <el-button type="primary" @click="doUpdateProfile">ä¿ å­˜</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
// import bus from '@/util/eventbus'

export default {
  name: 'bd-profile-admin',
  data () {
    return {
      editProfileDialogVisible: false,
      form: {
        username: '',
        password: '',
        name: '',
        avatar: '',
        mobile: '',
        email: '',
        description: '',
        welcomeTip: ''
      },
      rules: {
        username: [
          { required: true, message: 'è¯·è¾“å…¥ç”¨æˆ·å', trigger: 'blur' }
        ],
        name: [
          { required: true, message: 'è¯·è¾“å…¥æ˜µç§°', trigger: 'blur' }
        ],
        avatar: [
          { required: true, message: 'ä¸Šä¼ å¤´åƒ', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: 'è¯·è¾“å…¥æ‰‹æœºå·', trigger: 'blur' }
        ],
        email: [
          { required: true, message: 'è¯·è¾“å…¥é‚®ç®±', trigger: 'blur' }
        ],
        description: [
          { required: true, message: 'è¯·è¾“å…¥ç®€ä»‹', trigger: 'blur' }
        ]
      },
      // ä¸Šä¼ å›¾ç‰‡ç›¸å…³å‚æ•°
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      isIp: constants.IS_IP,
      electron: constants.IS_ELECTRON
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'mainHost' ]),
    adminUrl () {
      if (this.isIp) {
        return constants.LOGIN_URL
      } else {
        return constants.HTTP_PREFIX + this.userInfo.subDomain + '.' + this.mainHost + '/admin#/login'
      }
    },
    isSaveButtonDisabled () {
      return false
    },
    uploadImageServerUrl () {
      return constants.UPLOAD_IMAGE_URL
    },
    isSubDomainDiabled () {
      return true
    }
  },
  methods: {
    ...mapActions([ 'updateProfile' ]),
    username (usernameAtSubDomain) {
      if (this.userInfo.agent) {
        return usernameAtSubDomain.split('@')[0]
      } else {
        return usernameAtSubDomain
      }
    },
    handleImageUploadSuccess (result, file) {
      console.log('upload image success result:', result, ' file:', file)
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('ä¸Šä¼ å›¾ç‰‡é”™è¯¯ï¼Œè¯·ç¨åŽé‡è¯•...')
      }
    },
    beforeImageUpload (file) {
      // console.log('before image upload file:', file)
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // éªŒè¯å›¾ç‰‡æ ¼å¼
      if (!isJPGorPNG) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡åªèƒ½æ˜¯ jpg æˆ– png æ ¼å¼!')
      }
      // éªŒè¯å›¾ç‰‡å¤§å°
      if (!isLt2M) {
        this.$message.error('ä¸Šä¼ å¤´åƒå›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡ 2MB!')
      }
      // è®¾ç½®æ–‡ä»¶å
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // è¿”å›ž
      return isJPGorPNG && isLt2M
    },
    doUpdateProfile () {
      console.log('updateProfile')
      if (this.userInfo.agent) {
        this.form.username = this.form.username + '@' + this.form.subDomain
      }
      this.updateProfile(this.form)
      this.editProfileDialogVisible = false
    },
    editProfile () {
      this.form.subDomain = this.userInfo.subDomain
      if (this.userInfo.agent) {
        this.form.username = this.userInfo.username.split('@')[0]
      } else {
        this.form.username = this.userInfo.username
      }
      this.form.nickname = this.userInfo.nickname
      this.form.avatar = this.userInfo.avatar
      this.form.mobile = this.userInfo.mobile
      this.form.email = this.userInfo.email
      this.form.description = this.userInfo.description
      this.form.welcomeTip = this.userInfo.welcomeTip
      //
      this.editProfileDialogVisible = true
    }
  }
}
</script>

<style scoped>
#bd-profile-admin {
  height: 100%;
  overflow: auto;
  text-align: left;
}
</style>



