/**
 * å½“å‰ä¼šè¯èŠå¤©è®°å½•
 * @param {*} state state
 */
export const messages = state => {
  let messages = state.user.messages.filter(message => {
    if (state.user.current_thread.type === constants.THREAD_TYPE_THREAD) {
      // return message.thread.tid === state.user.current_thread.tid // æ ¹æ®visitor.uidè¿‡æ»¤ï¼Œä¸èƒ½ä½¿ç”¨thread.tid
      return message.thread.visitor.uid === state.user.current_thread.visitor.uid
    } else if (state.user.current_thread.type === constants.THREAD_TYPE_CONTACT) {
      // return message.cid === state.user.current_contact.uid || message.user.uid === state.user.current_contact.uid
      return false
    } else if (state.user.current_thread.type === constants.THREAD_TYPE_GROUP) {
      // return message.gid === state.user.current_group.wid
      return false
    }
  })
  return messages.sort(function (a, b) {
    if (a.id > b.id) {
      return 1
    }
    if (a.id < b.id) {
      return -1
    }
    return 0
  })
}

