export const regions = state => {
  let regions = []
  for (let i = 0; i < state.regions.provinces.length; i++) {
    const province = state.regions.provinces[i]
    regions.push({ 'value': province.name, 'object': province })
  }
  for (let i = 0; i < state.regions.cities.length; i++) {
    const city = state.regions.cities[i]
    regions.push({ 'value': city.parent.name + city.name, 'object': city })
  }
  return regions
}

export const provinceCities = state => {
  let provinceCities = []
  for (let i = 0; i < state.regions.cities.length; i++) {
    const city = state.regions.cities[i]
    let contains = false
    for (let i = 0; i < provinceCities.length; i++) {
      const provinceCity = provinceCities[i]
      if (city.parent.code === provinceCity.value) {
        provinceCities[i].children.push({ value: city.code, label: city.name })
        contains = true
      }
    }
    if (!contains) {
      provinceCities.push({ value: city.parent.code, label: city.parent.name, children: [{ value: city.code, label: city.name }] })
    }
  }
  return provinceCities
}

