export const currentCompanyCuwCategoryId = state => {
  return state.current_company_cuw_category_id
}

export const companyCuws = state => {
  let cuws = []
  if (state.current_company_cuw_category_id === 0) {
    for (let i = 0; i < state.cuws[1].cuwChildren.length; i++) {
      const category = state.cuws[1].cuwChildren[i]
      for (let j = 0; j < category.cuwChildren.length; j++) {
        const cuw = category.cuwChildren[j]
        cuws.push(cuw)
      }
    }
  } else {
    for (let i = 0; i < state.cuws[1].cuwChildren.length; i++) {
      const category = state.cuws[1].cuwChildren[i]
      if (category.id === state.current_company_cuw_category_id) {
        for (let j = 0; j < category.cuwChildren.length; j++) {
          const cuw = category.cuwChildren[j]
          cuws.push(cuw)
        }
      }
    }
  }
  return cuws
}

