export const layimFriends = state => {
  let friends = [{
    id: '1',
    groupname: 'åŒäº‹',
    list: []
  }, {
    id: '2',
    groupname: 'é™Œç”Ÿäºº',
    list: []
  }]
  for (let i = 0; i < state.user.contacts.length; i++) {
    const friend = state.user.contacts[i]
    friends[0].list.push({
      id: friend.uid,
      username: friend.realName,
      sign: friend.description,
      avatar: friend.avatar,
      status: 'offline'
    })
  }
  return friends
}

export const layimGroups = state => {
  let groups = []
  for (let i = 0; i < state.user.groups.length; i++) {
    const group = state.user.groups[i]
    groups.push({
      id: group.gid,
      groupname: group.nickname,
      sign: group.description,
      avatar: group.avatar
    })
  }
  return groups
}



